import React, { Component, Fragment } from 'react';
import Modal from 'react-bootstrap/Modal';
import { Button, Form } from 'react-bootstrap';
import { connect } from 'react-redux';
import { updateProfile } from '../../../store/actions/profileActions';


class Edit extends Component {
    constructor(props) {
        super(props);
        this.state = {
            name: props.data.name,
            email: props.data.email,
            contact: props.data.contact,
            address: props.data.address,
            logoText: props.data.logoText,
            companyName: props.data.companyName,
            isWait: false,
        }
    }
    changeHandler = event => {
        this.setState({
            [event.target.name]: event.target.value
        })
    }
    submitHandler = async event => {
        event.preventDefault()
        this.setState({ isWait: true })
        let { updateProfile, closeHandler } = this.props
        let response = await updateProfile(this.state)
        if (response) closeHandler()
        this.setState({ isWait: false })
    }
    render() {
        let { name, contact, address, logoText, companyName, isWait } = this.state
        let isDone = name && address

        return (
            <Fragment>
                <Modal show={this.props.isOpen} onHide={this.props.closeHandler}>
                    <Modal.Header closeButton>
                        <Modal.Title>Edit Profile Information</Modal.Title>
                    </Modal.Header>
                    <Modal.Body>
                        <Form onSubmit={this.submitHandler}>
                            <Form.Group>
                                <Form.Label>Full Name *</Form.Label>
                                <Form.Control
                                    type="text"
                                    name="name"
                                    defaultValue={name}
                                    onChange={this.changeHandler}
                                    placeholder="Enter Full Name"
                                />
                            </Form.Group>
                            <Form.Group>
                                <Form.Label>Contact Number *</Form.Label>
                                <Form.Control
                                    type="number"
                                    name="contact"
                                    defaultValue={contact}
                                    onChange={this.changeHandler}
                                    placeholder="Enter Contact Number"
                                />
                            </Form.Group>
                            <Form.Group>
                                <Form.Label>Address *</Form.Label>
                                <Form.Control
                                    type="text"
                                    name="address"
                                    defaultValue={address}
                                    onChange={this.changeHandler}
                                    placeholder="Enter Address"
                                />
                            </Form.Group>
                            <Form.Group>
                                <Form.Label>Slogan</Form.Label>
                                <Form.Control
                                    type="text"
                                    name="logoText"
                                    defaultValue={logoText}
                                    onChange={this.changeHandler}
                                    placeholder="Enter Slogan"
                                />
                            </Form.Group>
                            <Form.Group>
                                <Form.Label>Company Name</Form.Label>
                                <Form.Control
                                    type="text"
                                    name="companyName"
                                    defaultValue={companyName}
                                    onChange={this.changeHandler}
                                    placeholder="Enter Company Name"
                                />
                            </Form.Group>
                            <Button type="submit" variant="dark" disabled={!isDone} size="lg" block>
                                {isWait ? `Please Wait...` : `Save`}
                            </Button>
                        </Form>
                    </Modal.Body>
                </Modal>
            </Fragment>
        )
    }
}
const mapStateToProps = state => ({
    user: state.user,
})
export default connect(mapStateToProps, { updateProfile })(Edit);