import React from 'react';
import { useSelector } from 'react-redux'
import { textLimit } from './helper';
import logo from '../components/assets/images/logo-found.jpg'


/* Get Airport Name */
export const GetAirport = (props) => {
    let airports = useSelector(state => state.common.airports)
    let result = null

    let { code, limit, returnVal, city } = props
    if (!limit) limit = 22

    if (airports.length > 0) {
        let getData = airports.filter(item => item.iata === code)
        if (getData.length > 0) {
            if (!returnVal) {
                if (getData[0].short) {
                    result = getData[0].short
                } else {
                    result = getData[0].name
                }

                if (city && getData[0].city) {
                    result = getData[0].city
                }

                result = textLimit(result, limit)
            } else {
                result = getData[0].name
            }
        }
    }
    if (!result) result = code

    return result
}



/* Get Airline Name and Logo */
export const GetAirline = (props) => {
    let airlines = useSelector(state => state.common.airlines)
    let result = null

    let { code, type, limit, returnVal } = props
    if (!limit) limit = 13

    if (airlines.length > 0) {
        if (type === 'logo') {
            let getData = airlines.filter(item => item.code === code)
            if (getData.length > 0) {
                result = getData[0].image
            }
            if (!result) result = logo
            result = <img src={result} alt="Logo" className="biman-logo" />
        } else {
            let getData = airlines.filter(item => item.code === code)
            if (getData.length > 0) {
                result = getData[0].name
            }
            if (!returnVal) {
                result = textLimit(result, limit)
            }
        }
    }
    if (!result) result = code
    return result
}
