import React, { Component, Fragment } from 'react'
import { connect } from 'react-redux'


class PersonArea extends Component {
    changeHandler = event => {
        if ((event.target.name === 'cabinClass') || (event.target.name === 'preferAir')) {
            this.props.stateHandler(event.target.name, event.target.value)
        } else {
            this.props.stateHandler(event.target.name, Number(event.target.value))
        }
    }
    componentWillMount() {
        document.addEventListener('mousedown', this.outSideClickHandler, false)
    }
    componentWillUnmount() {
        document.removeEventListener('mousedown', this.outSideClickHandler, false)
    }
    outSideClickHandler = e => {
        if (this.node.contains(e.target)) return
        this.props.menuHandler()
    }
    render() {
        let { airlines } = this.props
        let { adult, child, infant, cabinClass, preferAir } = this.props.data

        return (
            <Fragment>
                <div className="passenger-popup" ref={node => this.node = node}>
                    <div className="row">
                        <div className="col-lg-4">
                            <label htmlFor="adult" className="eTicketLabel">Adult</label>
                            <select
                                id="adult"
                                defaultValue={adult}
                                selected="1"
                                className="form-control"
                                name='adult'
                                onChange={this.changeHandler}
                            >
                                <option value="1">1</option>
                                <option value="2">2</option>
                                <option value="3">3</option>
                                <option value="4">4</option>
                                <option value="5">5</option>
                                <option value="6">6</option>
                                <option value="7">7</option>
                                <option value="8">8</option>
                                <option value="9">9</option>
                            </select>
                        </div>

                        <div className="col-lg-4">
                            <label htmlFor="child" className="eTicketLabel">Child</label>
                            <select
                                id="child"
                                defaultValue={child}
                                selected="1"
                                className="form-control"
                                name='child'
                                onChange={this.changeHandler}
                            >
                                <option value="0">0</option>
                                <option value="1">1</option>
                                <option value="2">2</option>
                                <option value="3">3</option>
                                <option value="4">4</option>
                            </select>
                            <small style={{ color: "#fff" }}>Age (2-12)</small>
                        </div>

                        <div className="col-lg-4">
                            <label htmlFor="infant" className="eTicketLabel">Infant</label>
                            <select
                                id="infant"
                                defaultValue={infant}
                                selected="1"
                                className="form-control"
                                name='infant'
                                onChange={this.changeHandler}
                            >
                                <option value="0">0</option>
                                <option value="1">1</option>
                                <option value="2">2</option>
                            </select>
                            <small style={{ color: "#fff" }} >Age (0-2)</small>
                        </div>
                    </div>

                    <div className="form-group">
                        <label htmlFor="cabinClass" className="eTicketLabel">Cabin Class</label>
                        <select
                            id="cabinClass"
                            value={cabinClass}
                            className="form-control"
                            name='cabinClass'
                            onChange={this.changeHandler}
                        >
                            <option value="All">All</option>
                            <option value="Economy">Economy</option>
                            <option value="Business">Business</option>
                            <option value="Premium Economy">Premium Economy</option>
                            <option value="First Class">First Class</option>
                        </select>
                    </div>

                    <div className="form-group">
                        <label htmlFor="preferAir" className="eTicketLabel">Preferred Air</label>
                        <select
                            id="preferAir"
                            value={preferAir}
                            className="form-control"
                            name='preferAir'
                            onChange={this.changeHandler}
                        >
                            <option value="All">All</option>
                            {airlines.length > 0 && airlines.map(item => <option value={item.code} key={item.code}>{item.name}</option>)}
                        </select>
                    </div>

                </div>
            </Fragment>
        )
    }
}
const mapStateToProps = state => ({
    airlines: state.common.airlines
})
export default connect(mapStateToProps)(PersonArea)