import React, { Component, Fragment } from 'react';
import { Form, Row, Button } from 'react-bootstrap';
import { storeData, getBasicInfo, updateData } from './../../store/actions/basicInfoActions';
import { connect } from 'react-redux';


class BasicInfo extends Component {
    constructor(props) {
        super(props);
        this.state = {
            _id: '',
            address: '',
            email: '',
            contact: '',
            message: '',
            message2: '',
            anyService: ''
        }
    }
    async componentDidMount() {
        let response = await this.props.getBasicInfo()
        if (Object.keys(response).length !== 0) {
            let { _id, address, email, contact, message, message2, anyService } = response
            this.setState({ _id, address, email, contact, message, message2, anyService })
        }
    }
    changeHandler = event => {
        this.setState({
            [event.target.name]: event.target.value
        })
    }
    submitHandler = async event => {
        event.preventDefault()
        let { storeData, updateData, history } = this.props
        let { _id } = this.state
        let response = false
        if (_id) {
            response = await updateData(this.state, _id)
        } else {
            response = await storeData(this.state)
        }
        response && history.push('/backend/dashboard')
    }
    render() {
        let { address, email, contact, message, message2, anyService } = this.state
        return (
            <Fragment>
                <div className="bg-light">
                    <div className="section-padding-top">
                        <div className="container bg-white">
                            <div className="row">
                                <div className="col-lg-12">
                                    <div className="section-title text-center section-title-padding">
                                        <h2>Contact Information</h2>
                                    </div>
                                </div>
                            </div>

                            <Form onSubmit={this.submitHandler}>
                                <Form.Group as={Row}>
                                    <Form.Label htmlFor='address'>Address</Form.Label>
                                    <Form.Control
                                        type='text'
                                        id='address'
                                        name='address'
                                        placeholder='Type here'
                                        value={address}
                                        onChange={this.changeHandler}
                                    />
                                </Form.Group>
                                <Form.Group as={Row}>
                                    <Form.Label htmlFor='email'>Email Address<span>*</span></Form.Label>
                                    <Form.Control
                                        type='email'
                                        id='email'
                                        name='email'
                                        placeholder='Type here'
                                        value={email}
                                        onChange={this.changeHandler}
                                    />
                                </Form.Group>
                                <Form.Group as={Row}>
                                    <Form.Label htmlFor='contact'>Contact Number<span>*</span></Form.Label>
                                    <Form.Control
                                        type='text'
                                        id='contact'
                                        name='contact'
                                        placeholder='Type here'
                                        value={contact}
                                        onChange={this.changeHandler}
                                    />
                                </Form.Group>
                                <Form.Group as={Row}>
                                    <Form.Label htmlFor='message'>Left Side Message</Form.Label>
                                    <Form.Control
                                        type='text'
                                        id='message'
                                        name='message'
                                        placeholder='Type here'
                                        value={message}
                                        onChange={this.changeHandler}
                                    />
                                </Form.Group>
                                <Form.Group as={Row}>
                                    <Form.Label htmlFor='message2'>Right Side Message</Form.Label>
                                    <Form.Control
                                        type='text'
                                        id='message2'
                                        name='message2'
                                        placeholder='Type here'
                                        value={message2}
                                        onChange={this.changeHandler}
                                    />
                                </Form.Group>
                                <Form.Group as={Row}>
                                    <Form.Label htmlFor='anyService'>Any Service</Form.Label>
                                    <Form.Control
                                        type='text'
                                        id='anyService'
                                        name='anyService'
                                        placeholder='Any Service'
                                        value={anyService}
                                        onChange={this.changeHandler}
                                    />
                                </Form.Group>
                                <Form.Group as={Row}>
                                    <Button
                                        variant="primary"
                                        type="submit"
                                        className='btn-primary'
                                    >Submit</Button>
                                </Form.Group>
                            </Form>
                        </div>
                    </div>
                </div>
            </Fragment>
        );
    }
}
export default connect(null, { storeData, getBasicInfo, updateData })(BasicInfo)