import React, { Component, Fragment } from 'react'
import { getMarqueeDetail } from '../../store/actions/marqueeActions';
import { connect } from 'react-redux';
import renderHTML from 'react-render-html';


class MarqueeDetail extends Component {
    constructor(props) {
        super(props);
        let { id } = props.match.params
        this.state = {
            data: {},
            dataId: id
        }
    }
    componentWillReceiveProps(props) {
        if (props.match.params.id !== this.state.dataId) {
            this.fetchData(props.match.params.id)
        }
    }
    componentDidMount() {
        this.fetchData(this.state.dataId)
    }
    fetchData = async (dataId) => {
        this.setState({ data: await this.props.getMarqueeDetail(dataId), dataId })
    }
    render() {
        let { data } = this.state
        return (
            <Fragment>
                <section className="section-padding">
                    <div className="container bg-light p-5">
                        <div className="row">
                            <div className="col-lg-8 col-md-8 offset-lg-2 offset-md-2">
                                <div className="section-title text-center section-title-padding">
                                    <h3>Detail Information</h3>
                                </div>
                            </div>
                        </div>

                        <div className="row">
                            <div className="col-lg-12">
                                <h3>{data.title}</h3>
                                <p>{data.description && renderHTML(data.description)}</p>
                            </div>
                        </div>
                    </div>
                </section>
            </Fragment>
        );
    }
}
export default connect(null, { getMarqueeDetail })(MarqueeDetail)