import { LOW_FARE_SEARCH, SET_AIR_PRICING, SET_RESERVATION } from "../actions/types"

const init = {
    lists: [],
    searchData: [],
    pricing: [],
    segmentInfo: [],
    reservation: [],
    airPriceStatus: 0,
    createReservationStatus: 0,
    isWait: false,
}

const flightReducer = (state = init, action) => {
    switch (action.type) {
        case LOW_FARE_SEARCH: {
            return {
                ...state,
                lists: action.payload.lists || {},
                searchData: action.payload.searchData || {},
            }
        }
        case SET_AIR_PRICING: {
            return {
                ...state,
                pricing: action.payload.pricing || [],
                segmentInfo: action.payload.segmentInfo || [],
            }
        }
        case SET_RESERVATION: {
            return {
                ...state,
                reservation: action.payload.reservation || {},
            }
        }
        default: return state
    }
}
export default flightReducer