import React, { Component, Fragment } from 'react';
import Modal from 'react-bootstrap/Modal';
import { Button, Form } from 'react-bootstrap';
import { connect } from 'react-redux';
import { storeData, updateData } from '../../../store/actions/commissionActions'

class Edit extends Component {
    constructor(props) {
        super(props);
        this.state = {
            id: props.data._id || null,
            agentId: '',
            airlineCode: props.data.airlineCode || '',
            amount: props.data.amount || '',
            type: props.data.type || '',
            symbol: props.data.symbol || '',
            isWait: false
        }
    }
    changeHandler = event => {
        this.setState({
            [event.target.name]: event.target.value
        })
    }
    submitHandler = async event => {
        event.preventDefault()
        this.setState({ isWait: true })
        let { actionIsDone, storeData, updateData } = this.props

        let response = false;
        let { id, agentId, airlineCode, amount, type, symbol } = this.state
        if (amount.slice(0, 1) === '-' || amount.slice(0, 1) === '+') {
            symbol = amount.slice(0, 1)
            amount = amount.slice(1)
        } else {
            symbol = '+'
        }
        let data = { agentId, airlineCode, amount, type, symbol }

        if (id) {
            response = await updateData(data, id)
        } else {
            response = await storeData(data)
        }
        if (response) actionIsDone()
        this.setState({ isWait: false })
    }
    render() {
        let { id, agentId, airlineCode, amount, type, symbol, isWait } = this.state
        let isDone = airlineCode && amount && type
        if (symbol) {
            amount = `${symbol}${amount}`
        }
        return (
            <Fragment>
                <Modal show={this.props.isOpen} onHide={this.props.closeHandler}>
                    <Modal.Header closeButton>
                        <Modal.Title>{id ? `Edit Commission` : `Add Commission`}</Modal.Title>
                    </Modal.Header>
                    <Modal.Body>
                        <Form onSubmit={this.submitHandler}>
                            {!id && <Form.Group>
                                <Form.Label>Agent ID</Form.Label>
                                <Form.Control
                                    type="text"
                                    name="agentId"
                                    defaultValue={agentId}
                                    onChange={this.changeHandler}
                                    placeholder="Enter Agent ID"
                                />
                            </Form.Group>}

                            <Form.Group>
                                <Form.Label>Airline Code <span>*</span></Form.Label>
                                <Form.Control
                                    type="text"
                                    name="airlineCode"
                                    defaultValue={airlineCode}
                                    onChange={this.changeHandler}
                                    placeholder="Enter Airline Code"
                                    required
                                />
                            </Form.Group>
                            <Form.Group>
                                <Form.Label>Amount <span>*</span></Form.Label>
                                <Form.Control
                                    type="number"
                                    name="amount"
                                    defaultValue={amount}
                                    onChange={this.changeHandler}
                                    placeholder="Enter Amount"
                                    required
                                />
                            </Form.Group>
                            <Form.Group>
                                <Form.Check
                                    type="radio"
                                    name="status"
                                    label="Amount(BDT)"
                                    checked={type === 1}
                                    id='statusTrue'
                                    onChange={(e) => this.setState({ type: 1 })}
                                />
                            </Form.Group>
                            <Form.Group>
                                <Form.Check
                                    type="radio"
                                    name="status"
                                    label="Percentage(%)"
                                    id='statusFalse'
                                    checked={type === 2}
                                    onChange={(e) => this.setState({ type: 2 })}
                                />
                            </Form.Group>

                            <Button type="submit" variant="dark" disabled={!isDone} size="lg" block>
                                {isWait ? `Please Wait...` : `Save`}
                            </Button>
                        </Form>
                    </Modal.Body>
                </Modal>
            </Fragment>
        )
    }
}
const mapStateToProps = state => ({
    common: state.common,
})
export default connect(mapStateToProps, { storeData, updateData })(Edit);