import { SET_MESSAGE, SET_AIRPORTS } from './types'
import Axios from 'axios'

// Get Data
export const getData = data => async dispatch => {
    return Axios.get(`/api/airports/`, data)
        .then(res => {
            dispatch({
                type: SET_AIRPORTS,
                payload: res.data.data
            })
            return res.data.data
        })
        .catch(err => {
            dispatch({
                type: SET_MESSAGE,
                payload: {
                    message: err.response && err.response.data.message,
                    type: 'error',
                }
            })
            return false
        })
}

// Store Data
export const storeData = data => async dispatch => {
    return Axios.post(`/api/airports/`, data)
        .then(res => {
            dispatch({
                type: SET_MESSAGE,
                payload: {
                    message: res.data.message,
                }
            })
            return true;
        })
        .catch(err => {
            dispatch({
                type: SET_MESSAGE,
                payload: {
                    message: err.response && err.response.data.message,
                    type: 'error',
                }
            })
            return false
        })
}

// Update Data
export const updateData = (data, id) => async dispatch => {
    return Axios.put(`/api/airports/${id}`, data)
        .then(res => {
            dispatch({
                type: SET_MESSAGE,
                payload: {
                    message: res.data.message,
                }
            })
            return true;
        })
        .catch(err => {
            dispatch({
                type: SET_MESSAGE,
                payload: {
                    message: err.response && err.response.data.message,
                    type: 'error',
                }
            })
            return false;
        })
}

// Delete Data
export const deleteData = id => async dispatch => {
    return Axios.delete(`/api/airports/${id}`)
        .then(res => {
            dispatch({
                type: SET_MESSAGE,
                payload: {
                    message: res.data.message,
                }
            })
            return true;
        })
        .catch(err => {
            dispatch({
                type: SET_MESSAGE,
                payload: {
                    message: err.response && err.response.data.message,
                    type: 'error',
                }
            })
            return false;
        })
}
