import React, { Component, Fragment } from 'react';
import Modal from 'react-bootstrap/Modal';
import { Button, Form } from 'react-bootstrap';
import { connect } from 'react-redux';
import { storeData, updateData } from '../../../store/actions/marqueeActions'


import ReactSummernote from 'react-summernote';
import 'react-summernote/dist/react-summernote.css'; // import styles
import $ from 'jquery';
import 'bootstrap/dist/css/bootstrap.css';
window.jQuery = $;
require('bootstrap');


class Edit extends Component {
    constructor(props) {
        super(props);
        this.state = {
            id: props.data._id || null,
            title: props.data.title || '',
            description: props.data.description || '',
            isWait: false
        }
    }
    changeHandler = event => {
        this.setState({
            [event.target.name]: event.target.value
        })
    }
    submitHandler = async event => {
        event.preventDefault();
        this.setState({ isWait: true })
        let { id } = this.state

        let { actionIsDone, storeData, updateData } = this.props
        let response = false
        if (id) {
            response = await updateData(this.state, id)
        } else {
            response = await storeData(this.state)
        }
        if (response) actionIsDone()
        this.setState({ isWait: false })
    }
    render() {
        let { id, title, description, isWait } = this.state
        let isDone = title && description
        return (
            <Fragment>
                <Modal show={this.props.isOpen} onHide={this.props.closeHandler}>
                    <Modal.Header closeButton>
                        <Modal.Title>{id ? `Edit Marquee information` : `Add Marquee`}</Modal.Title>
                    </Modal.Header>
                    <Modal.Body>
                        <Form onSubmit={this.submitHandler}>
                            <Form.Group>
                                <Form.Label>Title <span>*</span></Form.Label>
                                <Form.Control
                                    type="text"
                                    name="title"
                                    defaultValue={title}
                                    onChange={this.changeHandler}
                                    placeholder="Enter Title"
                                    required
                                />
                            </Form.Group>

                            <Form.Group>
                                <Form.Label>Description</Form.Label>
                                <ReactSummernote
                                    defaultValue={description}
                                    className='w-100'
                                    options={{
                                        lang: 'ru-RU',
                                        height: 120,
                                        dialogsInBody: true,
                                        toolbar: [
                                            ['style', ['style']],
                                            ['font', ['bold', 'underline', 'clear']],
                                            ['para', ['ul', 'ol', 'paragraph']],
                                            ['insert', ['link']],
                                        ],
                                    }}
                                    onChange={(description) => this.setState({ description })}
                                />
                            </Form.Group>

                            <Button type="submit" variant="dark" disabled={!isDone} size="lg" block>
                                {isWait ? `Please Wait...` : `Save`}
                            </Button>
                        </Form>
                    </Modal.Body>
                </Modal>
            </Fragment>
        )
    }
}
const mapStateToProps = state => ({
    common: state.common,
})
export default connect(mapStateToProps, { storeData, updateData })(Edit);