import React, { Component, Fragment } from 'react';
import Modal from 'react-bootstrap/Modal';
import { Button, Form } from 'react-bootstrap';
import { connect } from 'react-redux';
import { storeData, updateData } from '../../../store/actions/airportActions'


class Edit extends Component {
    constructor(props) {
        super(props);
        this.state = {
            id: props.data._id || null,
            iata: props.data.iata || null,
            name: props.data.name || null,
            short: props.data.short || null,
            city: props.data.city || null,
            iso: props.data.iso || null,
            lat: props.data.lat || null,
            lon: props.data.lon || null,
            isWait: false
        }
    }
    UNSAFE_componentWillReceiveProps(props) {
        this.setState({
            ...props.data
        })
    }
    changeHandler = event => {
        this.setState({
            [event.target.name]: event.target.value
        })
    }
    submitHandler = async event => {
        event.preventDefault()
        this.setState({ isWait: true })
        let { storeData, updateData, actionIsDone, data } = this.props
        let response = false;
        let { id } = this.state
        if (id) {
            response = await updateData(this.state, id)
        } else {
            response = await storeData(this.state)
        }
        response && actionIsDone()
        this.setState({ isWait: false })
    }
    render() {
        let { iata, name, short, iso, lat, lon, city, isWait } = this.state
        let isDone = iata && name && iso

        return (
            <Fragment>
                <Modal show={this.props.isOpen} onHide={this.props.closeHandler}>
                    <Modal.Header closeButton>
                        <Modal.Title>Airport Information</Modal.Title>
                    </Modal.Header>
                    <Modal.Body>
                        <Form onSubmit={this.submitHandler}>
                            <Form.Group>
                                <Form.Label>Short Name<span>*</span></Form.Label>
                                <Form.Control
                                    type="text"
                                    name="short"
                                    defaultValue={short}
                                    onChange={this.changeHandler}
                                    placeholder="Enter Short Name"
                                />
                            </Form.Group>
                            <Form.Group>
                                <Form.Label>Name<span>*</span></Form.Label>
                                <Form.Control
                                    type="text"
                                    name="name"
                                    defaultValue={name}
                                    onChange={this.changeHandler}
                                    placeholder="Enter Name"
                                />
                            </Form.Group>
                            <Form.Group>
                                <Form.Label>City</Form.Label>
                                <Form.Control
                                    type="text"
                                    name="city"
                                    defaultValue={city}
                                    onChange={this.changeHandler}
                                    placeholder="Enter City"
                                />
                            </Form.Group>
                            <Form.Group>
                                <Form.Label>ISO</Form.Label>
                                <Form.Control
                                    type="text"
                                    name="iso"
                                    defaultValue={iso}
                                    onChange={this.changeHandler}
                                    placeholder="Enter ISO"
                                />
                            </Form.Group>
                            <Form.Group>
                                <Form.Label>Lat</Form.Label>
                                <Form.Control
                                    type="text"
                                    name="lat"
                                    defaultValue={lat}
                                    onChange={this.changeHandler}
                                    placeholder="Enter lat"
                                />
                            </Form.Group>
                            <Form.Group>
                                <Form.Label>Lan</Form.Label>
                                <Form.Control
                                    type="text"
                                    name="lon"
                                    defaultValue={lon}
                                    onChange={this.changeHandler}
                                    placeholder="Enter lon"
                                />
                            </Form.Group>
                            <Button type="submit" variant="dark" disabled={!isDone} size="lg" block>
                                {isWait ? `Please Wait...` : `Submit`}
                            </Button>
                        </Form>
                    </Modal.Body>
                </Modal>
            </Fragment>
        )
    }
}
export default connect(null, { storeData, updateData })(Edit);