import React, { Component, Fragment } from 'react';
import Axios from 'axios';
import ReactTable from 'react-table';
import 'react-table/react-table.css';
import Edit from './Edit';
import { Button, Form } from 'react-bootstrap';
import { Link } from 'react-router-dom';


class AdminUser extends Component {
    constructor(props) {
        super(props);

        let type = null

        let path = props.match.path
        if (path === "/backend/admin/user/b2c") {
            type = 1
        } else if (path === "/backend/admin/user/b2b") {
            type = 2
        } else if (path === "/backend/admin/user/moderators") {
            type = 3
        }

        this.state = {
            data: [],
            loading: true,
            limit: 10,
            page: 1,
            pages: -1,
            isOpen: false,
            editData: {},
            type,
            name: ''
        }
    }
    componentDidMount() {
        this.fetchData();
    }
    fetchData(limit = this.state.limit, page = this.state.page, type = this.state.type, name = this.state.name) {
        this.setState({
            data: [],
            loading: true
        })
        Axios.post(`/api/users/get`, { limit, page, type, name })
            .then((res) => {
                this.setState({
                    data: res.data.data.docs,
                    page: res.data.data.page,
                    pages: res.data.data.totalPages,
                    limit: res.data.data.limit,
                    loading: false
                })
            })
            .catch(err => console.error(err))
    }
    actionIsDone = () => {
        this.setState({ isOpen: false, editData: {} })
        this.fetchData();
    }
    closeHandler = () => {
        this.setState({ isOpen: false, editData: {} })
    }
    typeChange = type => {
        this.setState({ type })
        this.fetchData('', '', type);
    }
    changeHandler = event => {
        let name = event.target.value
        this.setState({ name })
        this.fetchData('', '', '', event.target.value);
    }
    render() {
        let { data, loading, pages, limit, isOpen, editData, type, name } = this.state
        const columns = [
            {
                Header: 'ID',
                accessor: 'uniqueCode',
                Cell: row => <Link className="link-color" to={`/backend/counter/${row.original.type === 2 ? row.original.agentId : row.original._id}`}>{row.original.uniqueCode}</Link>,
            },
            {
                Header: 'Full Name',
                accessor: 'name',
                Cell: row => <Link className="link-color" to={`/backend/counter/${row.original.type === 2 ? row.original.agentId : row.original._id}`}>{row.original.name}</Link>,
            },
            {
                Header: 'Available Balance',
                accessor: 'balance',
            },
            {
                Header: 'Email',
                accessor: 'email',
            },
            {
                Header: 'Contact Number',
                accessor: 'contact',
            },
            {
                Header: 'Type',
                accessor: 'type',
                Cell: row => <span>{(row.original.type === 1 && "User") || (row.original.type === 2 && "B2B") || (row.original.type === 3 && "Admin")}</span>,
            },
            {
                Header: 'Status',
                accessor: 'status',
                Cell: row => <span>{(row.original.status === 0 && "Pending") || (row.original.status === 1 && "Active") || (row.original.status === 2 && "Blocked")}</span>,
            },
            {
                Header: 'Action',
                Cell: row => <span><button className="btn btn-dark" onClick={() => this.setState({ isOpen: true, editData: row.original })}><i className="fa fa-edit"></i></button>
                </span>,
            },
        ];
        return (
            <Fragment>
                <div className="bg-light">
                    <div className="section-padding-top">
                        <div className="container bg-white">
                            <div className="section-title text-center">
                                <h2>{(type === null && "List of All Users") || (type === 1 && "List of B2C User") || (type === 2 && "List of B2B User") || (type === 3 && "List of Moderators")}</h2>
                            </div>

                            <div className="row">
                                <div className="col-lg-3">
                                    <Form.Group>
                                        <Form.Control
                                            type="text"
                                            name="name"
                                            value={name}
                                            onChange={this.changeHandler}
                                            placeholder="Search By Name"
                                        />
                                    </Form.Group>
                                </div>
                                <div className="col-lg-9">
                                    <Button type="button" variant="dark" onClick={() => this.setState({ isOpen: true })}><i className="fa fa-plus" /></Button>
                                </div>
                            </div>


                            <div className="row">
                                <div className="col-lg-12">
                                    <ReactTable
                                        data={data}
                                        columns={columns}
                                        defaultPageSize={limit}
                                        minRows={1}
                                        noDataText='No rows found'
                                        loading={loading}
                                        manual
                                        pages={pages}
                                        onPageChange={(page) => this.fetchData(limit, page + 1)}
                                        onPageSizeChange={(limit, page) => this.fetchData(limit, page)}
                                    />
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
                {isOpen &&
                    <Edit
                        isOpen={isOpen}
                        closeHandler={this.closeHandler}
                        actionIsDone={this.actionIsDone.bind(this)}
                        data={editData}
                    />
                }
            </Fragment>
        );
    }
}
export default AdminUser