import React, { Component, Fragment } from 'react'
import { MdLinearScale } from "react-icons/md";
import { getMyDate, getMyDay, getMyFullYear, getMyMonth, getMyTime, timeDifference } from "./../../../utils/helper";
import { GetAirport, GetAirline } from '../../../utils/GetAirportAirline';
import { GetTransitTime } from "./Layover";


class SegmentInfo extends Component {
    render() {
        let { segment } = this.props

        let firstSegment = segment[0]["$"];
        let lastSegment = segment[segment.length - 1]["$"];

        let differ = timeDifference(firstSegment.DepartureTime, lastSegment.ArrivalTime)
        // differ = `${differ.slice(0, 2)}h ${differ.slice(3)}m`
        return (
            <Fragment>
                <div className="row">
                    <div className="col-lg-2 logo-line pr-0">
                        <GetAirline code={firstSegment.Carrier} type='logo' />
                        <p className="normaltext"><GetAirline code={firstSegment.Carrier} type='abc' /></p>
                        <p className="subtext">
                            {firstSegment.Carrier} {firstSegment.FlightNumber} || {firstSegment.Equipment}
                        </p>
                    </div>

                    <div className="col-lg-3">
                        {`${getMyDay(firstSegment.DepartureTime)}, ${getMyDate(
                            firstSegment.DepartureTime
                        )} ${getMyMonth(firstSegment.DepartureTime)} ${getMyFullYear(
                            firstSegment.DepartureTime
                        )}`}
                        <br />
                        <h4 className="mb-0 bigtext">
                            {getMyTime(firstSegment.DepartureTime)}
                        </h4>
                        <p className="normalfont"><GetAirport code={firstSegment.Origin} limit={26} /></p>
                    </div>
                    <div className="col-lg-4 text-center p-0 d-flex justify-content-center flex-column flex-wrap">
                        <p className='flight-total-time'><i className='fa fa-clock-o' title='TOTAL TIME' /> {differ}</p>
                        <div className="progress-line">
                            {segment.length === 1 && 'Non-Stop'}
                            <ul className='d-flex flex-wrap justify-content-between transit-list-area mb-0'>
                                {segment.length > 1 && segment.map((item, index) => {
                                    if (index > 0 && index < segment.length) {
                                        return <li className="transit-list-items d-flex flex-column" key={index}>
                                            <div className='d-flex flex-wrap align-items-center'>
                                                <MdLinearScale />
                                                <span className="transit-airport-code">
                                                    {item['$'].Origin}
                                                </span>
                                                <MdLinearScale />
                                            </div>
                                            <div>
                                                <GetTransitTime ArrivalTime={segment[index - 1]['$'].ArrivalTime} DepartureTime={segment[index]['$'].DepartureTime} />
                                            </div>
                                        </li>
                                    }
                                    return ''
                                })}
                            </ul>
                        </div>
                    </div>
                    <div className="col-lg-3">
                        {`${getMyDay(lastSegment.ArrivalTime)}, ${getMyDate(
                            lastSegment.ArrivalTime
                        )} ${getMyMonth(lastSegment.ArrivalTime)} ${getMyFullYear(
                            lastSegment.ArrivalTime
                        )}`}
                        <br />
                        <h4 className="mb-0 bigtext">
                            {getMyTime(lastSegment.ArrivalTime)}
                        </h4>
                        <p className="normalfont">
                            <GetAirport code={lastSegment.Destination} limit={26} />
                        </p>
                    </div>
                </div>
            </Fragment>
        )
    }
}
export default SegmentInfo