export const flightType = (airports, segments) => {
    let airportCode = []
    for (let i = 0; segments.length > i; i++) {
        airportCode.push(segments[i].origin)
        airportCode.push(segments[i].destination)
    }

    let countryCode = []
    for (let i = 0; airportCode.length > i; i++) {
        for (let j = 0; airports.length > j; j++) {
            if (airportCode[i] === airports[j].iata) {
                countryCode.push(airports[j].iso)
            }
        }
    }
    let wayType = ''
    if (allEqual(countryCode)) {
        wayType = 'Domestic'
    } else {
        wayType = 'International'
    }
    return wayType
}
const allEqual = arr => arr.every(v => v === arr[0])