import React, { Component, Fragment } from 'react';
import Modal from 'react-bootstrap/Modal';
import { Button, Form } from 'react-bootstrap';
import { connect } from 'react-redux';
import { storeData, updateData } from '../../../store/actions/airlineActions'


class Edit extends Component {
    constructor(props) {
        super(props);
        this.state = {
            id: props.data._id || null,
            code: props.data.code || null,
            name: props.data.name || null,
            image: props.data.image || null,
            isWait: false,
            filePath: props.data.image || null,
        }
    }
    UNSAFE_componentWillReceiveProps(props) {
        this.setState({
            ...props.data
        })
    }
    changeHandler = event => {
        this.setState({
            [event.target.name]: event.target.value
        })
    }
    fileUploadHandler = event => {
        this.setState({
            image: event.target.files[0],
        });
        let reader = new FileReader()
        let _this = this
        reader.onload = function (r) {
            _this.setState({
                filePath: r.target.result,
            });
        }
        reader.readAsDataURL(event.target.files[0]);
    }
    submitHandler = async event => {
        event.preventDefault()
        this.setState({ isWait: true })
        let { storeData, updateData, actionIsDone, data } = this.props
        let response = false;

        let { code, name, image, id } = this.state

        let fd = new FormData()
        fd.append('file', image)
        fd.append('code', code)
        fd.append('name', name)
        fd.append('image', image)


        if (id) {
            response = await updateData(fd, id)
        } else {
            response = await storeData(fd)
        }
        response && actionIsDone()
        this.setState({ isWait: false })
    }
    render() {
        let { code, name, isWait, filePath } = this.state
        let isDone = code && name

        return (
            <Fragment>
                <Modal show={this.props.isOpen} onHide={this.props.closeHandler}>
                    <Modal.Header closeButton>
                        <Modal.Title>Airport Information</Modal.Title>
                    </Modal.Header>
                    <Modal.Body>
                        <Form onSubmit={this.submitHandler}>
                            <Form.Group>
                                <Form.Label>Code<span>*</span></Form.Label>
                                <Form.Control
                                    type="text"
                                    name="code"
                                    defaultValue={code}
                                    onChange={this.changeHandler}
                                    placeholder="Enter code"
                                />
                            </Form.Group>
                            <Form.Group>
                                <Form.Label>Name<span>*</span></Form.Label>
                                <Form.Control
                                    type="text"
                                    name="name"
                                    defaultValue={name}
                                    onChange={this.changeHandler}
                                    placeholder="Enter Name"
                                />
                            </Form.Group>
                            <Form.Group className="custom-file mb-4">
                                <Form.Control
                                    id="file"
                                    type="file"
                                    className="custom-file-input"
                                    onChange={this.fileUploadHandler}
                                />
                                <Form.Label htmlFor="file" className='custom-file-label'>Choose File</Form.Label>
                            </Form.Group>
                            <Form.Group>
                                {filePath && <img src={filePath} alt='Flag' width='150' />}
                            </Form.Group>
                            <Button type="submit" variant="dark" disabled={!isDone} size="lg" block>
                                {isWait ? `Please Wait...` : `Submit`}
                            </Button>
                        </Form>
                    </Modal.Body>
                </Modal>
            </Fragment>
        )
    }
}
export default connect(null, { storeData, updateData })(Edit);