import React, { Component, Fragment } from 'react';
import Axios from 'axios';
import ReactTable from 'react-table';
import 'react-table/react-table.css';
import { Link } from 'react-router-dom';
import { connect } from 'react-redux';
import { dateTime } from './../../../utils/helper';
import { Form } from 'react-bootstrap';


class Statement extends Component {
    constructor(props) {
        super(props);
        this.state = {
            data: [],
            loading: true,
            limit: 100,
            page: 1,
            pages: -1,
            status: 2,
            userId: null,
            name: ''
        }
    }
    componentDidMount() {
        this.fetchData();
    }
    fetchData(limit = this.state.limit, page = this.state.page, name = this.state.name) {
        let { userId, status } = this.state
        this.setState({
            data: [],
            loading: true,
        })
        Axios.post(`/api/statements/get`, { limit, page, status, userId, name })
            .then(res => {
                this.setState({
                    data: res.data.data.docs,
                    page: res.data.data.page,
                    pages: res.data.data.totalPages,
                    limit: res.data.data.limit,
                    loading: false
                })
            })
    }
    changeHandler = event => {
        let name = event.target.value
        this.setState({ name })
        this.fetchData('', '', event.target.value);
    }
    render() {
        let { data, loading, pages, limit, name } = this.state

        const columns = [
            {
                Header: 'SL',
                accessor: 'index',
                Cell: row => <span>{row.index + 1}</span>,
                width: 30,
            },
            {
                Header: 'Pay Trx ID',
                accessor: 'refId',
            },
            {
                Header: 'Ref ID',
                accessor: 'particular',
            },
            {
                Header: 'Agent ID',
                Cell: row => <Link className="link-color" to={`/backend/users/${row.original.userId.type === 2 ? row.original.userId.agentId : row.original.userId._id}`}>{row.original.userId.uniqueCode}</Link>,
            },
            {
                Header: 'Create Date',
                accessor: 'createdAt',
                Cell: row => <span>{dateTime(row.original.createdAt)}</span>,
            },
            {
                Header: 'Amount',
                accessor: 'amount',
                Cell: row => <span>{row.original.type === 1 ? <span className='text-success'>+ {row.original.amount}</span> : <span className='text-danger'>- {row.original.amount}</span>}</span>,
            },
            {
                Header: 'Status',
                accessor: 'status',
            },
            {
                Header: 'Alert',
                accessor: 'status',
                Cell: row => <span>&nbsp;</span>,
            },
        ];

        return (
            <Fragment>
                <div className="bg-light">
                    <div className="section-padding-top">
                        <div className="container bg-white">

                            <div className="section-title text-center"><h2>Statement</h2></div>

                            <div className="row">
                                <div className="col-lg-3">
                                    <Form.Group>
                                        <Form.Control
                                            type="text"
                                            name="name"
                                            value={name}
                                            onChange={this.changeHandler}
                                            placeholder="Search"
                                        />
                                    </Form.Group>
                                </div>
                            </div>

                            <div className="row">
                                <div className="col-lg-12">
                                    <ReactTable
                                        data={data}
                                        columns={columns}
                                        defaultPageSize={limit}
                                        minRows={1}
                                        noDataText='No rows found'
                                        loading={loading}
                                        manual
                                        pages={pages}
                                        onPageChange={(page) => this.fetchData(limit, page + 1)}
                                        onPageSizeChange={(limit, page) => this.fetchData(limit, page)}
                                    />
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </Fragment>
        );
    }
}
const mapStateToProps = state => ({
    common: state.common,
    user: state.auth.user,
})
export default connect(mapStateToProps)(Statement)