import React, { Component, Fragment } from "react";
import { connect } from "react-redux";
import { airPricingReq } from "../../../store/actions/flightActions";
import ButtonSection from "./ButtonSection";
import TransitItem from "./TransitItem";
import SegmentInfo from "./SegmentInfo";


class FlightList extends Component {
  state = {
    isWait: false,
    isCollapse: false,
  };

  submitHandler = async () => {
    this.setState({ isWait: true })
    let { segment, pricing, history, airPricingReq } = this.props

    let response = await airPricingReq(pricing, segment)
    if (response) {
      history.push('/reservation')
    } else {
      this.setState({ isWait: false })
    }
  }


  render() {
    let { isWait, isCollapse } = this.state;
    let { segment, pricing, fareList, isBook } = this.props;



    return (
      <Fragment>
        <div className="flight-result-section">
          <div className="row">

            <div className="col-lg-8">
              <SegmentInfo
                segment={segment[0]}
              />
              {segment[1].length > 0 && <span>
                <div className="border-bottom my-1"></div>
                <SegmentInfo
                  segment={segment[1]}
                />
              </span>}

              {segment[2].length > 0 && <span>
                <div className="border-bottom my-1"></div>
                <SegmentInfo
                  segment={segment[2]}
                />
              </span>}

              {segment[3].length > 0 && <span>
                <div className="border-bottom my-1"></div>
                <SegmentInfo
                  segment={segment[3]}
                />
              </span>}
            </div>

            <div className="col-lg-4 pr-0">
              <div className="air-manage-btn-section">

                {/* All Buttons Area */}

                <ButtonSection
                  fareList={fareList}
                  pricing={pricing['pricing']}
                  transitHandler={() => this.setState({ isCollapse: !isCollapse })}
                  isBook={isBook}
                />

                <div className="price-area">
                  {pricing['pricing']["$"].TotalPrice.slice(3)}
                </div>
                <div className="book-btn-area">
                  {isBook &&
                    <button
                      className="btn btn-block btn-red"
                      disabled={isWait}
                      onClick={this.submitHandler}
                    >
                      {isWait ? 'Loading...' : 'Book'}
                    </button>}
                </div>
              </div>
            </div>
          </div>


          {isCollapse && <TransitItem
            segment={[...segment[0], ...segment[1], ...segment[2], ...segment[3]]}
            segmentList={segment}
            isCollapse={isCollapse}
          />}

        </div>
      </Fragment>
    );
  }
}
export default connect(null, { airPricingReq })(FlightList);
