import React, { Component, Fragment } from 'react';
import { Link } from 'react-router-dom';

class UserHeader extends Component {
    constructor(props) {
        super(props);
        this.state = {
            userId: props.userId,
        }
    }
    render() {
        let { userId } = this.state
        let { pathname } = window.location
        let activeUrl = pathname.slice(1, pathname.length - 25)
        return (
            <Fragment>
                <div className="row mb-3">
                    <div className="col-lg-12">
                        <div className="section-title">
                            <Link
                                to={`/backend/ticket/booking/${userId}`}
                                className={activeUrl === 'backend/ticket/booking' && 'active'}
                            >Booking</Link>
                            <Link
                                to={`/backend/ticket/on-hold/${userId}`}
                                className={activeUrl === 'backend/ticket/on-hold' && 'active'}
                            >On Hold</Link>
                            <Link
                                to={`/backend/ticket/ticket/${userId}`}
                                className={activeUrl === 'backend/ticket/ticket' && 'active'}
                            >Ticketed</Link>
                            <Link
                                to={`/backend/ticket/in-process/${userId}`}
                                className={activeUrl === 'backend/ticket/in-process' && 'active'}
                            >In Process</Link>
                            <Link
                                to={`/backend/ticket/expire/${userId}`}
                                className={activeUrl === 'backend/ticket/expire' && 'active'}
                            >Expire</Link>
                            <Link
                                to={`/backend/ticket/unconfirmed/${userId}`}
                                className={activeUrl === 'backend/ticket/unconfirmed' && 'active'}
                            >Unconfirmed</Link>
                            <Link
                                to={`/backend/ticket/cancelled/${userId}`}
                                className={activeUrl === 'backend/ticket/cancelled' && 'active'}
                            >Cancelled</Link>
                            <Link
                                to={`/backend/ticket/refund/${userId}`}
                                className={activeUrl === 'backend/ticket/refund' && 'active'}
                            >Refund</Link>
                            <Link
                                to={`/backend/ticket/void/${userId}`}
                                className={activeUrl === 'backend/ticket/void' && 'active'}
                            >Void</Link>
                            <Link
                                to={`/backend/ticket/no-show/${userId}`}
                                className={activeUrl === 'backend/ticket/no-show' && 'active'}
                            >No-Show</Link>
                            <Link
                                to={`/backend/customer/${userId}`}
                                className={activeUrl === 'backend/customer' && 'active'}
                            >Customer List</Link>
                            <Link
                                to={`/backend/statements/${userId}`}
                                className={activeUrl === 'backend/statements' && 'active'}
                            >Statement</Link>
                            <Link
                                to={`/backend/deposits/${userId}`}
                                className={activeUrl === 'backend/deposits' && 'active'}
                            >Deposit</Link>
                            <Link
                                to={`/backend/counter/${userId}`}
                                className={activeUrl === 'backend/counter' && 'active'}
                            >Counter</Link>
                            <Link
                                to={`/backend/bonus/${userId}`}
                                className={activeUrl === 'backend/bonus' && 'active'}
                            >Incentive N Bonus List</Link>
                        </div>
                    </div>
                </div>
            </Fragment>
        );
    }
}
export default UserHeader