import React, { Component, Fragment } from 'react';
import Axios from 'axios'
import dateFormat from 'dateformat';
import UserHeader from '../user/UserHeader';

class Report extends Component {
    constructor(props) {
        super(props);
        let userId = null
        if (props.match.params.userId) {
            userId = props.match.params.userId
        }
        this.state = {
            userId,
            user: {},
            totalSearch: 0,
            reservation: 0,
            inProcess: 0,
            issue: 0,
            cancel: 0,
            ticket: [],
            search: []
        }
    }
    componentDidMount() {
        let url = `/api/users/detail`
        if (this.state.userId) {
            url = `/api/users/detail/${this.state.userId}`
        }
        Axios.get(url)
            .then(res => {
                this.setState({
                    ticket: res.data.data.ticket,
                    search: res.data.data.search,
                })
            })
            .catch(err => console.log(err))
    }
    render() {
        let { userId, ticket, search } = this.state
        let daySearch = []
        let dayTicket = []
        if (search.length > 0) {
            daySearch = search.filter(item => dateFormat(new Date(item.createdAt), "yyyy-mm-dd") === dateFormat(new Date(), "yyyy-mm-dd"))
        }
        if (ticket.length > 0) {
            dayTicket = ticket.filter(item => dateFormat(new Date(item.createdAt), "yyyy-mm-dd") === dateFormat(new Date(), "yyyy-mm-dd"))
        }
        return (
            <Fragment>
                <div className="bg-light">
                    <div className="section-padding-top">
                        <div className="container bg-white">
                            {userId && <UserHeader userId={userId} />}

                            <div className="section-title text-center">
                                <h2>Report</h2>
                            </div>

                            <table className='table'>
                                <thead>
                                    <tr>
                                        <th>SL NO.</th>
                                        <th>DATE</th>
                                        <th>T SALE</th>
                                        <th>T DEPOSIT</th>
                                        <th>T UNUSED</th>
                                        <th>T COMMISSION RECEIVED</th>
                                        <th>T COMMISSION PAY</th>
                                        <th>T EXPENSE</th>
                                        <th>T PROFIT</th>
                                        <th>NET PROFIT</th>
                                        <th>STATUS</th>
                                    </tr>
                                </thead>
                                <tbody>
                                    <tr>
                                        <td>1</td>
                                        <td>{daySearch.length}</td>
                                        <td>{dayTicket.filter(item => item.status === 'booking').length}</td>
                                        <td>{dayTicket.filter(item => item.status === 'on-hold').length}</td>
                                        <td>{dayTicket.filter(item => item.status === 'ticket').length}</td>
                                        <td>{dayTicket.filter(item => item.status === 'in-process').length}</td>
                                        <td>{dayTicket.filter(item => item.status === 'expire').length}</td>
                                        <td>{dayTicket.filter(item => item.status === 'unconfirmed').length}</td>
                                        <td>{dayTicket.filter(item => item.status === 'no-show').length}</td>
                                        <td>{dayTicket.filter(item => item.status === 'cancelled').length}</td>
                                        <td>{dayTicket.filter(item => item.status === 'refund').length}</td>
                                        <td>{dayTicket.filter(item => item.status === 'void').length}</td>
                                    </tr>
                                    <tr>
                                        <td>Total</td>
                                        <td>{search.length}</td>
                                        <td>{ticket.filter(item => item.status === 'booking').length}</td>
                                        <td>{ticket.filter(item => item.status === 'on-hold').length}</td>
                                        <td>{ticket.filter(item => item.status === 'ticket').length}</td>
                                        <td>{ticket.filter(item => item.status === 'in-process').length}</td>
                                        <td>{ticket.filter(item => item.status === 'expire').length}</td>
                                        <td>{ticket.filter(item => item.status === 'unconfirmed').length}</td>
                                        <td>{ticket.filter(item => item.status === 'no-show').length}</td>
                                        <td>{ticket.filter(item => item.status === 'cancelled').length}</td>
                                        <td>{ticket.filter(item => item.status === 'refund').length}</td>
                                        <td>{ticket.filter(item => item.status === 'void').length}</td>
                                    </tr>
                                </tbody>
                            </table>
                        </div>
                    </div>
                </div>
            </Fragment>
        );
    }
}
export default Report