import React, { Fragment } from 'react'
import moment from 'moment';


export const GetLayover = (props) => {
    let { firstSegment, lastSegment } = props

    let lastTime = new Date(firstSegment['$'].ArrivalTime)
    let nowTime = new Date(lastSegment['$'].DepartureTime)

    let layover = moment.utc(moment(nowTime, "DD/MM/YYYY HH:mm:ss").diff(moment(lastTime, "DD/MM/YYYY HH:mm:ss"))).format("HH:mm")
    return (
        <Fragment>
            <div className="row">
                <div className="col col-md-10 lay"></div>
                <div className="col col-md-2">
                    <div className='layover'>
                        {`Layover ${layover.slice(0, 2)}h ${layover.slice(3)}m`}
                    </div>
                </div>
                {/* <div className="col col-md-1 lay"></div> */}
            </div>
        </Fragment>
    );
}


export const GetTransitTime = (props) => {
    let { ArrivalTime, DepartureTime } = props
    let lastTime = new Date(ArrivalTime)
    let nowTime = new Date(DepartureTime)

    let layover = moment.utc(moment(nowTime, "DD/MM/YYYY HH:mm:ss").diff(moment(lastTime, "DD/MM/YYYY HH:mm:ss"))).format("HH:mm")
    return <div className='transit-time'>{`${layover.slice(0, 2)}h ${layover.slice(3)}m`}</div>
}



export const GetTicketLayover = (props) => {
    let { firstSegment, lastSegment } = props

    let lastTime = new Date(firstSegment['$'].ArrivalTime)
    let nowTime = new Date(lastSegment['$'].DepartureTime)

    let layover = moment.utc(moment(nowTime, "DD/MM/YYYY HH:mm:ss").diff(moment(lastTime, "DD/MM/YYYY HH:mm:ss"))).format("HH:mm")
    return (
        <Fragment>
            {`${layover.slice(0, 2)}h ${layover.slice(3)}m`}
        </Fragment>
    );
}