import React, { Component, Fragment } from 'react'
import { Route } from 'react-router-dom'

// Admin Component
import UserDetail from './backend/user/UserDetail';

import Ticket from './backend/ticket/Ticket';
import Profile from './backend/profile/Profile';
import Dashboard from './backend/Dashboard';
import AdminUser from './backend/user/AdminUser';
import BasicInfo from './backend/BasicInfo';
import B2BTicket from './backend/ticket/B2BTicket';
import AirportList from './backend/airport/AirportList';
import AirlineList from './backend/airline/AirlineList';
import CountryList from './backend/country/CountryList';
import Counter from './backend/user/Counter';
import B2BStatement from './backend/account/B2BStatement';
import CustomerList from './backend/user/CustomerList';
import B2BDeposit from './backend/account/B2BDeposit';
import Deposit from './backend/account/Deposit';
import Bonus from './backend/bonus/Bonus';
import B2BBonus from './backend/bonus/B2BBonus';
import Statement from './backend/account/Statement';
import Request from './backend/ticket/Request';
import Slider from './backend/slider/Slider';
import Package from './backend/package/Package';
import Marquee from './backend/marquee/Marquee';
import Service from './backend/service/Service';
import Commission from './backend/commission/Commission';
import Carrier from './backend/carrier/Carrier';
import Report from './backend/account/Report';
import UsefulLink from './backend/useful-link/UsefulLink';
import FooterLogo from './backend/footer-logo/FooterLogo';
import IATARecord from './backend/iata-record/IATARecord';
import Expense from './backend/expense/Expense';
import AgentFinancialRecord from './backend/account/AgentFinancialRecord';
import BestSeller from './backend/account/BestSeller';




class ProtectedRoute extends Component {
    render() {
        return (
            <Fragment>
                {/************************Basic Route************************/}
                <Route path='/backend/home/slider' component={Slider} history={this.props.history} />
                <Route path='/backend/home/package' component={Package} history={this.props.history} />
                <Route path='/backend/home/marquee' component={Marquee} history={this.props.history} />
                <Route path='/backend/home/service' component={Service} history={this.props.history} />
                <Route path='/backend/home/useful-link' component={UsefulLink} history={this.props.history} />
                <Route path='/backend/home/footer-logo' component={FooterLogo} history={this.props.history} />




                <Route path='/backend/profile' component={Profile} history={this.props.history} />
                <Route path='/backend/basic-info' component={BasicInfo} history={this.props.history} />
                <Route path='/backend/dashboard' component={Dashboard} history={this.props.history} />
                <Route path='/backend/admin/iata-record' exact component={IATARecord} history={this.props.history} />
                <Route path='/backend/admin/expense' exact component={Expense} history={this.props.history} />


                {/************************Airport and Airline Route************************/}
                <Route path='/backend/airports' exact component={AirportList} history={this.props.history} />
                <Route path='/backend/airlines' exact component={AirlineList} history={this.props.history} />
                <Route path='/backend/countries' exact component={CountryList} history={this.props.history} />
                <Route path='/backend/agent/commission' component={Commission} history={this.props.history} />
                <Route path='/backend/carrier/manage' component={Carrier} history={this.props.history} />


                {/************************Account Route************************/}
                <Route path='/backend/statements/:userId' exact component={B2BStatement} history={this.props.history} />
                <Route path='/backend/admin/statements' exact component={Statement} history={this.props.history} />
                <Route path='/backend/deposits/:userId' exact component={B2BDeposit} history={this.props.history} />
                <Route path='/backend/admin/deposits' exact component={Deposit} history={this.props.history} />
                <Route path='/backend/admin/reports' exact component={Report} history={this.props.history} />
                <Route path='/backend/agent/financial' exact component={AgentFinancialRecord} history={this.props.history} />
                <Route path='/backend/best/seller' exact component={BestSeller} history={this.props.history} />


                {/************************Agent Ticket Route************************/}
                <Route path='/backend/ticket/:ticketStatus/:userId' exact component={B2BTicket} history={this.props.history} />
                <Route path='/backend/request' exact component={Request} history={this.props.history} />

                <Route path='/backend/counter/:userId' exact component={Counter} history={this.props.history} />
                <Route path='/backend/counter' exact component={Counter} history={this.props.history} />
                <Route path='/backend/customer/:userId' exact component={CustomerList} history={this.props.history} />

                <Route path='/backend/admin/customer' exact component={CustomerList} history={this.props.history} />



                {/************************Admin Ticket Route************************/}
                <Route path='/backend/admin/ticket/:ticketStatus' exact component={Ticket} history={this.props.history} />

                {/************************Bonus Route************************/}
                <Route path='/backend/admin/bonus' exact component={Bonus} history={this.props.history} />


                <Route path='/backend/bonus/:userId' component={B2BBonus} history={this.props.history} />


                {/************************Admin Route************************/}
                <Route path='/backend/admin/user/moderators' component={AdminUser} history={this.props.history} />
                <Route path='/backend/admin/user/b2b' component={AdminUser} history={this.props.history} />
                <Route path='/backend/admin/user/b2c' component={AdminUser} history={this.props.history} />
                <Route path='/backend/admin/user/all' component={AdminUser} history={this.props.history} />
                <Route path='/backend/b2b/user/:agentId' component={UserDetail} history={this.props.history} />
                <Route path='/backend/users/:id' component={UserDetail} history={this.props.history} />

            </Fragment>
        )
    }
}
export default ProtectedRoute