export const textLimit = (text, limit = 25) => {
    if (!text) return null
    return text.length > limit ? `${text.substring(0, limit)}...` : text
}

export const dateTime = (date, type = 1) => {
    if (!date) return ''
    date = new Date(date)
    if (type === 1) {
        return dateFormat(new Date(date), "dd mmm yyyy HH:MM:ss");
    } else if (type === 2) {
        return dateFormat(new Date(date), "dd mmm yyyy");
    } else if (type === 3) {
        return dateFormat(new Date(date), "HH:MM:ss");
    }
}

export const getDateTime = (data, type = 1) => {
    if (!data) return ''
    if (type === 1) {
        return <moment format="D MMM YYYY HH:mm" withTitle>
            {data}
        </moment>
    } else if (type === 2) {
        return <moment format="D MMM YYYY" withTitle>
            {data}
        </moment>
    } else if (type === 3) {
        return <moment format="HH:mm" withTitle>
            {data}
        </moment>
    }
}

export const timeConvert = num => {
    let hours = (num / 60);
    let rHours = Math.floor(hours);
    let minutes = (hours - rHours) * 60;
    let rMinutes = Math.round(minutes);
    return `${rHours}h ${rMinutes}m`
}


