import React, { Component, Fragment } from 'react';
import Modal from 'react-bootstrap/Modal';
import { Button, Form } from 'react-bootstrap';
import { connect } from 'react-redux';
import { storeData, updateData } from '../../../store/actions/carrierActions'

class Edit extends Component {
    constructor(props) {
        super(props);
        this.state = {
            id: props.data._id || null,
            code: props.data.code || '',
            status: props.data.status || false,
            isWait: false
        }
    }
    changeHandler = event => {
        this.setState({
            [event.target.name]: event.target.value
        })
    }
    submitHandler = async event => {
        event.preventDefault()
        this.setState({ isWait: true })
        let { actionIsDone, storeData, updateData } = this.props

        let response = false;
        let { id } = this.state
        if (id) {
            response = await updateData(this.state, id)
        } else {
            response = await storeData(this.state)
        }
        if (response) actionIsDone()
        this.setState({ isWait: false })
    }
    render() {
        let { id, code, status, isWait } = this.state
        let isDone = code
        return (
            <Fragment>
                <Modal show={this.props.isOpen} onHide={this.props.closeHandler}>
                    <Modal.Header closeButton>
                        <Modal.Title>{id ? `Edit Commission` : `Add Commission`}</Modal.Title>
                    </Modal.Header>
                    <Modal.Body>
                        <Form onSubmit={this.submitHandler}>
                            <Form.Group>
                                <Form.Label>Airline Code <span>*</span></Form.Label>
                                <Form.Control
                                    type="text"
                                    name="code"
                                    defaultValue={code}
                                    onChange={this.changeHandler}
                                    placeholder="Enter Airline Code"
                                    required
                                />
                            </Form.Group>
                            <Form.Group>
                                <Form.Check
                                    type="radio"
                                    name="status"
                                    label="Show"
                                    checked={status === true}
                                    value='true'
                                    id='statusTrue'
                                    onChange={(e) => this.setState({ status: true })}
                                />
                            </Form.Group>
                            <Form.Group>
                                <Form.Check
                                    type="radio"
                                    name="status"
                                    label="Hide"
                                    value='false'
                                    id='statusFalse'
                                    checked={status === false}
                                    onChange={(e) => this.setState({ status: false })}
                                />
                            </Form.Group>
                            <Button type="submit" variant="dark" disabled={!isDone} size="lg" block>
                                {isWait ? `Please Wait...` : `Save`}
                            </Button>
                        </Form>
                    </Modal.Body>
                </Modal>
            </Fragment>
        )
    }
}
const mapStateToProps = state => ({
    common: state.common,
})
export default connect(mapStateToProps, { storeData, updateData })(Edit);