import React, { Component, Fragment } from 'react';
import { registration } from '../../store/actions/authActions';
import { connect } from 'react-redux';
import { Link } from 'react-router-dom';
import { Button } from 'react-bootstrap';
import Form from 'react-bootstrap/Form';
import { updateActionStatus } from '../../store/actions/commonActions';

class Register extends Component {
    constructor(props) {
        super(props);
        this.state = {
            name: '',
            contact: '',
            email: '',
            password: '',
            confirmPassword: '',
            type: 1,
            status: 1,
            isAdmin: 0,
            isWait: false,
        }
    }
    changeHandler = event => {
        this.setState({
            [event.target.name]: event.target.value
        })
    }
    submitHandler = async event => {
        event.preventDefault()
        this.setState({ isWait: true })
        let { registration, history } = this.props
        let response = await registration(this.state)
        if (response) history.push('/')
        this.setState({ isWait: false })
    }
    render() {
        let { name, contact, email, password, confirmPassword, isWait, isAdmin } = this.state
        let isDone = name && contact && email && password && (password === confirmPassword)

        return (
            <Fragment>
                <section className="section-padding-top pb-2">
                    <div className="container">
                        <div className="input-form-area">
                            <div className="row">
                                <div className="col-lg-6 col-md-12 offset-3">
                                    <Form onSubmit={this.submitHandler}>

                                        <Form.Group>
                                            <Form.Label>Full Name/Company Name *</Form.Label>
                                            <Form.Control
                                                type="text"
                                                name="name"
                                                value={name}
                                                onChange={this.changeHandler}
                                                placeholder="Enter Full Name"
                                            />
                                        </Form.Group>

                                        <Form.Group>
                                            <Form.Label>Contact Number *</Form.Label>
                                            <Form.Control
                                                type="number"
                                                name="contact"
                                                value={contact}
                                                onChange={this.changeHandler}
                                                placeholder="Enter Contact Number"
                                            />
                                        </Form.Group>

                                        <Form.Group>
                                            <Form.Label>Email address *</Form.Label>
                                            <Form.Control
                                                type="email"
                                                name="email"
                                                value={email}
                                                onChange={this.changeHandler}
                                                autoComplete="false"
                                                className="form-control"
                                                placeholder="Enter Email"
                                            />
                                        </Form.Group>

                                        <Form.Group>
                                            <Form.Label>Password *</Form.Label>
                                            <Form.Control
                                                type="password"
                                                name="password"
                                                value={password}
                                                onChange={this.changeHandler}
                                                className="form-control"
                                                placeholder="Enter Password"
                                            />
                                        </Form.Group>

                                        <Form.Group>
                                            <Form.Label>Confirm Password *</Form.Label>
                                            <Form.Control
                                                type="password"
                                                name="confirmPassword"
                                                value={confirmPassword}
                                                onChange={this.changeHandler}
                                                className="form-control"
                                                placeholder="Enter Confirm Password"
                                            />
                                        </Form.Group>

                                        <Form.Group controlId="formBasicCheckbox">
                                            <Form.Check
                                                className="text-white"
                                                type="checkbox"
                                                label="Is Business Account?"
                                                checked={isAdmin}
                                                onChange={() => this.setState({ isAdmin: !isAdmin })}
                                            />
                                        </Form.Group>

                                        <Button type="submit" variant="dark" disabled={!isDone} size="lg" block>
                                            {isWait ? `Please Wait...` : `Submit`}
                                        </Button>
                                    </Form>

                                    <p className="mt-3">
                                        <span className="float-left">
                                            Already have account? <Link to='/login'>Login here...</Link>
                                        </span>
                                    </p>
                                </div>
                            </div>
                        </div>
                    </div>
                </section>
            </Fragment>
        )
    }
}
export default connect(null, { registration, updateActionStatus })(Register)