import { SET_MESSAGE } from './types'
import Axios from 'axios'

// Order In Process
export const orderInProcess = (data, id) => async dispatch => {
    return Axios.put(`/api/tickets/order/process/${id}`, data)
        .then(res => {
            dispatch({
                type: SET_MESSAGE,
                payload: {
                    message: res.data.message,
                }
            })
            return true
        })
        .catch(err => {
            dispatch({
                type: SET_MESSAGE,
                payload: {
                    message: err.response && err.response.data.message,
                    type: 'error',
                }
            })
            return false
        })
}

