// Get Ticket Detail
export const getTicketDetail = id => async dispatch => {
    return Axios.get(`/api/tickets/${id}`)
        .then(res => {
            return res.data.data
        })
        .catch(err => {
            dispatch({
                type: SET_MESSAGE,
                payload: {
                    message: err.response && err.response.data.message,
                    type: 'error',
                }
            })
            return false
        })
}

// Confirm Ticket
export const inProcessTicket = id => dispatch => {
    Axios.put(`/api/tickets/${id}`, { status: 1 })
        .then(res => {
            dispatch({
                type: SET_MESSAGE,
                payload: {
                    message: res.data.message,
                    type: 'success',
                }
            })
        })
        .catch(err => {
            dispatch({
                type: SET_MESSAGE,
                payload: {
                    message: err.response && err.response.data.message,
                    type: 'error',
                }
            })
        })
}

