import { SET_MESSAGE } from './types'
import Axios from 'axios'
import refreshToken from '../../utils/refreshToken';


// Photo Update
export const updateProfilePhoto = data => async dispatch => {
    return Axios.post(`/api/users/profile-photo-update`, data)
        .then(res => {
            refreshToken()
            dispatch({
                type: SET_MESSAGE,
                payload: {
                    message: res.data.message,
                }
            })
            console.log('res', res.data)
            return true;
        })
        .catch(err => {
            dispatch({
                type: SET_MESSAGE,
                payload: {
                    message: err.response && err.response.data.message,
                    type: 'error',
                }
            })
            return false;
        })
}

