import React, { Component, Fragment } from 'react'
import { Button } from 'react-bootstrap';
import ProfileImg from "../../assets/images/client/2.png"
import Edit from './Edit';
import ChangePassword from './ChangePassword';
import EditPhoto from './EditPhoto';
import EditLogo from './EditLogo';
import { connect } from 'react-redux';


class Profile extends Component {
    constructor(props) {
        super(props);
        this.state = {
            isOpen: false,
            isOpenPassword: false,
            isOpenPhoto: false,
            isOpenLogo: false,
            user: props.user,
        }
    }
    static getDerivedStateFromProps(nextProps, prevState) {
        if (JSON.stringify(nextProps.user) === JSON.stringify(prevState.user)) return null
        return {
            user: nextProps.user,
        }
    }
    closeHandler = () => {
        this.setState({
            isOpen: false,
            isOpenLogo: false,
            isOpenPhoto: false,
            isOpenPassword: false,
        })
    }
    render() {
        let { isOpen, isOpenPassword, isOpenPhoto, isOpenLogo, user } = this.state
        let { uniqueCode, name, email, contact, address, file, logo, logoText, companyName } = user
        return (
            <Fragment>
                <div className="section-padding-top">
                    <div className="container">
                        <div className="row">
                            <div className="col-lg-6">
                                <div className="section-title text-center section-title-padding">
                                    <img src={logo || ProfileImg} alt="ProfileImage" width='100' />
                                    <i className="fa fa-edit profile-edit-icon" onClick={() => this.setState({ isOpenLogo: true })} />
                                </div>
                            </div>

                            <div className="col-lg-6">
                                <div className="section-title text-center section-title-padding">
                                    <img src={file || ProfileImg} alt="ProfileImage" width='100' />
                                    <i className="fa fa-edit profile-edit-icon" onClick={() => this.setState({ isOpenPhoto: true })} />
                                </div>
                            </div>
                        </div>


                        <div className="row mb-5">
                            <div className="col-lg-6">
                                <Button
                                    variant="dark"
                                    className="float-left"
                                    onClick={() => this.setState({ isOpen: true })}
                                >
                                    <i className="fa fa-edit" /> Edit Profile</Button>
                            </div>

                            <div className="col-lg-6">
                                <Button
                                    variant="dark"
                                    className="float-right"
                                    onClick={() => this.setState({ isOpenPassword: true })}
                                >
                                    <i className="fa fa-lock" /> Change Password</Button>
                            </div>
                        </div>

                        <div className="row">
                            <div className="col-lg-12">
                                <div className="section-title section-title-padding">
                                    <p>ID: {uniqueCode}</p>
                                    <p>Name: {name}</p>
                                    <p>Email: {email}</p>
                                    <p>Contact Number: {contact}</p>
                                    <p>Address: {address}</p>
                                    <p>Company Name: {companyName}</p>
                                    <p>Slogan: {logoText}</p>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>


                {isOpen &&
                    <Edit
                        isOpen={isOpen}
                        closeHandler={this.closeHandler}
                        data={user}
                    />
                }
                {isOpenPhoto &&
                    <EditPhoto
                        isOpen={isOpenPhoto}
                        closeHandler={this.closeHandler}
                        file={file}
                    />
                }
                {isOpenLogo &&
                    <EditLogo
                        isOpen={isOpenLogo}
                        closeHandler={this.closeHandler}
                        file={logo}
                    />
                }
                <ChangePassword
                    isOpen={isOpenPassword}
                    closeHandler={this.closeHandler}
                />
            </Fragment>
        );
    }
}
const mapStateToProps = state => ({
    user: state.auth.user,
})
export default connect(mapStateToProps)(Profile)