import React, { Component, Fragment } from 'react';
import Axios from 'axios';
import ReactTable from 'react-table';
import 'react-table/react-table.css';
import { connect } from 'react-redux';
import Edit from './Edit';
import { Button } from 'react-bootstrap';
import { deleteData } from '../../../store/actions/serviceActions'
import { textLimit } from '../../../utils/helper';
import renderHTML from 'react-render-html';


class Service extends Component {
    constructor(props) {
        super(props);
        this.state = {
            data: [],
            loading: true,
            limit: 10,
            page: 1,
            pages: -1,
            status: 2,
            userId: null,
            isOpen: false,
            editData: {},
        }
    }
    componentDidMount() {
        this.fetchData();
    }
    fetchData(limit = this.state.limit, page = this.state.page) {
        this.setState({
            data: [],
            loading: true,
        })
        Axios.get(`/api/services`)
            .then(res => {
                this.setState({
                    data: res.data.data,
                    loading: false
                })
            })
    }
    actionIsDone = () => {
        this.setState({ isOpen: false, editData: {} })
        this.fetchData();
    }
    deleteHandler = id => {
        this.props.deleteData(id)
        this.actionIsDone()
    }
    render() {
        let { data, loading, pages, limit, isOpen, editData } = this.state
        const columns = [
            {
                Header: 'Title',
                accessor: 'title',
            },
            {
                Header: 'Description',
                accessor: 'description',
                Cell: row => <span>{row.original.description && textLimit(renderHTML(row.original.description), 100)}</span>
            },
            {
                Header: "Action",
                Cell: row => <span>
                    <Button type="button" className="mx-2" variant="dark" size="sm" onClick={() => this.setState({ isOpen: true, editData: row.original })}><i className="fa fa-edit" /></Button>

                    <Button type="button" className="mx-2" variant="danger" size="sm" onClick={() => window.confirm('Are you sure?') && this.deleteHandler(row.original._id)}><i className="fa fa-close" /></Button>
                </span>,
            },
        ];

        return (
            <Fragment>
                <div className="bg-light">
                    <div className="section-padding-top">
                        <div className="container bg-white">

                            <div className="section-title text-center"><h2>Service</h2></div>

                            <div className="row">
                                <div className="col-lg-12">
                                    <Button type="button" variant="dark" onClick={() => this.setState({ isOpen: true })}><i className="fa fa-plus" /></Button>
                                </div>
                            </div>

                            <div className="row">
                                <div className="col-lg-12">
                                    <ReactTable
                                        data={data}
                                        columns={columns}
                                        defaultPageSize={limit}
                                        minRows={1}
                                        noDataText='No rows found'
                                        loading={loading}
                                        manual
                                        pages={pages}
                                        onPageChange={(page) => this.fetchData(limit, page + 1)}
                                        onPageSizeChange={(limit, page) => this.fetchData(limit, page)}
                                    />
                                </div>
                            </div>
                        </div>
                    </div>
                </div>

                {isOpen &&
                    <Edit
                        isOpen={isOpen}
                        closeHandler={() => this.setState({ isOpen: false, editData: {} })}
                        actionIsDone={this.actionIsDone.bind(this)}
                        data={editData}
                    />
                }
            </Fragment>
        );
    }
}
const mapStateToProps = state => ({
    common: state.common,
    user: state.auth.user,
})
export default connect(mapStateToProps, { deleteData })(Service)