import { SET_MESSAGE, SET_AIRLINES } from './types'
import Axios from 'axios'

// Get Data
export const getAirline = data => async dispatch => {
    return Axios.get(`/api/airlines/`, data)
        .then(res => {
            dispatch({
                type: SET_AIRLINES,
                payload: res.data.data
            })
            return res.data.data
        })
        .catch(err => {
            dispatch({
                type: SET_MESSAGE,
                payload: {
                    message: err.response && err.response.data.message,
                    type: 'error',
                }
            })
            return false
        })
}


// Store Data
export const storeData = data => async dispatch => {
    return Axios.post(`/api/airlines/`, data)
        .then(res => {
            dispatch({
                type: SET_MESSAGE,
                payload: {
                    message: res.data.message,
                }
            })
            return true;
        })
        .catch(err => {
            dispatch({
                type: SET_MESSAGE,
                payload: {
                    message: err.response && err.response.data.message,
                    type: 'error',
                }
            })
            return false
        })
}

// Update Data
export const updateData = (data, id) => async dispatch => {
    return Axios.put(`/api/airlines/${id}`, data)
        .then(res => {
            dispatch({
                type: SET_MESSAGE,
                payload: {
                    message: res.data.message,
                }
            })
            return true;
        })
        .catch(err => {
            dispatch({
                type: SET_MESSAGE,
                payload: {
                    message: err.response && err.response.data.message,
                    type: 'error',
                }
            })
            return false;
        })
}

// Delete Data
export const deleteData = id => async dispatch => {
    return Axios.delete(`/api/airlines/${id}`)
        .then(res => {
            dispatch({
                type: SET_MESSAGE,
                payload: {
                    message: res.data.message,
                }
            })
            return true;
        })
        .catch(err => {
            dispatch({
                type: SET_MESSAGE,
                payload: {
                    message: err.response && err.response.data.message,
                    type: 'error',
                }
            })
            return false;
        })
}
