import React, { Component, Fragment } from 'react'

class RouteArea extends Component {
    render() {
        let { route } = this.props.data
        return (
            <Fragment>
                <div className="row">
                    <div className="col-lg-6 col-md-6">
                        <div className="flight-root-area flight-hotel-select-section">
                            <span className='active'><i className="fa fa-plane" /><br /> Flight</span>
                            <span><i className="fa fa-hospital-o" /><br /> Hotel</span>
                            {/* <span><i className="fa fa-plane" /> <i className="fa fa-hospital-o" /><br /> Flight + Hotel</span> */}
                        </div>

                    </div>
                    <div className="col-lg-6 col-md-6">
                        <div className="flight-root-area">
                            <span
                                onClick={() => this.props.stateHandler('route', 'RETURN_FLIGHT')}
                                className={route === 'RETURN_FLIGHT' ? 'active' : ''}
                            >Return</span>
                            <span
                                onClick={() => this.props.stateHandler('route', 'ONE_WAY')}
                                className={route === 'ONE_WAY' ? 'active' : ''}
                            >One way</span>
                            <span
                                onClick={() => this.props.stateHandler('route', 'MULTI_CITY')}
                                className={route === 'MULTI_CITY' ? 'active' : ''}
                            >Multi-city</span>
                        </div>
                    </div>
                </div>
            </Fragment >
        )
    }
}
export default RouteArea