import React, { Component, Fragment } from 'react';
import Axios from 'axios';
import UserHeader from './UserHeader';
import dateFormat from 'dateformat';
import { Form } from 'react-bootstrap';
import Loading from '../../layout/Loading';


class CustomerList extends Component {
    constructor(props) {
        super(props);
        this.state = {
            data: [],
            loading: true,
            limit: 500,
            page: 1,
            pages: -1,
            userId: props.match.params.userId || null,
            name: ''
        }
    }
    componentDidMount() {
        this.fetchData();
    }
    fetchData(limit = this.state.limit, page = this.state.page, userId = this.state.userId, name = this.state.name) {
        this.setState({
            data: [],
            loading: true
        })
        Axios.post(`/api/passengers/get`, { limit, page, userId, name })
            .then((res) => {
                this.setState({
                    data: res.data.data.docs,
                    page: res.data.data.page,
                    pages: res.data.data.totalPages,
                    limit: res.data.data.limit,
                    loading: false
                })
            })
    }
    changeHandler = event => {
        let name = event.target.value
        this.setState({ name })
        this.fetchData('', '', '', event.target.value);
    }
    render() {
        let { data, loading, userId, name } = this.state

        let passengers = []
        for (let i = 0; data.length > i; i++) {
            if (passengers.length === 0) {
                passengers.push(data[i])
            } else {
                for (let j = 0; passengers.length > j; j++) {
                    if ((data[i].firstName !== passengers[j].firstName) && (data[i].dob !== passengers[j].dob)) {
                        passengers.push(data[i])
                    }
                }
            }
        }
        let getTotalTravel = (firstName, dob) => {
            let response = data.filter(item => (item.firstName === firstName) && (item.dob === dob)).length
            return response === 0 ? 1 : response

        }


        return (
            <Fragment>
                <div className="bg-light">
                    <div className="section-padding-top">
                        <div className="container bg-white">

                            {userId && <UserHeader userId={userId} />}

                            <div className="section-title text-center">
                                <h2>Customer List</h2>
                            </div>

                            <div className="row">
                                <div className="col-lg-3">
                                    <Form.Group>
                                        <Form.Control
                                            type="text"
                                            name="name"
                                            value={name}
                                            onChange={this.changeHandler}
                                            placeholder="Search By Name"
                                        />
                                    </Form.Group>
                                </div>
                            </div>

                            <div className="row">
                                <div className="col-lg-12">
                                    {loading ? <Loading /> :
                                        <table className='table'>
                                            <thead>
                                                <tr>
                                                    <th>SL NO.</th>
                                                    <th>Name</th>
                                                    <th>Date of Birth</th>
                                                    <th>Contact</th>
                                                    <th>Email</th>
                                                    <th>No of Travel</th>
                                                    <th>Passport No</th>
                                                </tr>
                                            </thead>
                                            <tbody>
                                                {passengers.length !== 0 && passengers.map((item, index) =>
                                                    <tr key={item._id}>
                                                        <td>{index + 1}</td>
                                                        <td>{item.firstName} {item.lastName}</td>
                                                        <td>{item.dob && dateFormat(new Date(item.dob), "dd/mm/yyyy")}</td>
                                                        <td>{item.contact}</td>
                                                        <td>{item.email}</td>
                                                        <td>{getTotalTravel(item.firstName, item.dob)}</td>
                                                        <td>{item.passport}</td>
                                                    </tr>
                                                )}
                                            </tbody>
                                        </table>}
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </Fragment>
        );
    }
}
export default CustomerList