import React, { Component, Fragment } from 'react';
import { FaBriefcase, FaEye, FaMoneyBillAlt } from "react-icons/fa";
import { MdAirlineSeatReclineExtra } from "react-icons/md";
import refundImg from '../../assets/images/refund.svg'
import nonRefundImg from '../../assets/images/non-refund.svg'


class ButtonSection extends Component {
    state = {
        bagPop: false,
        farePop: false,
        seat: false,
    }

    render() {
        let { bagPop, farePop, seat } = this.state

        let { fareList, pricing, isBook } = this.props

        let refund = 'Non-Refundable'
        if (pricing['air:AirPricingInfo'][0]['$'].Refundable) {
            refund = 'Refundable'
        }

        let totalPassenger = 0
        pricing['air:AirPricingInfo'].map(item => {
            return totalPassenger += item['air:PassengerType'].length
        })

        return (
            <Fragment>
                <div className="button-area">
                    <FaBriefcase
                        title="Baggage Rules"
                        className="popicon"
                        onMouseEnter={() =>
                            this.setState({ bagPop: true })
                        }
                        onMouseLeave={() =>
                            this.setState({ bagPop: false })
                        }
                    />

                    {bagPop && (
                        <div className="popup-bar">
                            <table>
                                <thead>
                                    <tr>
                                        <th>Route</th>
                                        <th>Baggage</th>
                                    </tr>
                                </thead>
                                <tbody>
                                    {fareList.filter(item => item['$'].PassengerTypeCode !== 'CNN').map(item =>
                                        <tr key={item['$'].Key}>
                                            {/* <td>{item['$'].PassengerTypeCode}</td> */}
                                            <td>{item['$'].Origin}-{item['$'].Destination}</td>
                                            <td>
                                                {item['air:BaggageAllowance'][0]['air:MaxWeight'][0]['$'] ? <span>{item['air:BaggageAllowance'][0]['air:MaxWeight'][0]['$'].Value} {item['air:BaggageAllowance'][0]['air:MaxWeight'][0]['$'].Unit}</span> : <span>{item['air:BaggageAllowance'][0]['air:NumberOfPieces'][0]} PCS</span>}
                                            </td>
                                        </tr>
                                    )}
                                </tbody>
                            </table>
                        </div>
                    )}

                    <FaMoneyBillAlt
                        title="Fare Breakdown"
                        className="popicon"
                        onMouseEnter={() =>
                            this.setState({ farePop: true })
                        }
                        onMouseLeave={() =>
                            this.setState({ farePop: false })
                        }
                    />
                    {/* Make the Table Dynamic */}
                    {farePop && (
                        <div className="popup-bar">
                            <table>
                                <thead>
                                    <tr>
                                        <th>Type</th>
                                        <th>Base</th>
                                        <th>Taxes</th>
                                        <th>Gross Fare</th>
                                        <th>Discount</th>
                                        <th>PAX</th>
                                        <th>Total PAX Fare</th>
                                    </tr>
                                </thead>
                                <tbody>
                                    {pricing['air:AirPricingInfo'].map(item =>
                                        <tr key={item['$'].Key}>
                                            <td>{item['air:PassengerType'][0]['$'].Code === 'CNN' ? 'CHD' : item['air:PassengerType'][0]['$'].Code}</td>
                                            <td>{item['$'].ApproximateBasePrice.slice(3)}</td>
                                            <td>{item['$'].Taxes.slice(3)}</td>
                                            <td>{Number(item['$'].ApproximateBasePrice.slice(3)) + Number(item['$'].ApproximateTaxes.slice(3))}</td>
                                            <td>00</td>
                                            <td>{item['air:PassengerType'].length}</td>
                                            <td>{item['$'].TotalPrice.slice(3)}</td>
                                        </tr>
                                    )}
                                    <tr>
                                        <td>Total</td>
                                        <td>{pricing['$'].ApproximateBasePrice.slice(3)}</td>
                                        <td>{pricing['$'].ApproximateTaxes.slice(3)}</td>
                                        <td>{pricing['$'].ApproximateTotalPrice.slice(3)}</td>
                                        <td>00</td>
                                        <td>{totalPassenger}</td>
                                        <td>{pricing['$'].TotalPrice.slice(3)}</td>
                                    </tr>
                                </tbody>
                            </table>
                        </div>
                    )}

                    <FaEye
                        title="Flight Details"
                        className="popicon"
                        onClick={this.props.transitHandler}
                    />

                    <MdAirlineSeatReclineExtra
                        title="Available Seats"
                        className="popicon seat-availability"
                        onMouseEnter={() =>
                            this.setState({ seat: true })
                        }
                        onMouseLeave={() =>
                            this.setState({ seat: false })
                        }
                    />
                    {seat && (
                        <div className="popup-bar">
                            <table>
                                <thead>
                                    <tr>
                                        <th>Cabin Class</th>
                                        <th>Class</th>
                                        {isBook && <th>Available Seats</th>}
                                    </tr>
                                </thead>
                                <tbody>
                                    {pricing["air:AirPricingInfo"][0][
                                        "air:BookingInfo"
                                    ].map((item, index) => (
                                        <tr key={index}>
                                            <td>{item["$"].CabinClass}</td>
                                            <td>{item["$"].BookingCode}</td>
                                            {isBook && <td>{item["$"].BookingCount}</td>}
                                        </tr>
                                    ))}
                                </tbody>
                            </table>
                        </div>
                    )}

                    <div className='refund-icon'>
                        {refund === 'Refundable' ?
                            <img src={refundImg} alt='Refund' title={refund} />
                            : <img src={nonRefundImg} alt='Non-Refund' title={refund} />
                        }
                    </div>

                    {/* <FaDollarSign
                        title={refund}
                        className="popicon"
                    /> */}
                </div>
            </Fragment>
        )
    }
}
export default ButtonSection


