import React, { Component, Fragment } from 'react';
import Axios from 'axios';
import ReactTable from 'react-table';
import 'react-table/react-table.css';
import { Link } from 'react-router-dom';
import { connect } from 'react-redux';
import NewEntry from './NewEntry';
import { Button, Form } from 'react-bootstrap';
import { approveData, rejectData } from '../../../store/actions/accountActions';
import { dateTime } from './../../../utils/helper';


class Deposit extends Component {
    constructor(props) {
        super(props);
        this.state = {
            data: [],
            loading: true,
            limit: 100,
            page: 1,
            pages: -1,
            status: null,
            isOpen: false,
        }
    }
    componentDidMount() {
        this.fetchData();
    }
    fetchData(limit = this.state.limit, page = this.state.page, status = this.state.status) {
        this.setState({
            data: [],
            loading: true,
        })
        Axios.post(`/api/deposits/get`, { limit, page, status })
            .then(res => {
                this.setState({
                    data: res.data.data.docs,
                    page: res.data.data.page,
                    pages: res.data.data.totalPages,
                    limit: res.data.data.limit,
                    loading: false
                })
            })
    }
    actionIsDone = () => {
        this.setState({ isOpen: false })
        this.fetchData();
    }
    approveHandler = id => {
        this.props.approveData(id)
        setTimeout(() => {
            this.fetchData()
        }, 1500)
    }
    rejectHandler = id => {
        this.props.rejectData(id)
        setTimeout(() => {
            this.fetchData()
        }, 1500)
    }
    render() {
        let { data, loading, pages, limit, isOpen } = this.state

        const columns = [
            {
                Header: 'SL',
                accessor: 'index',
                Cell: row => <span>{row.index + 1}</span>,
                width: 30,
            },
            {
                Header: 'Ref ID',
                accessor: 'uniqueCode',
            },
            {
                Header: 'By',
                Cell: row => <Link className="link-color" to={`/backend/counter/${row.original.userId.type === 2 ? row.original.userId.agentId : row.original.userId._id}`}>{row.original.userId.uniqueCode}</Link>,
            },
            {
                Header: 'Amount',
                accessor: 'amount',
            },
            {
                Header: 'Available',
                accessor: 'available',
            },
            {
                Header: 'After Deposit',
                accessor: 'afterDeposit',
            },
            {
                Header: 'Create Date',
                accessor: 'createdAt',
                Cell: row => <span>{dateTime(row.original.createdAt)}</span>,
            },
            {
                Header: 'File',
                accessor: 'file',
                Cell: row => <span>{row.original.file && <a href={row.original.file} download >Download</a>}</span>,
            },
            {
                Header: 'status',
                accessor: 'status',
                Cell: row => <span>{(row.original.status === 0 && "Pending") || (row.original.status === 1 && "Approve") || (row.original.status === 2 && <span className='text-danger'>Rejected</span>)}</span>,
            },
            {
                Header: "Action",
                Cell: row => <span>
                    {row.original.status !== 1 &&
                        <Button title='Approve' type="button" className="mx-2" variant="dark" size="sm" onClick={() => window.confirm('Are you sure?') && this.approveHandler(row.original._id)}><i className="fa fa-check" /></Button>}

                    {row.original.status !== 2 &&
                        <Button title='Reject' type="button" variant="danger" size="sm" onClick={() => window.confirm('Are you sure?') && this.rejectHandler(row.original._id)}><i className="fa fa-close" /></Button>}
                </span>,
                width: 300
            },
            {
                Header: 'Note',
                accessor: 'comment',
            },
            {
                Header: 'Admin',
                Cell: row => <span>{row.original.adminId && row.original.adminId.uniqueCode}</span>,
            },
        ];

        return (
            <Fragment>
                <div className="bg-light">
                    <div className="section-padding-top">
                        <div className="container bg-white">
                            <div className="row">
                                <div className="col-lg-3">
                                    <Form.Group>
                                        <Form.Control
                                            type="text"
                                            name="name"
                                            // value={name}
                                            // onChange={this.changeHandler}
                                            placeholder="Search"
                                        />
                                    </Form.Group>
                                </div>
                                <div className="col-lg-9">
                                    <Button type="button" variant="dark" onClick={() => this.setState({ isOpen: true })}><i className="fa fa-plus" /></Button>
                                </div>
                            </div>


                            <div className="row">
                                <div className="col-lg-12">
                                    <ReactTable
                                        data={data}
                                        columns={columns}
                                        defaultPageSize={limit}
                                        minRows={1}
                                        noDataText='No rows found'
                                        loading={loading}
                                        manual
                                        pages={pages}
                                        onPageChange={(page) => this.fetchData(limit, page + 1)}
                                        onPageSizeChange={(limit, page) => this.fetchData(limit, page)}
                                    />
                                </div>
                            </div>
                        </div>
                    </div>
                </div>


                {isOpen &&
                    <NewEntry
                        isOpen={isOpen}
                        closeHandler={() => this.setState({ isOpen: false, editData: {} })}
                        actionIsDone={this.actionIsDone.bind(this)}
                    />
                }

            </Fragment>
        );
    }
}
const mapStateToProps = state => ({
    common: state.common,
    user: state.auth.user,
})
export default connect(mapStateToProps, { approveData, rejectData })(Deposit)