import React, { Component, Fragment } from 'react';
import Modal from 'react-bootstrap/Modal';
import { Button, Form } from 'react-bootstrap';
import { connect } from 'react-redux';
import { storeData, updateData } from '../../../store/actions/bonusActions'
import DatePicker from "react-datepicker";
import "react-datepicker/dist/react-datepicker.css";
import dateFormat from 'dateformat';

class Edit extends Component {
    constructor(props) {
        super(props);
        this.state = {
            id: props.data._id || null,
            date: props.data.date || null,
            agentId: props.data.agentId || '',
            purpose: props.data.purpose || '',
            recommended: props.data.recommended || '',
            amount: props.data.amount || 0,
            totalAmount: props.data.totalAmount || 0,
            isWait: false
        }
    }
    changeHandler = event => {
        this.setState({
            [event.target.name]: event.target.value
        })
    }
    submitHandler = async event => {
        event.preventDefault()
        this.setState({ isWait: true })
        let { id } = this.state
        let { actionIsDone, storeData, updateData } = this.props

        let response = false;
        if (id) {
            response = await updateData(this.state, id)
        } else {
            response = await storeData(this.state)
        }
        if (response) actionIsDone()
        this.setState({ isWait: false })
    }
    render() {
        let { id, date, agentId, purpose, recommended, amount, totalAmount, isWait } = this.state
        let isDone = purpose && recommended && amount && totalAmount
        return (
            <Fragment>
                <Modal show={this.props.isOpen} onHide={this.props.closeHandler}>
                    <Modal.Header closeButton>
                        <Modal.Title>{id ? `Edit Bonus information` : `Add Bonus`}</Modal.Title>
                    </Modal.Header>
                    <Modal.Body>
                        <Form onSubmit={this.submitHandler}>
                            <Form.Group>
                                <Form.Label>Agent ID <span>*</span></Form.Label>
                                <Form.Control
                                    type="text"
                                    name="agentId"
                                    defaultValue={agentId}
                                    onChange={this.changeHandler}
                                    placeholder="Enter Agent ID"
                                    required
                                />
                            </Form.Group>
                            <Form.Group>
                                <Form.Label>Date <span>*</span></Form.Label>
                                <DatePicker
                                    className="form-control"
                                    selected={date && new Date(date)}
                                    onChange={(date) => this.setState({ date: dateFormat(date, "yyyy-mm-dd") })}
                                    placeholder="Enter Date"
                                    dateFormat="dd MMM yyyy"
                                />
                            </Form.Group>
                            <Form.Group>
                                <Form.Label>Purpose <span>*</span></Form.Label>
                                <Form.Control
                                    type="text"
                                    name="purpose"
                                    defaultValue={purpose}
                                    onChange={this.changeHandler}
                                    placeholder="Enter Purpose"
                                    required
                                />
                            </Form.Group>
                            <Form.Group>
                                <Form.Label>Recommended <span>*</span></Form.Label>
                                <Form.Control
                                    type="text"
                                    name="recommended"
                                    defaultValue={recommended}
                                    onChange={this.changeHandler}
                                    placeholder="Enter Recommended"
                                    required
                                />
                            </Form.Group>
                            <Form.Group>
                                <Form.Label>Amount <span>*</span></Form.Label>
                                <Form.Control
                                    type="number"
                                    name="amount"
                                    defaultValue={amount}
                                    onChange={this.changeHandler}
                                    placeholder="Enter Amount"
                                    required
                                />
                            </Form.Group>
                            <Form.Group>
                                <Form.Label>Total Amount <span>*</span></Form.Label>
                                <Form.Control
                                    type="number"
                                    name="totalAmount"
                                    defaultValue={totalAmount}
                                    onChange={this.changeHandler}
                                    placeholder="Enter Total Amount"
                                    required
                                />
                            </Form.Group>

                            <Button type="submit" variant="dark" disabled={!isDone} size="lg" block>
                                {isWait ? `Please Wait...` : `Save`}
                            </Button>
                        </Form>
                    </Modal.Body>
                </Modal>
            </Fragment>
        )
    }
}
const mapStateToProps = state => ({
    common: state.common,
})
export default connect(mapStateToProps, { storeData, updateData })(Edit);