import { SET_MESSAGE, SET_ACTION } from './types';
import Axios from 'axios'

// Update profile picture 
export const storeSubscribe = data => dispatch => {
    Axios.post(`/api/subscribe/`, data)
        .then(res => {
            dispatch({
                type: SET_MESSAGE,
                payload: {
                    message: 'Thank you for connected with us.',
                }
            })
        })
        .catch(err => {
            dispatch({
                type: SET_MESSAGE,
                payload: {
                    message: err.response && err.response.data,
                    type: 'error',
                }
            })
        })
}

// Update action status 
export const updateActionStatus = status => dispatch => {
    dispatch({
        type: SET_ACTION,
        payload: status
    })
}