import React, { Component, Fragment } from 'react'
import { getPackageDetail } from '../../store/actions/packageActions';
import { connect } from 'react-redux';
import renderHTML from 'react-render-html';


class PackageDetail extends Component {
    constructor(props) {
        super(props);
        let { id } = props.match.params
        this.state = {
            data: {},
            dataId: id
        }
    }
    componentWillReceiveProps(props) {
        if (props.match.params.id !== this.state.dataId) {
            this.fetchData(props.match.params.id)
        }
    }
    componentDidMount() {
        this.fetchData(this.state.dataId)
    }
    fetchData = async (dataId) => {
        this.setState({ data: await this.props.getPackageDetail(dataId), dataId })
    }
    render() {
        let { data } = this.state
        return (
            <Fragment>
                <section className="section-padding">
                    <div className="container bg-light p-5">
                        <div className="row">
                            <div className="col-lg-8 col-md-8 offset-lg-2 offset-md-2">
                                <div className="section-title text-center section-title-padding">
                                    <h3>Detail Information</h3>
                                </div>
                            </div>
                        </div>

                        <div className="row">
                            <div className="col-lg-12 text-center">
                                <img src={data.image} alt='Slider' className='package-detail-thm' />
                            </div>
                        </div>

                        <div className="row">
                            <div className="col-lg-12">
                                <h3 className='mb-0'><b>{data.title}<br />From  {data.amount}</b></h3>
                                <p>{data.description && renderHTML(data.description)}</p>
                            </div>
                        </div>
                    </div>
                </section>
            </Fragment>
        );
    }
}
export default connect(null, { getPackageDetail })(PackageDetail)