import React, { Component, Fragment } from 'react'
import { connect } from 'react-redux';
import FlightList from '../flight/result/FlightList';
import PassengerInfo from './PassengerInfo';
import { getTicketDetail } from '../../store/actions/ticketActions';
import PdfMakerBtn from './PdfMakerBtn';
import LoadingImg from '../layout/LoadingImg';


class FlightBooking extends Component {
    constructor(props) {
        super(props);
        let { ticketId } = props.match.params
        this.state = {
            ticketId,
            data: [],
            isLoading: true
        }
    }
    async componentDidMount() {
        this.setState({
            data: await this.props.getTicketDetail(this.state.ticketId),
            isLoading: false
        })
    }
    render() {
        let { data, isLoading } = this.state
        let segmentInfo = {}
        if (Object.keys(data).length !== 0) {
            if (data.ticket) {
                if (data.ticket.segmentResult) {
                    segmentInfo = JSON.parse(data.ticket.segmentResult)
                }
            }
        }
        return (
            <Fragment>
                {isLoading && <LoadingImg />}

                {Object.keys(segmentInfo).length !== 0 &&
                    <section className="reservation-wrapper section-padding-top pb-2">
                        <div className="container">
                            <FlightList
                                pricing={segmentInfo}
                                segment={segmentInfo.airSegmentGroup}
                                fareList={segmentInfo.fareList}
                                history={this.props.history}
                                isBook={false}
                            />
                            <PassengerInfo segmentInfo={segmentInfo} data={data} history={this.props.history} />
                            <PdfMakerBtn data={data} />
                        </div>
                    </section>}
            </Fragment>
        )
    }
}

const mapStateToProps = state => ({
    flight: state.flight,
})
export default connect(mapStateToProps, { getTicketDetail })(FlightBooking)