import React, { Component, Fragment } from 'react';
import { connect } from 'react-redux';
import { resetPassword } from '../../store/actions/authActions';
import { Link } from 'react-router-dom';
import { Button } from 'react-bootstrap';
import Form from 'react-bootstrap/Form';


class ResetPassword extends Component {
    constructor(props) {
        super(props);
        this.state = {
            userKey: props.match.params.id,
            password: '',
            isWait: false,
        }
    }
    changeHandler = event => {
        this.setState({
            [event.target.name]: event.target.value
        })
    }
    submitHandler = async event => {
        event.preventDefault()
        this.setState({ isWait: true })
        let { resetPassword, history } = this.props
        let response = await resetPassword(this.state)
        if (response) history.push('/')
        this.setState({ isWait: false })
    }
    render() {
        let { password, confirmPassword, isWait } = this.state
        let isDone = password && confirmPassword && (password === confirmPassword)

        return (
            <Fragment>
                <section className="section-padding-top pb-2">
                    <div className="container">
                        <div className="input-form-area">
                            <div className="row">
                                <div className="col-lg-6 col-md-12 offset-3">
                                    <Form onSubmit={this.submitHandler}>
                                        <Form.Group>
                                            <Form.Label>Password *</Form.Label>
                                            <Form.Control
                                                type="password"
                                                name="password"
                                                value={password}
                                                onChange={this.changeHandler}
                                            />
                                        </Form.Group>
                                        <Form.Group>
                                            <Form.Label>Confirm Password *</Form.Label>
                                            <Form.Control
                                                type="password"
                                                name="confirmPassword"
                                                value={confirmPassword}
                                                onChange={this.changeHandler}
                                            />
                                        </Form.Group>

                                        <Button type="submit" variant="dark" disabled={!isDone} size="lg" block>
                                            {isWait ? `Please Wait...` : `Submit`}
                                        </Button>
                                    </Form>

                                    <p className="mt-3">
                                        <span className="float-left">
                                            Already have account? <Link to='/login'>Login here...</Link>
                                        </span>
                                    </p>
                                </div>
                            </div>
                        </div>
                    </div>
                </section>
            </Fragment>
        )
    }
}
const mapStateToProps = state => ({
    auth: state.auth,
})
export default connect(mapStateToProps, { resetPassword })(ResetPassword)