import React, { Component, Fragment } from 'react';
import Axios from 'axios';
import ReactTable from 'react-table';
import 'react-table/react-table.css';
import { connect } from 'react-redux';
import { Button, Form } from 'react-bootstrap';
import Edit from './Edit';
import { dateTime } from '../../../utils/helper';
import { deleteData } from '../../../store/actions/carrierActions'
import { GetAirline } from '../../../utils/GetAirportAirline';

class Carrier extends Component {
    constructor(props) {
        super(props);
        this.state = {
            data: [],
            loading: true,
            limit: 100,
            page: 1,
            pages: -1,
            isOpen: false,
            editData: {},
        }
    }
    componentDidMount() {
        this.fetchData();
    }
    fetchData(limit = this.state.limit, page = this.state.page) {
        this.setState({
            data: [],
            loading: true,
        })
        Axios.post(`/api/carriers/get`, { limit, page })
            .then(res => {
                this.setState({
                    data: res.data.data.docs,
                    page: res.data.data.page,
                    pages: res.data.data.totalPages,
                    limit: res.data.data.limit,
                    loading: false
                })
            })
    }
    actionIsDone = () => {
        this.setState({ isOpen: false, editData: {} })
        this.fetchData();
    }
    deleteHandler = async (id) => {
        await this.props.deleteData(id)
        setTimeout(() => {
            this.fetchData()
        }, 500)
    }
    render() {
        let { data, loading, pages, limit, isOpen, editData } = this.state

        const columns = [
            {
                Header: 'SL',
                accessor: 'index',
                Cell: row => <span>{row.index + 1}</span>,
                width: 30,
            },
            {
                Header: 'Airline',
                accessor: 'code',
                Cell: row => <span className='table-logo'>{<GetAirline code={row.original.code} type='logo' />}</span>,
            },
            {
                Header: 'Airline',
                accessor: 'code',
                Cell: row => <span>{<GetAirline code={row.original.code} />}</span>,
            },
            {
                Header: 'Airline',
                accessor: 'code',
            },
            {
                Header: 'Status',
                accessor: 'status',
                Cell: row => <span>{row.original.status ? <span className='text-success'>SHOW</span> : <span className='text-danger'>HIDE</span>}</span>,
            },
            {
                Header: 'Date',
                accessor: 'createdAt',
                Cell: row => <span>{dateTime(row.original.createdAt, 2)}</span>,
            },
            {
                Header: 'Action',
                Cell: row => <span>
                    <button className="btn btn-dark mr-3" onClick={() => this.setState({ editData: row.original, isOpen: true })}><i className="fa fa-edit"></i></button>
                    <button className="btn btn-danger" onClick={() => window.confirm('Are you sure?') && this.deleteHandler(row.original._id)}><i className="fa fa-trash"></i></button>
                </span>,
            },
        ];

        return (
            <Fragment>
                <div className="bg-light">
                    <div className="section-padding-top">
                        <div className="container bg-white">
                            <div className="section-title text-center"><h2>Manage Carrier</h2></div>

                            <div className="row">
                                <div className="col-lg-3">
                                    <Form.Group>
                                        <Form.Control
                                            type="text"
                                            name="name"
                                            // value={name}
                                            // onChange={this.changeHandler}
                                            placeholder="Search"
                                        />
                                    </Form.Group>
                                </div>
                                <div className="col-lg-9">
                                    <Button type="button" variant="dark" onClick={() => this.setState({ isOpen: true })}><i className="fa fa-plus" /></Button>
                                </div>
                            </div>

                            <div className="row">
                                <div className="col-lg-12">
                                    <ReactTable
                                        data={data}
                                        columns={columns}
                                        defaultPageSize={limit}
                                        minRows={1}
                                        noDataText='No rows found'
                                        loading={loading}
                                        manual
                                        pages={pages}
                                        onPageChange={(page) => this.fetchData(limit, page + 1)}
                                        onPageSizeChange={(limit, page) => this.fetchData(limit, page)}
                                    />
                                </div>
                            </div>
                        </div>
                    </div>
                </div>


                {isOpen &&
                    <Edit
                        isOpen={isOpen}
                        closeHandler={() => this.setState({ isOpen: false, editData: {} })}
                        actionIsDone={this.actionIsDone.bind(this)}
                        data={editData}
                    />
                }
            </Fragment>
        );
    }
}
const mapStateToProps = state => ({
    user: state.auth.user,
})
export default connect(mapStateToProps, { deleteData })(Carrier)