import React, { Component, Fragment } from 'react';
import Modal from 'react-bootstrap/Modal';
import { Button, Form } from 'react-bootstrap';
import { connect } from 'react-redux';
import { storeData, updateData } from '../../../store/actions/expenseActions'


class Edit extends Component {
    constructor(props) {
        super(props);
        this.state = {
            id: props.data._id || null,
            salary: props.data.salary || 0,
            houseRent: props.data.houseRent || 0,
            officeExpense: props.data.officeExpense || 0,
            marketing: props.data.marketing || 0,
            netProfit: props.data.netProfit || 0,
            note: props.data.note || '',
            date: props.data.date || null,
            status: props.data.status || 0,
            isWait: false
        }
    }
    changeHandler = event => {
        this.setState({
            [event.target.name]: event.target.value
        })
    }
    submitHandler = async event => {
        event.preventDefault()
        this.setState({ isWait: true })
        let { actionIsDone, storeData, updateData } = this.props

        let response = false;
        let { id } = this.state
        if (id) {
            response = await updateData(this.state, id)
        } else {
            response = await storeData(this.state)
        }
        if (response) actionIsDone()
        this.setState({ isWait: false })
    }
    render() {
        let { id, salary, houseRent, officeExpense, marketing, netProfit, note, isWait } = this.state
        let isDone = salary || houseRent || officeExpense
        return (
            <Fragment>
                <Modal show={this.props.isOpen} onHide={this.props.closeHandler}>
                    <Modal.Header closeButton>
                        <Modal.Title>{id ? `Edit Expense` : `Add Expense`}</Modal.Title>
                    </Modal.Header>
                    <Modal.Body>
                        <Form onSubmit={this.submitHandler}>
                            <Form.Group>
                                <Form.Label>Salary</Form.Label>
                                <Form.Control
                                    type="number"
                                    name="salary"
                                    defaultValue={salary}
                                    onChange={this.changeHandler}
                                    placeholder="Enter Salary"
                                />
                            </Form.Group>
                            <Form.Group>
                                <Form.Label>House Rent</Form.Label>
                                <Form.Control
                                    type="number"
                                    name="houseRent"
                                    defaultValue={houseRent}
                                    onChange={this.changeHandler}
                                    placeholder="Enter House Rent"
                                />
                            </Form.Group>
                            <Form.Group>
                                <Form.Label>Office Expense</Form.Label>
                                <Form.Control
                                    type="number"
                                    name="officeExpense"
                                    defaultValue={officeExpense}
                                    onChange={this.changeHandler}
                                    placeholder="Enter Office Expense"
                                />
                            </Form.Group>
                            <Form.Group>
                                <Form.Label>Marketing</Form.Label>
                                <Form.Control
                                    type="number"
                                    name="marketing"
                                    defaultValue={marketing}
                                    onChange={this.changeHandler}
                                    placeholder="Enter Marketing"
                                />
                            </Form.Group>
                            <Form.Group>
                                <Form.Label>Net Profit</Form.Label>
                                <Form.Control
                                    type="number"
                                    name="netProfit"
                                    defaultValue={netProfit}
                                    onChange={this.changeHandler}
                                    placeholder="Enter Net Profit"
                                />
                            </Form.Group>
                            <Form.Group>
                                <Form.Label>Note</Form.Label>
                                <Form.Control
                                    as='textarea'
                                    type="text"
                                    name="note"
                                    defaultValue={note}
                                    onChange={this.changeHandler}
                                    placeholder="Enter Note"
                                />
                            </Form.Group>
                            <Button type="submit" variant="dark" disabled={!isDone} size="lg" block>
                                {isWait ? `Please Wait...` : `Save`}
                            </Button>
                        </Form>
                    </Modal.Body>
                </Modal>
            </Fragment>
        )
    }
}
export default connect(null, { storeData, updateData })(Edit);