import React, { Component, Fragment } from 'react';
import { connect } from 'react-redux';
import { forgotPassword } from '../../store/actions/authActions';
import { Link } from 'react-router-dom';
import { Button } from 'react-bootstrap';
import Form from 'react-bootstrap/Form';


class ForgotPassword extends Component {
    constructor(props) {
        super(props);
        this.state = {
            email: '',
            password: '',
            isWait: 0,
        }
    }
    changeHandler = event => {
        this.setState({
            [event.target.name]: event.target.value
        })
    }
    submitHandler = async event => {
        event.preventDefault()
        this.setState({ isWait: true })
        let { forgotPassword, history } = this.props
        let response = await forgotPassword(this.state)
        if (response) history.push('/')
        this.setState({ isWait: false })
    }
    render() {
        let { email, isWait } = this.state
        let isDone = email

        return (
            <Fragment>
                <section className="section-padding-top pb-2">
                    <div className="container">
                        <div className="input-form-area">
                            <div className="row">
                                <div className="col-lg-6 col-md-12 offset-3">
                                    <Form onSubmit={this.submitHandler}>
                                        <Form.Group>
                                            <Form.Label>Email address *</Form.Label>
                                            <Form.Control
                                                type="email"
                                                name="email"
                                                value={email}
                                                onChange={this.changeHandler}
                                                placeholder="Enter Email"
                                            />
                                        </Form.Group>

                                        <Button type="submit" variant="dark" disabled={!isDone} size="lg" block>
                                            {isWait ? `Please Wait...` : `Submit`}
                                        </Button>
                                    </Form>

                                    <p className="mt-3">
                                        <span className="float-left">
                                            Already have account? <Link to='/login'>Login here...</Link>
                                        </span>
                                    </p>
                                </div>
                            </div>
                        </div>
                    </div>
                </section>
            </Fragment>
        )
    }
}
export default connect(null, { forgotPassword })(ForgotPassword)