import React, { Component, Fragment } from 'react';
import { connect } from 'react-redux';
import { bestSeller } from '../../../store/actions/agentActions';


class BestSeller extends Component {
    state = {
        data: []
    }
    async componentDidMount() {
        this.setState({
            data: await this.props.bestSeller()
        })
    }
    render() {
        let { data } = this.state
        let users = []
        if (data.user) {
            let { user, statement } = data
            for (let i = 0; user.length > i; i++) {
                let ticketNo = 0
                let totalSale = 0
                for (let j = 0; statement.length > j; j++) {
                    if (user[i]._id === statement[j].userId) {
                        ticketNo = ticketNo + 1
                        totalSale += statement[j].amount
                    }
                }
                users.push({ ...user[i], ticketNo, totalSale })
            }
            users.sort((a, b) => parseFloat(b.totalSale) - parseFloat(a.totalSale))
            users = users.slice(0, 1)
        }

        return (
            <Fragment>
                <div className="bg-light">
                    <div className="section-padding-top">
                        <div className="container bg-white">

                            <div className="section-title text-center"><h2>Today Best Seller</h2></div>

                            <table className='table'>
                                <thead>
                                    <tr>
                                        <th>SL NO.</th>
                                        <th>Agent ID</th>
                                        <th>Agent Name</th>
                                        <th>Available Balance</th>
                                        <th>Number</th>
                                        <th>Amount</th>
                                    </tr>
                                </thead>
                                <tbody>
                                    {users.length !== 0 && users.map((item, index) =>
                                        <tr key={item._id}>
                                            <td>{index + 1}</td>
                                            <td>{item.uniqueCode}</td>
                                            <td>{item.name}</td>
                                            <td>{item.balance}</td>
                                            <td>{item.ticketNo}</td>
                                            <td>{item.totalSale}</td>
                                        </tr>
                                    )}
                                </tbody>
                            </table>
                        </div>
                    </div>
                </div>
            </Fragment>
        );
    }
}
export default connect(null, { bestSeller })(BestSeller)