import React, { Component, Fragment } from 'react'
import { Route, Switch } from 'react-router-dom'
import { connect } from 'react-redux';

/*************************Import Layout********************************/
import Home from './home/Home';
import Flight from './flight/Flight';
import CreateReservation from './reservation/CreateReservation';
import FlightBooking from './booking/FlightBooking';
import Login from './auth/Login';
import Register from './auth/Register';
import ForgotPassword from './auth/ForgotPassword';
import ResetPassword from './auth/ResetPassword';
import About from './pages/About';
import PageNotFound from './pages/PageNotFound';
import ProtectedRoute from './ProtectedRoute';
import MarqueeDetail from './pages/MarqueeDetail';
import PackageDetail from './pages/PackageDetail';
import TicketPdf from './booking/pdf/TicketPdf';
import ServiceDetail from './pages/ServiceDetail';
import UsefulLinkDetail from './pages/UsefulLinkDetail';


class Routes extends Component {
    state = {
        isAuth: false,
    }
    static getDerivedStateFromProps(nextProps, prevState) {
        if (nextProps.auth.isAuth === prevState.isAuth) return null
        return {
            isAuth: nextProps.auth.isAuth,
        }
    }
    render() {
        let { isAuth } = this.state
        return (
            <Fragment>
                <Switch>

                    <Route path='/pdf/ticket/:ticketId' component={TicketPdf} history={this.props.history} />

                    <Route path='/useful-link/detail/:id' component={UsefulLinkDetail} history={this.props.history} />
                    <Route path='/service/detail/:id' component={ServiceDetail} history={this.props.history} />
                    <Route path='/marquee/detail/:id' component={MarqueeDetail} history={this.props.history} />
                    <Route path='/package/detail/:id' component={PackageDetail} history={this.props.history} />
                    <Route path='/about' component={About} history={this.props.history} />
                    <Route path='/rest-password/:id' component={ResetPassword} history={this.props.history} />
                    <Route path='/forgot-password' component={ForgotPassword} history={this.props.history} />
                    <Route path='/login' component={Login} history={this.props.history} />
                    <Route path='/register' component={Register} history={this.props.history} />
                    <Route path='/booking/:ticketId' component={FlightBooking} history={this.props.history} />
                    <Route path='/reservation' component={CreateReservation} history={this.props.history} />
                    <Route path='/flight' component={Flight} history={this.props.history} />

                    {/***************Backend Routes****************/}
                    {isAuth && <Route path='/backend/*' exact component={ProtectedRoute} history={this.props.history} />}

                    <Route path='/' exact component={Home} history={this.props.history} />
                    <Route path='/*' exact component={PageNotFound} history={this.props.history} />

                </Switch>
            </Fragment >
        )
    }
}
const mapStateToProps = state => ({
    auth: state.auth,
})
export default connect(mapStateToProps)(Routes)