import React, { Component, Fragment } from 'react';
import Axios from 'axios';
import ReactTable from 'react-table';
import 'react-table/react-table.css';
import { Link } from 'react-router-dom';
import { connect } from 'react-redux';
import { dateTime, getMyTime, timeConvert } from './../../../utils/helper';
import { DropdownButton, Dropdown } from 'react-bootstrap'
import { updateData, orderInProcess } from '../../../store/actions/ticketActions'
import { bookingCancel } from '../../../store/actions/flightActions'
import { Form } from 'react-bootstrap';
import TicketHeader from './TicketHeader';
import LinkTable from './LinkTable';


class Ticket extends Component {
    constructor(props) {
        super(props);
        let { ticketStatus } = props.match.params
        this.state = {
            _this: this,
            data: [],
            loading: true,
            limit: 10,
            page: 1,
            pages: -1,
            status: ticketStatus,
            price: [],
            passengers: [],
            airSegment: [],
            fareIsOpen: false,
            linkTable: false
        }
    }
    static getDerivedStateFromProps(nextProps, prevState) {
        if (JSON.stringify(nextProps.match.params.ticketStatus) === JSON.stringify(prevState.status)) return null
        prevState._this.fetchData('', '', nextProps.match.params.ticketStatus)
        return null
    }
    componentDidMount() {
        this.fetchData();
    }
    fetchData(limit = this.state.limit, page = this.state.page, status = this.state.status) {
        this.setState({
            data: [],
            loading: true,
            status,
        })
        Axios.post(`/api/tickets/get`, { limit, page, status })
            .then(res => {
                this.setState({
                    data: res.data.data.data.docs,
                    page: res.data.data.data.page,
                    pages: res.data.data.data.totalPages,
                    limit: res.data.data.data.limit,
                    loading: false,
                    price: res.data.data.price,
                    passengers: res.data.data.passenger,
                    airSegment: res.data.data.airSegment,
                })
            })
    }
    statusChangeHandler = async (id, status) => {
        this.setState({
            loading: true,
        })
        await this.props.updateData({ status }, id)
        this.fetchData()
    }
    orderInProcessHandler = async (id) => {
        this.setState({
            loading: true,
        })
        await this.props.orderInProcess({ orderInProcess: true }, id)
        this.fetchData()
    }
    cancelHandler = async (PNR, ticketId) => {
        await this.props.bookingCancel(PNR, ticketId)
        this.fetchData();
    }
    render() {
        let { data, loading, pages, limit, price, passengers, airSegment, status, fareIsOpen, linkTable } = this.state

        const getPassenger = ticketId => {
            if (Object.keys(passengers).length !== 0) {
                let selObj = passengers.filter(item => item.ticketId === ticketId)
                if (selObj.length === 0) return null
                let noPass = Object.keys(selObj).length !== 0 ? `(${Object.keys(selObj).length})` : ''
                return <span>{`${selObj[0].firstName} ${selObj[0].lastName} ${noPass}`} </span>
            } else {
                return null
            }
        }
        const getPassportVisa = ticketId => {
            if (Object.keys(passengers).length !== 0) {
                let selObj = passengers.filter(item => item.ticketId === ticketId)
                if (selObj.length === 0) return null
                if (selObj[0].passport) {
                    return <div><span>V-Y</span>, <span>P-N</span></div>
                } else {
                    return <div><span>V-N</span>, <span>P-N</span></div>
                }
            } else {
                return null
            }
        }
        const getFlightDate = ticketId => {
            if (Object.keys(airSegment).length !== 0) {
                let selObj = airSegment.filter(item => item.ticketId === ticketId)
                if (selObj.length === 0) return null
                return dateTime(selObj[0].departureTime)
            } else {
                return null
            }
        }
        const getPrice = ticketId => {
            if (Object.keys(price).length !== 0) {
                let selObj = price.filter(item => item.ticketId === ticketId)
                if (selObj.length === 0) return null
                return selObj[0].totalPrice.slice(3)
            } else {
                return null
            }
        }
        const getFlightWay = ticketId => {
            let result = 'One-way'
            if (Object.keys(airSegment).length !== 0) {
                airSegment.map(item => {
                    if (item.ticketId === ticketId && item.Group === '1') {
                        result = 'Return-way'
                    }
                    return true;
                })
                return result;
            } else {
                return null
            }
        }
        const actionsBasedStatus = (ticketId, status, orderInProcess) => {
            return (
                <div>
                    <DropdownButton id="collasible-nav-dropdown" title="Take Action">
                        {(status === 'in-process' && orderInProcess) && <Dropdown.Item href="#blank"
                            href="#blank"
                            onClick={() => window.confirm('Are you sure?') && this.statusChangeHandler(ticketId, 'ticket')}
                        >{orderInProcess ? 'Update' : 'Ticket'}</Dropdown.Item>}

                        {status === 'in-process' && !orderInProcess && <Dropdown.Item
                            href="#blank"
                            onClick={() => window.confirm('Are you sure?') && this.orderInProcessHandler(ticketId)}
                        >Order In Process</Dropdown.Item>}

                        {(status === 'booking' || status === 'on-hold') && <Dropdown.Item
                            href="#blank"
                            onClick={() => window.confirm('Are you sure?') && this.statusChangeHandler(ticketId, 'in-process')}
                        >In Process</Dropdown.Item>}

                        {(status === 'booking' || status === 'in-process') && <Dropdown.Item href="#blank"
                            href="#blank"
                            onClick={() => window.confirm('Are you sure?') && this.statusChangeHandler(ticketId, 'on-hold')}
                        >On Hold</Dropdown.Item>}

                        {(status === 'booking' || status === 'in-process') && <Dropdown.Item
                            href="#blank"
                            onClick={() => window.confirm('Are you sure?') && this.statusChangeHandler(ticketId, 'expire')}
                        >Expire</Dropdown.Item>}

                        {(status === 'ticket' || status === 'no-show') && <Dropdown.Item
                            href="#blank"
                            onClick={() => window.confirm('Are you sure?') && this.statusChangeHandler(ticketId, 're-issue')}
                        >Re Issue</Dropdown.Item>}

                        {(status === 'refund' || status === 'ticket' || status === 'no-show') && <Dropdown.Item
                            href="#blank"
                            onClick={() => window.confirm('Are you sure?') && this.statusChangeHandler(ticketId, 'refund')}
                        >Refund</Dropdown.Item>}


                        {(status === 'booking' || status === 'ticket' || status === 'on-hold' || status === 'in-process') && <Dropdown.Item
                            href="#blank"
                            onClick={() => window.confirm('Are you sure?') && this.cancelHandler(ticketId)}
                        >Cancelled</Dropdown.Item>}

                        {(status === 'void' || status === 'ticket') && <Dropdown.Item
                            href="#blank"
                            onClick={() => window.confirm('Are you sure?') && this.statusChangeHandler(ticketId, 'void')}
                        >Void</Dropdown.Item>}

                        {status === 'ticket' && <Dropdown.Item
                            href="#blank"
                            onClick={() => window.confirm('Are you sure?') && this.statusChangeHandler(ticketId, 'no-show')}
                        >No Show</Dropdown.Item>}

                    </DropdownButton>
                </div>
            )
        }

        const columns = [
            {
                Header: 'SL',
                accessor: '_id',
                Cell: row => <span>{row.index + 1}</span>,
                maxWidth: 30,
                resizable: false
            },
            {
                Header: 'Action',
                accessor: 'status',
                Cell: row => <span>{actionsBasedStatus(row.original._id, row.original.status, row.original.orderInProcess, row.original.locatorCode)}</span>,
                maxWidth: 120,
            },
            {
                Header: 'Ref ID',
                accessor: '_id',
                Cell: row => <Link className="link-color" to={`/booking/${row.original._id}`}>{row.original.refId.slice(2)}</Link>,
                maxWidth: 100,
            },
            {
                Header: 'PNR',
                accessor: 'locatorCode',
                maxWidth: 70,
            },
            {
                Header: 'Segment|Flight No|Equip|DepT-ArrT|FlightT|Dist|P|C|R|OT|DT',
                Cell: row => <span>{
                    airSegment.map(item => item.ticketId === row.original._id && <span key={item._id} className='d-block'>{`${item.origin}-${item.destination} | ${item.carrier + item.flightNumber} | ${item.equipment} | ${getMyTime(item.departureTime, 3).slice(0, 5)}-${getMyTime(item.arrivalTime)} | ${timeConvert(item.travelTime)} | ${item.distance ? item.distance : ''} | ${item.providerCode} | ${item.classOfService} | ${row.original.refundable === 'true' ? 'Yes' : 'No'} | T${item.originTerminal ? item.originTerminal : ''} | T${item.destinationTerminal ? item.destinationTerminal : ''}`}</span>)
                }</span>,
                width: 400,
            },
            {
                Header: 'PAX Name N No.',
                accessor: 'createdAt',
                Cell: row => <span>{getPassenger(row.original._id)}</span>,
                maxWidth: 100,
            },
            {
                Header: 'DocX',
                Cell: row => <span>{getPassportVisa(row.original._id)}</span>,
                maxWidth: 50,
            },
            {
                Header: 'Trip Type',
                Cell: row => <span>{getFlightWay(row.original._id)}</span>,
                maxWidth: 50,
            },
            {
                Header: 'Agent Net Fare',
                Cell: row => <span className='base-fare-section'><Link to='#blank' onClick={() => this.setState({ fareIsOpen: row.original._id })} onMouseOut={() => this.setState({ fareIsOpen: null })}>BDT={getPrice(row.original._id)}</Link>
                    {fareIsOpen === row.original._id &&
                        <table className='fare-show-table' border='1'>
                            <thead>
                                <tr>
                                    <th>%</th>
                                    <th>Sales Fare</th>
                                    <th>Pay</th>
                                    <th>Agent Com</th>
                                </tr>
                            </thead>
                            <tbody>
                                <tr>
                                    <td>&nbsp;</td>
                                    <td>&nbsp;</td>
                                    <td>&nbsp;</td>
                                    <td>&nbsp;</td>
                                </tr>
                            </tbody>
                        </table>}
                </span>,
            },
            {
                Header: 'Status',
                Cell: row => <span>{!row.original.orderInProcess ? status : 'Order In Process'}</span>,
                maxWidth: 200,
            },
            {
                Header: 'Particular',
                accessor: 'comment',
            },
            {
                Header: 'Agent ID',
                Cell: row => <Link className="link-color" to={`/backend/users/${row.original.userId.type === 2 ? row.original.userId.agentId : row.original.userId._id}`}>{row.original.userId.uniqueCode}</Link>,
            },
            {
                Header: 'Link',
                Cell: row => <span><Link
                    to='#blank'
                    onClick={() => this.setState({ linkTable: row.original._id })}
                    onMouseOut={() => this.setState({ linkTable: null })}
                >Link</Link>
                    {linkTable === row.original._id && <LinkTable />}
                </span>,
            },
            {
                Header: 'Alert',
                accessor: 'alert',
            },
            {
                Header: 'By',
                accessor: 'byUserId',
                Cell: row => <span>{row.original.byUserId && row.original.byUserId.name}</span>,
            },
        ];
        let date = {}
        if (status === 'ticket') {
            columns.splice(11, 0, {
                Header: 'SMS/Email',
                Cell: row => <Link className="link-color" to={`/backend/users/${row.original.userId.type === 2 ? row.original.userId.agentId : row.original.userId._id}`}>Email</Link>,
            })

            date = {
                Header: 'Date',
                Cell: row => <span>
                    <span className='text-danger'>Booking: {dateTime(row.original.createdAt)}</span><br />
                    <span className='text-danger'>TL: {dateTime(row.original.latestTicketingTime)}</span><br />
                    <span className='text-success'>Issued: {dateTime(row.original.createdAt)}</span><br />
                    <span className='text-success'>Travel: {getFlightDate(row.original._id)}</span><br />
                </span>,
                maxWidth: 200,
                resizable: true
            }
        } else {
            date = {
                Header: 'Date',
                Cell: row => <span>
                    <span className='text-danger'>Booking: {dateTime(row.original.createdAt)}</span><br />
                    <span className='text-danger'>TL: {dateTime(row.original.latestTicketingTime)}</span><br />
                </span>,
                maxWidth: 200,
            }
        }
        columns.splice(1, 0, date)

        if (status === 'booking') {
            columns.push({
                Header: 'SMS/Email',
                Cell: row => <Link className="link-color" to={`/backend/users/${row.original.userId.type === 2 ? row.original.userId.agentId : row.original.userId._id}`}>SMS/Email</Link>,
            })
        }

        if (status === 'booking') {
            columns.push({
                Header: 'SMS/Email',
                Cell: row => <Link className="link-color" to={`/backend/users/${row.original.userId.type === 2 ? row.original.userId.agentId : row.original.userId._id}`}>SMS/Email</Link>,
            })
        }

        return (
            <Fragment>
                <div className="bg-light">
                    <div className="section-padding-top">
                        <div className="container bg-white">

                            <TicketHeader status={status} />

                            <div className="row">
                                <div className="col-lg-3">
                                    <Form.Group>
                                        <Form.Control
                                            type="text"
                                            name="name"
                                            // value={name}
                                            // onChange={this.changeHandler}
                                            placeholder="Search"
                                        />
                                    </Form.Group>
                                </div>
                            </div>

                            <div className="row">
                                <div className="col-lg-12">
                                    <ReactTable
                                        data={data}
                                        columns={columns}
                                        defaultPageSize={limit}
                                        minRows={1}
                                        noDataText='No rows found'
                                        loading={loading}
                                        manual
                                        pages={pages}
                                        onPageChange={(page) => this.fetchData(limit, page + 1)}
                                        onPageSizeChange={(limit, page) => this.fetchData(limit, page)}
                                        className='border-0 shadow'
                                    />
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </Fragment>
        );
    }
}
const mapStateToProps = state => ({
    user: state.auth.user,
})
export default connect(mapStateToProps, { updateData, orderInProcess, bookingCancel })(Ticket)