import { SET_MESSAGE } from './types'
import Axios from 'axios'


// Get All Data
export const getAllPassenger = () => async dispatch => {
    return Axios.post(`/api/passengers/get`)
        .then(res => {
            return res.data.data.docs;
        })
        .catch(err => {
            dispatch({
                type: SET_MESSAGE,
                payload: {
                    message: err.response && err.response.data.message,
                    type: 'error',
                }
            })
            return false;
        })
}

// Update Data
export const uploadPassportVisa = (data, id) => async dispatch => {
    return Axios.put(`/api/passengers/passport/visa/${id}`, data)
        .then(res => {
            dispatch({
                type: SET_MESSAGE,
                payload: {
                    message: res.data.message,
                    type: 'success',
                }
            })
            return true;
        })
        .catch(err => {
            dispatch({
                type: SET_MESSAGE,
                payload: {
                    message: err.response && err.response.data.message,
                    type: 'error',
                }
            })
            return false;
        })
}