import React, { Component, Fragment } from 'react';
import { connect } from 'react-redux';
import { agentFinancialRecord } from '../../../store/actions/agentActions';
import { dateTime } from '../../../utils/helper';
import dateFormat from 'dateformat';


class AgentFinancialRecord extends Component {
    state = {
        data: []
    }
    async componentDidMount() {
        this.setState({
            data: await this.props.agentFinancialRecord()
        })
    }
    render() {
        let { data } = this.state
        let expense = []
        if (data && data.statement) {
            expense = data.statement.filter(item => item.type === 2)
        }

        let getTotalDeposit = (userId) => {
            let response = 0
            if (data.deposit) {
                for (let i = 0; data.deposit.length > i; i++) {
                    if (data.deposit[i].userId === userId) {
                        response += data.deposit[i].amount
                    }
                }
            }
            return response;
        }
        let getTotalUsed = (userId) => {
            let response = 0
            if (expense) {
                for (let i = 0; expense.length > i; i++) {
                    if (expense[i].userId === userId) {
                        response += expense[i].amount
                    }
                }
            }
            return response;
        }
        let getTotalBonus = (userId) => {
            let response = 0
            if (data.bonus) {
                for (let i = 0; data.bonus.length > i; i++) {
                    if (data.bonus[i].userId === userId) {
                        response += data.bonus[i].amount
                    }
                }
            }
            return response;
        }
        let getTotalTicket = (userId) => {
            let response = 0
            if (expense) {
                for (let i = 0; expense.length > i; i++) {
                    if (expense[i].userId === userId && (expense[i].ticketId !== null)) {
                        response += expense[i].amount
                    }
                }
            }
            return response;
        }
        let getLastMonthUsed = (userId) => {
            var date = new Date();
            var firstDay = new Date(date.getFullYear(), date.getMonth() - 1, 1);
            var lastDay = new Date(date.getFullYear(), date.getMonth(), 0);
            firstDay = dateFormat(new Date(firstDay), "yyyy-mm-dd")
            lastDay = dateFormat(new Date(lastDay), "yyyy-mm-dd")

            let response = 0
            if (expense) {
                for (let i = 0; expense.length > i; i++) {
                    if ((dateFormat(new Date(expense[i].createdAt), "yyyy-mm-dd") >= firstDay) && (dateFormat(new Date(expense[i].createdAt), "yyyy-mm-dd") <= lastDay) && (expense[i].userId === userId)) {
                        response += expense[i].amount
                    }
                }
            }
            return response;
        }
        let getTodayUsed = (userId) => {
            let response = 0
            if (expense) {
                for (let i = 0; expense.length > i; i++) {
                    if ((dateFormat(new Date(expense[i].createdAt), "yyyy-mm-dd") === dateFormat(new Date(), "yyyy-mm-dd")) && (expense[i].userId === userId)) {
                        response += expense[i].amount
                    }
                }
            }
            return response;
        }

        return (
            <Fragment>
                <div className="bg-light">
                    <div className="section-padding-top">
                        <div className="container bg-white">
                            <div className="section-title text-center"><h2>Agent Financial Info</h2></div>

                            <table className='table'>
                                <thead>
                                    <tr>
                                        <th>SL NO.</th>
                                        <th>Agent ID</th>
                                        <th>Agent Name</th>
                                        <th>Date</th>
                                        <th>Available</th>
                                        <th>Used Bal Today</th>
                                        <th>Used Bal Last month</th>
                                        <th>Total Used</th>
                                        <th>Total Deposit</th>
                                        <th>Total Incentive</th>
                                        <th>Total Ticket</th>
                                    </tr>
                                </thead>
                                <tbody>
                                    {Object.keys(data).length !== 0 && data.user.map((user, index) =>
                                        <tr key={user._id}>
                                            <td>{index + 1}</td>
                                            <td>{user.uniqueCode}</td>
                                            <td>{user.name}</td>
                                            <td>{dateTime(user.createdAt, 2)}</td>
                                            <td>{user.balance}</td>
                                            <td>{getTodayUsed(user._id)}</td>
                                            <td>{getLastMonthUsed(user._id)}</td>
                                            <td>{getTotalUsed(user._id)}</td>
                                            <td>{getTotalDeposit(user._id)}</td>
                                            <td>{getTotalBonus(user._id)}</td>
                                            <td>{getTotalTicket(user._id)}</td>
                                        </tr>
                                    )}

                                </tbody>
                            </table>
                        </div>
                    </div>
                </div>
            </Fragment>
        );
    }
}
export default connect(null, { agentFinancialRecord })(AgentFinancialRecord)