import React, { Component, Fragment } from 'react';
import Axios from 'axios';
import ReactTable from 'react-table';
import 'react-table/react-table.css';
import { Link } from 'react-router-dom';
import { connect } from 'react-redux';
import { Button, Form } from 'react-bootstrap';
import Edit from './Edit';
import UserHeader from '../user/UserHeader';
import { dateTime } from '../../../utils/helper';


class B2BDeposit extends Component {
    constructor(props) {
        super(props);
        this.state = {
            data: [],
            loading: true,
            limit: 100,
            page: 1,
            pages: -1,
            status: null,
            isOpen: false,
            editData: {},
            authUser: props.user,
            userId: props.match.params.userId
        }
    }
    componentDidMount() {
        this.fetchData();
    }
    fetchData(limit = this.state.limit, page = this.state.page) {
        let { userId, status } = this.state
        this.setState({
            data: [],
            loading: true,
        })
        Axios.post(`/api/deposits/get`, { limit, page, status, userId })
            .then(res => {
                this.setState({
                    data: res.data.data.docs,
                    page: res.data.data.page,
                    pages: res.data.data.totalPages,
                    limit: res.data.data.limit,
                    loading: false
                })
            })
    }
    actionIsDone = () => {
        this.setState({ isOpen: false, editData: {} })
        this.fetchData();
    }
    render() {
        let { data, loading, pages, limit, authUser, isOpen, userId, editData } = this.state

        const columns = [
            {
                Header: 'SL',
                accessor: 'index',
                Cell: row => <span>{row.index + 1}</span>,
                width: 30,
            },
            {
                Header: 'Ref ID',
                accessor: 'uniqueCode',
            },
            {
                Header: 'Amount',
                accessor: 'amount',
            },
            {
                Header: 'Available',
                accessor: 'available',
            },
            {
                Header: 'After Deposit',
                accessor: 'afterDeposit',
            },
            {
                Header: 'Create Date',
                accessor: 'createdAt',
                Cell: row => <span>{dateTime(row.original.createdAt)}</span>,
            },
            {
                Header: 'File',
                accessor: 'file',
                Cell: row => <span>{row.original.file && <a href={row.original.file} download >Download</a>}</span>,
            },
            {
                Header: 'By',
                Cell: row => <Link className="link-color" to={`/backend/counter/${row.original.userId.type === 2 ? row.original.userId.agentId : row.original.userId._id}`}>{row.original.userId.name}</Link>,
            },
            {
                Header: 'Note',
                accessor: 'comment',
            },
            {
                Header: 'status',
                accessor: 'status',
                Cell: row => <span>{(row.original.status === 0 && "Pending") || (row.original.status === 1 && "Approve") || (row.original.status === 2 && <span className='text-danger'>Rejected</span>)}</span>,
            },
            {
                Header: "Action",
                Cell: row => <span>
                    {row.original.status === 0 &&
                        <Button type="button" variant="dark" size="sm" onClick={() => this.setState({ isOpen: true, editData: row.original })}><i className="fa fa-pencil" /></Button>}
                </span>,
            },
        ];

        return (
            <Fragment>
                <div className="bg-light">
                    <div className="section-padding-top">
                        <div className="container bg-white">
                            <UserHeader userId={userId} />

                            <div className="section-title text-center"><h2>Deposit</h2></div>

                            <div className="row">
                                <div className="col-lg-3">
                                    <Form.Group>
                                        <Form.Control
                                            type="text"
                                            name="name"
                                            // value={name}
                                            // onChange={this.changeHandler}
                                            placeholder="Search"
                                        />
                                    </Form.Group>
                                </div>
                                {userId === authUser._id &&
                                    <div className="col-lg-9">
                                        <Button type="button" variant="dark" onClick={() => this.setState({ isOpen: true })}><i className="fa fa-plus" /></Button>
                                    </div>}
                            </div>






                            <div className="row">
                                <div className="col-lg-12">
                                    <ReactTable
                                        data={data}
                                        columns={columns}
                                        defaultPageSize={limit}
                                        minRows={1}
                                        noDataText='No rows found'
                                        loading={loading}
                                        manual
                                        pages={pages}
                                        onPageChange={(page) => this.fetchData(limit, page + 1)}
                                        onPageSizeChange={(limit, page) => this.fetchData(limit, page)}
                                    />
                                </div>
                            </div>
                        </div>
                    </div>
                </div>


                {isOpen &&
                    <Edit
                        isOpen={isOpen}
                        closeHandler={() => this.setState({ isOpen: false, editData: {} })}
                        actionIsDone={this.actionIsDone.bind(this)}
                        data={editData}
                    />
                }

            </Fragment>
        );
    }
}
const mapStateToProps = state => ({
    common: state.common,
    user: state.auth.user,
})
export default connect(mapStateToProps)(B2BDeposit)