import { TARGET_BRANCH } from "../../store/actions/types"
import dateFormat from 'dateformat';
import { getMonthName } from "../helper";


const AirCreateReservationReqLog = (AirPriceRsp, travelerAllInfo) => {
    let AirSegment = AirPriceRsp['air:AirItinerary'][0]['air:AirSegment']
    let AirPricingSolution = AirPriceRsp['air:AirPriceResult'][0]['air:AirPricingSolution'][0]['$']
    let AirPricingInfo = AirPriceRsp['air:AirPriceResult'][0]['air:AirPricingSolution'][0]['air:AirPricingInfo']
    let FareNote = AirPriceRsp['air:AirPriceResult'][0]['air:AirPricingSolution'][0]['air:FareNote']
    let HostToken = AirPriceRsp['air:AirPriceResult'][0]['air:AirPricingSolution'][0]['common_v47_0:HostToken']


    let adult = 1;
    let children = 0;

    let segmentXML = ``
    let Connection = ``
    AirSegment.map(segment => {
        Connection = segment['air:Connection'] ? `<air:Connection/>` : '';
        let FlightDetails = ``
        segment['air:FlightDetails'].map(item => {

            let InFlightServices = ``
            item['air:InFlightServices'] &&
                item['air:InFlightServices'].map(service => {
                    InFlightServices += `
                        <air:InFlightServices>${service}</air:InFlightServices>`;
                    return true;
                })
            let Meals = ``
            if (item['air:Meals']) {
                Meals += `<air:Meals>${item['air:Meals'][0]}</air:Meals>`;
            }
            FlightDetails += `<air:FlightDetails Key="${item['$']['Key']}" Origin="${item['$']['Origin']}" Destination="${item['$']['Destination']}" DepartureTime="${item['$']['DepartureTime']}" ArrivalTime="${item['$']['ArrivalTime']}" FlightTime="${item['$']['FlightTime']}" TravelTime="${item['$']['TravelTime']}" Equipment="${item['$']['Equipment']}" AutomatedCheckin="${item['$']['AutomatedCheckin']}">
                        ${Meals}
                        ${InFlightServices}
                    </air:FlightDetails>`;
            return true;
        })

        segmentXML += `
                <air:AirSegment 
                                Key="${segment['$']['Key']}" 
                                Group="${segment['$']['Group']}" 
                                Carrier="${segment['$']['Carrier']}" 
                                FlightNumber="${segment['$']['FlightNumber']}" 
                                Origin="${segment['$']['Origin']}" 
                                Destination="${segment['$']['Destination']}" 
                                DepartureTime="${segment['$']['DepartureTime']}" 
                                ArrivalTime="${segment['$']['ArrivalTime']}" 
                                FlightTime="${segment['$']['FlightTime']}" 
                                Distance="${segment['$']['Distance']}" 
                                Equipment="${segment['$']['Equipment']}" 
                                ChangeOfPlane="${segment['$']['ChangeOfPlane']}" 
                                ParticipantLevel="${segment['$']['ParticipantLevel']}" 
                                PolledAvailabilityOption="${segment['$']['PolledAvailabilityOption']}" 
                                OptionalServicesIndicator="${segment['$']['OptionalServicesIndicator']}" 
                                AvailabilitySource="${segment['$']['AvailabilitySource']}" 
                                AvailabilityDisplayType="${segment['$']['AvailabilityDisplayType']}"
                                ClassOfService="T"
                                ProviderCode="1G"
                                CabinClass="Economy"
                                >
                        <air:CodeshareInfo OperatingCarrier="${segment['air:CodeshareInfo'][0]['$']['OperatingCarrier']}">${segment['air:CodeshareInfo'][0]['_']}</air:CodeshareInfo>
                        ${FlightDetails}
                        ${Connection}
                    </air:AirSegment>`;
        return null;
    })


    let AirPricingInfoLog = ``
    AirPricingInfo.map((pricing, pricingIndex) => {

        /* Fare Info */
        let FareInfo = ``
        pricing['air:FareInfo'].map(fare => {

            let Endorsement = ``
            fare['common_v47_0:Endorsement'] &&
                fare['common_v47_0:Endorsement'].map(endorsement => {
                    Endorsement += `
                <common_v47_0:Endorsement Value="${endorsement['$']['Value']}"/>`;
                    return true;
                })

            let FareRuleKey = ``
            fare['air:FareRuleKey'] &&
                fare['air:FareRuleKey'].map(rule => {
                    FareRuleKey += `
                <air:FareRuleKey FareInfoRef="${rule['$']['FareInfoRef']}" ProviderCode="${rule['$']['ProviderCode']}">${rule['_']}</air:FareRuleKey>`;
                    return true;
                })

            let Brand = ``
            fare['air:Brand'] &&
                fare['air:Brand'].map(brand => {
                    Brand += `
                <air:Brand Key="${brand['$']['Key']}"/>`;
                    return true;
                })

            FareInfo += `<air:FareInfo Key="${fare['$']['Key']}" FareBasis="${fare['$']['FareBasis']}" PassengerTypeCode="${fare['$']['PassengerTypeCode']}" Origin="${fare['$']['Origin']}" Destination="${fare['$']['Destination']}" EffectiveDate="${fare['$']['EffectiveDate']}" DepartureDate="${fare['$']['DepartureDate']}" Amount="${fare['$']['Amount']}" TaxAmount="${fare['$']['TaxAmount']}">
                        ${Endorsement}
                        ${FareRuleKey}
                        ${Brand}
                    </air:FareInfo>`;
            return true;
        })

        /* Booking Info */
        let BookingInfo = ``
        pricing['air:BookingInfo'] &&
            pricing['air:BookingInfo'].map(booking => {
                BookingInfo += `
            <air:BookingInfo BookingCode="${booking['$']['BookingCode']}" CabinClass="${booking['$']['CabinClass']}" FareInfoRef="${booking['$']['FareInfoRef']}" SegmentRef="${booking['$']['SegmentRef']}" HostTokenRef="${booking['$']['HostTokenRef']}"/>`;
                return true;
            })

        /* Tax Info */
        let TaxInfo = ``
        pricing['air:TaxInfo'] &&
            pricing['air:TaxInfo'].map(tax => {
                TaxInfo += `
            <air:TaxInfo Category="${tax['$']['Category']}" Amount="${tax['$']['Amount']}" Key="${tax['$']['Key']}"/>`;
                return true;
            })

        /* Passenger Type */
        let PassengerType = ``
        pricing['air:PassengerType'] &&
            pricing['air:PassengerType'].map((passenger, passengerIndex) => {
                let passengerTypeKey = 'P_0';
                if (passenger['$']['Code'] === "ADT" && passengerIndex === 1) {
                    passengerTypeKey = 'P_1';
                    adult = adult + 1;
                }
                else if (passenger['$']['Code'] === "CNN" && pricingIndex === 1) {
                    passengerTypeKey = 'P_2';
                    children = children + 1;
                }
                PassengerType += `
                <air:PassengerType Code="${passenger['$']['Code']}" BookingTravelerRef="${passengerTypeKey}" />`;
                return true;
            })

        /* Change Penalty */
        let ChangePenalty = ``
        pricing['air:ChangePenalty'] &&
            pricing['air:ChangePenalty'].map(change => {
                if (change['air:Percentage']) {
                    ChangePenalty += `
                    <air:ChangePenalty>
                        <air:Percentage>${change['air:Percentage'][0]}</air:Percentage>
                    </air:ChangePenalty>`;
                }
                return true;
            })



        /* Baggage Allowances */
        let BaggageAllowances = ``
        pricing['air:BaggageAllowances'] &&
            pricing['air:BaggageAllowances'].map(baggage => {


                /**************************BaggageAllowanceInfo**************************/
                let BaggageAllowanceInfo = ``
                baggage['air:BaggageAllowanceInfo'] &&
                    baggage['air:BaggageAllowanceInfo'].map(allowanceInfo => {

                        /* URLInfo */
                        let URLInfo = ``
                        allowanceInfo['air:URLInfo'] &&
                            allowanceInfo['air:URLInfo'].map(url => {
                                URLInfo += `
                            <air:URLInfo>
                                <air:URL>${url['air:URL'][0]}</air:URL>
                            </air:URLInfo>`;
                                return true;
                            })

                        /* TextInfo */
                        let TextInfo = ``
                        allowanceInfo['air:TextInfo'] &&
                            allowanceInfo['air:TextInfo'].map(text => {
                                TextInfo += `
                            <air:TextInfo>
                                <air:Text>${text['air:Text'][0]}</air:Text>
                                <air:Text>${text['air:Text'][1]}</air:Text>
                            </air:TextInfo>`;
                                return true;
                            })

                        /* BagDetails */
                        let BagDetails = ``
                        allowanceInfo['air:BagDetails'] &&
                            allowanceInfo['air:BagDetails'].map(bag => {

                                /* BaggageRestriction */
                                let BaggageRestriction = ``
                                bag['air:BaggageRestriction'] &&
                                    bag['air:BaggageRestriction'].map(restriction => {
                                        BaggageRestriction += `
                                            <air:BaggageRestriction>
                                                <air:TextInfo>
                                                    <air:Text>${restriction['air:TextInfo'][0]['air:Text'][0]}</air:Text>
                                                </air:TextInfo>
                                            </air:BaggageRestriction>`;
                                        return true;
                                    })

                                BagDetails += `
                        <air:BagDetails ApplicableBags="${bag['$']['ApplicableBags']}">
                            ${BaggageRestriction}
                        </air:BagDetails>`;
                                return true;
                            })

                        BaggageAllowanceInfo += `
                        <air:BaggageAllowanceInfo TravelerType="${allowanceInfo['$']['TravelerType']}" Origin="${allowanceInfo['$']['Origin']}" Destination="${allowanceInfo['$']['Destination']}" Carrier="${allowanceInfo['$']['Carrier']}">
                            ${URLInfo}
                            ${TextInfo}
                            ${BagDetails}
                        </air:BaggageAllowanceInfo>`;
                        return true;
                    })



                /****************CarryOnAllowanceInfo*******************/
                let CarryOnAllowanceInfo = ``
                baggage['air:CarryOnAllowanceInfo'] &&
                    baggage['air:CarryOnAllowanceInfo'].map(carry => {

                        /* CarryTaxInfo */
                        let CarryTaxInfo = ``
                        carry['air:TextInfo'] &&
                            carry['air:TextInfo'].map(text => {
                                CarryTaxInfo += `
                            <air:TextInfo>
                                <air:Text>${text['air:Text'][0]}</air:Text>
                            </air:TextInfo>`;
                                return true;
                            })

                        /* CarryOnDetails */
                        let CarryOnDetails = ``
                        carry['air:CarryOnDetails'] &&
                            carry['air:CarryOnDetails'].map(detail => {

                                /* DetailBaggageRestriction */
                                let DetailBaggageRestriction = ``
                                detail['air:BaggageRestriction'] &&
                                    detail['air:BaggageRestriction'].map(detailRestriction => {
                                        DetailBaggageRestriction += `
                                <air:BaggageRestriction>
                                    <air:TextInfo>
                                        <air:Text>${detailRestriction['air:TextInfo'][0]['air:Text'][0]}</air:Text>
                                    </air:TextInfo>
                                </air:BaggageRestriction>`;
                                        return true;
                                    })

                                CarryOnDetails += `
                            <air:CarryOnDetails ApplicableCarryOnBags="${detail['$']['ApplicableCarryOnBags']}">
                                ${DetailBaggageRestriction}
                            </air:CarryOnDetails>`;
                                return true;
                            })

                        CarryOnAllowanceInfo += `
                        <air:CarryOnAllowanceInfo Origin="${carry['$']['Origin']}" Destination="${carry['$']['Destination']}" Carrier="${carry['$']['Carrier']}">
                            ${CarryTaxInfo}
                            ${CarryOnDetails}
                        </air:CarryOnAllowanceInfo>`;
                        return true;
                    })


                BaggageAllowances += `
                    <air:BaggageAllowances>
                        ${BaggageAllowanceInfo}
                        ${CarryOnAllowanceInfo}
                    </air:BaggageAllowances>`;
                return true;
            })





        AirPricingInfoLog += `
                <air:AirPricingInfo Key="${pricing['$']['Key']}" TotalPrice="${pricing['$']['TotalPrice']}" BasePrice="${pricing['$']['BasePrice']}" ApproximateTotalPrice="${pricing['$']['ApproximateTotalPrice']}" ApproximateBasePrice="${pricing['$']['ApproximateBasePrice']}" ApproximateTaxes="${pricing['$']['ApproximateTaxes']}" Taxes="${pricing['$']['Taxes']}" LatestTicketingTime="${pricing['$']['LatestTicketingTime']}" PricingMethod="${pricing['$']['PricingMethod']}" IncludesVAT="${pricing['$']['IncludesVAT']}" ETicketability="${pricing['$']['ETicketability']}" ProviderCode="${pricing['$']['ProviderCode']}">
                    ${FareInfo}
                    ${BookingInfo}
                    ${TaxInfo}
                    <air:FareCalc>${pricing['air:FareCalc'][0]}</air:FareCalc>
                    ${PassengerType}
                    ${ChangePenalty}
                    ${BaggageAllowances}
                </air:AirPricingInfo>`;
        return true;
    })

    /********************FareNote*****************************/
    let FareNoteLog = ``
    FareNote &&
        FareNote.map(fareNote => {
            FareNoteLog += `
        <air:FareNote Key="${fareNote['$']['Key']}">${fareNote['_']}</air:FareNote>`;
            return true;
        })

    /***********************HostToken**************************/
    let HostTokenLog = ``
    HostToken &&
        HostToken.map(token => {
            HostTokenLog += `
        <common_v47_0:HostToken Key="${token['$']['Key']}">${token['_']}</common_v47_0:HostToken>`;
            return true;
        })


    // *********************************Traveler Info*****************************
    let { travelerInfo, contact } = travelerAllInfo
    contact = contact.toString()
    if (contact.slice(0, 1) === 0) {
        contact = contact.slice(1)
    }

    let BookingTraveler = ''
    if (travelerInfo.length > 0) {
        travelerInfo.map((item, index) => {
            if (item.dateOfBirth !== null) {

                BookingTraveler += `
            <com:BookingTraveler Key="P_${index}" Age="21" DOB="${item.dateOfBirth}" Gender="${item.gender}" TravelerType="${item.passengerType}">
                <com:BookingTravelerName First="${item.firstName}" Last="${item.lastName}" />
                <com:PhoneNumber
                            CountryCode="${item.countryCode}"
                            Location="DAC"
                            Number="${contact}"
                            Type="Other"
                    />
                <com:SSR Type="DOCS" FreeText="P/${item.nationality}/${item.passportNumber}/${item.nationality}/${item.expiryDate.slice(8) + getMonthName(item.expiryDate.slice(5, 7)) + item.expiryDate.slice(2, 4)}/${item.gender}/${item.dateOfBirth.slice(8) + getMonthName(item.dateOfBirth.slice(5, 7)) + item.dateOfBirth.slice(2, 4)}/${item.lastName}/${item.firstName}"/>
            </com:BookingTraveler>`;
            }
            return true;
        })
    }

    // let BookingTraveler = `
    //         <com:BookingTraveler Key="P_0" Age="21" DOB="1998-07-25" Gender="${travelerInfo['gender']}" TravelerType="ADT">
    //             <com:BookingTravelerName First="${travelerInfo['firstName']}" Last="${travelerInfo['lastName']}" />
    //             <com:PhoneNumber
    //                         CountryCode="${travelerInfo['countryCode']}"
    //                         Location="DAC"
    //                         Number="${travelerInfo['contactNumber']}"
    //                         Type="Other"
    //                 />
    //             <com:SSR Type="DOCS" FreeText="P/BD/${travelerInfo['passport']}/BD/25Jul98/${travelerInfo['gender']}/23Oct20/${travelerInfo['lastName']}/${travelerInfo['firstName']}"/>
    //         </com:BookingTraveler>`;
    // if (adult === 2) {
    //     BookingTraveler += `
    //         <com:BookingTraveler Key="P_1" Age="31" DOB="1988-07-25" Gender="M" TravelerType="ADT">
    //                 <com:BookingTravelerName First="AHMED" Last="SAQUIB" />
    //                 <com:PhoneNumber
    //                                     CountryCode="880"
    //                                     Location="DAC"
    //                                     Number="1707500800"
    //                                     Type="Other"
    //                             />
    //                 <com:SSR Type="DOCS" FreeText="P/BD/EA221731/BD/25Jul88/M/23Oct20/SAQUIB/AHMED"/>
    //             </com:BookingTraveler>`
    // }
    // if (adult > 0) {
    //     BookingTraveler += `
    //             <com:BookingTraveler Key="P_2" Age="7" DOB="2012-07-25" Gender="M" TravelerType="C07">
    //                 <com:BookingTravelerName First="SAQUIB" Last="MOHAMMAD" />
    //                 <com:PhoneNumber
    //                                     CountryCode="880"
    //                                     Location="DAC"
    //                                     Number="1707500800"
    //                                     Type="Other"
    //                             />
    //                 <com:SSR Type="DOCS" FreeText="P/BD/EA24DS3/BD/25Jul12/M/23Oct20/MOHAMMAD/SAQUIB"/>
    //                 <com:NameRemark Key="P_2">
    //                     <com:RemarkData>P-C07</com:RemarkData>
    //                 </com:NameRemark>
    //             </com:BookingTraveler>`
    // }


    let date = new Date();
    let TicketDate = dateFormat(date.setDate(date.getDate() + 1), "yyyy-mm-dd") + 'T02:37:17+06:00';

    let schema = `<soap:Envelope 
  xmlns:soap="http://schemas.xmlsoap.org/soap/envelope/">
    <soap:Header 
    xmlns:univ="http://www.travelport.com/schema/universal_v47_0">
        <univ:SupportedVersions airVersion="air_v47_0"/>
    </soap:Header>
    <soap:Body>
        <univ:AirCreateReservationReq
            AuthorizedBy="user" TraceId=""
            RetainReservation="Both" TargetBranch="${TARGET_BRANCH}"
            RestrictWaitlist="true"
            
      xmlns:univ="http://www.travelport.com/schema/universal_v47_0"
            
      xmlns:com="http://www.travelport.com/schema/common_v47_0"
            
      xmlns:air="http://www.travelport.com/schema/air_v47_0"
            
      xmlns:common_v47_0="http://www.travelport.com/schema/common_v47_0"
            >
            <com:BillingPointOfSaleInfo OriginApplication="ST" />
            ${BookingTraveler}
            <air:AirPricingSolution Key="${AirPricingSolution['Key']}" TotalPrice="${AirPricingSolution['TotalPrice']}" BasePrice="${AirPricingSolution['BasePrice']}" ApproximateTotalPrice="${AirPricingSolution['ApproximateTotalPrice']}" ApproximateBasePrice="${AirPricingSolution['ApproximateBasePrice']}" Taxes="${AirPricingSolution['Taxes']}" Fees="${AirPricingSolution['Fees']}" ApproximateTaxes="${AirPricingSolution['ApproximateTaxes']}" QuoteDate="${AirPricingSolution['QuoteDate']}">
                ${segmentXML}
                ${AirPricingInfoLog}
                ${FareNoteLog}
                ${HostTokenLog}
            </air:AirPricingSolution>
            <com:ActionStatus Type="TAU" TicketDate="${TicketDate}" ProviderCode="1G" />
        </univ:AirCreateReservationReq>
    </soap:Body>
</soap:Envelope>`;
    return schema
}
export default AirCreateReservationReqLog