import { SET_MESSAGE } from './types'
import Axios from 'axios'


// Get user info 
export const getUserInfo = id => async dispatch => {
    return Axios.get(`/api/users/info/${id}`)
        .then(res => {
            return res.data.data
        })
        .catch(err => {
            dispatch({
                type: SET_MESSAGE,
                payload: {
                    message: err.response && err.response.data.message,
                    type: 'error',
                }
            })
            return false
        })
}


// Data Store
export const storeData = (data) => async dispatch => {
    return Axios.post(`/api/users`, data)
        .then(res => {
            dispatch({
                type: SET_MESSAGE,
                payload: {
                    message: res.data.message,
                }
            })
            return true;
        })
        .catch(err => {
            dispatch({
                type: SET_MESSAGE,
                payload: {
                    message: err.response && err.response.data.message,
                    type: 'error',
                }
            })
            return false;
        })
}


// Data Update
