import React, { Component, Fragment } from 'react';
import Modal from 'react-bootstrap/Modal';
import { Button, Form } from 'react-bootstrap';
import { connect } from 'react-redux';
import { changePassword } from '../../../store/actions/profileActions';

class ChangePassword extends Component {
    constructor(props) {
        super(props);
        this.state = {
            currentPassword: '',
            password: '',
            confirmPassword: '',
            isWait: false,
        }
    }
    changeHandler = event => {
        this.setState({
            [event.target.name]: event.target.value
        })
    }
    submitHandler = event => {
        event.preventDefault()
        this.setState({ isWait: true })
        let { changePassword, actionIsDone } = this.props
        let response = changePassword(this.state)
        if (response) actionIsDone()
        this.setState({ isWait: false })
    }
    render() {
        let { currentPassword, password, confirmPassword, isWait } = this.state
        let isDone = currentPassword && password && confirmPassword && (password === confirmPassword)

        return (
            <Fragment>
                <Modal show={this.props.isOpen} onHide={this.props.closeHandler}>
                    <Modal.Header closeButton>
                        <Modal.Title>Edit Profile Information</Modal.Title>
                    </Modal.Header>
                    <Modal.Body>
                        <Form onSubmit={this.submitHandler}>
                            <Form.Group>
                                <Form.Label>Current Password *</Form.Label>
                                <Form.Control
                                    type="password"
                                    name="currentPassword"
                                    defaultValue={currentPassword}
                                    onChange={this.changeHandler}
                                    placeholder="Enter Current Password"
                                />
                            </Form.Group>
                            <Form.Group>
                                <Form.Label>New Password *</Form.Label>
                                <Form.Control
                                    type="password"
                                    name="password"
                                    defaultValue={password}
                                    onChange={this.changeHandler}
                                    placeholder="Enter New Password"
                                />
                            </Form.Group>
                            <Form.Group>
                                <Form.Label>Confirm Password *</Form.Label>
                                <Form.Control
                                    type="password"
                                    name="confirmPassword"
                                    defaultValue={confirmPassword}
                                    onChange={this.changeHandler}
                                    placeholder="Enter Confirm Password"
                                />
                            </Form.Group>

                            <Button type="submit" variant="dark" disabled={!isDone} size="lg" block>
                                {isWait ? `Please Wait...` : `Save`}
                            </Button>
                        </Form>
                    </Modal.Body>
                </Modal>
            </Fragment>
        )
    }
}
const mapStateToProps = state => ({
    user: state.user,
})
export default connect(mapStateToProps, { changePassword })(ChangePassword);