import React, { Component, Fragment } from 'react';
import ReactTable from 'react-table';
import 'react-table/react-table.css';
import Edit from './Edit';
import { Button, Form } from 'react-bootstrap';
import { deleteData } from '../../../store/actions/airportActions'
import { connect } from 'react-redux';
import Axios from 'axios'

class AirportList extends Component {
    constructor(props) {
        super(props);
        this.state = {
            data: [],
            loading: true,
            limit: 10,
            page: 1,
            pages: -1,
            isOpen: false,
            editData: {},
            name: '',
            code: ''
        }
    }
    componentDidMount() {
        this.fetchData();
    }
    fetchData(limit = this.state.limit, page = this.state.page, name = this.state.name, iata = this.state.iata) {
        this.setState({
            data: [],
            loading: true,
        })
        Axios.post(`/api/airports/get`, { limit, page, name, iata })
            .then(res => {
                this.setState({
                    data: res.data.data.docs,
                    page: res.data.data.page,
                    pages: res.data.data.totalPages,
                    limit: res.data.data.limit,
                    loading: false
                })
            })
    }
    changeHandler = event => {
        this.setState({
            [event.target.name]: event.target.value
        })
        let { limit, page, name, iata } = this.state

        if (event.target.name === 'name') {
            name = event.target.value
        } else if (event.target.name === 'iata') {
            iata = event.target.value
        }
        this.fetchData(limit, page, name, iata);
    }
    actionIsDone = async () => {
        this.setState({
            airlines: [],
            isOpen: false,
            editData: {},
            loading: true
        })
        this.fetchData();
    }
    removeHandler = async (id) => {
        let { deleteData } = this.props
        await deleteData(id)
        this.fetchData();
    }
    render() {
        let { data, loading, pages, limit, name, iata, isOpen, editData } = this.state
        const columns = [
            {
                Header: 'IATA',
                accessor: 'iata',
            },
            {
                Header: 'Short Name',
                accessor: 'short',
            },
            {
                Header: 'Name',
                accessor: 'name',
            },
            {
                Header: 'City',
                accessor: 'city',
            },
            {
                Header: 'ISO',
                accessor: 'iso',
            },
            {
                Header: 'LAT',
                accessor: 'lat',
            },
            {
                Header: 'LON',
                accessor: 'lon',
            },
            {
                Header: 'Action',
                Cell: row => <span>
                    <button className="btn btn-dark" onClick={() => this.setState({ isOpen: true, editData: row.original })}><i className="fa fa-edit"></i></button>
                    <button className="btn btn-danger ml-4" onClick={() => window.confirm('Are you sure?') && this.removeHandler(row.original.iata)}><i className="fa fa-trash"></i></button>
                </span>,
            },
        ];
        return (
            <Fragment>
                <div className="bg-light">
                    <div className="section-padding-top">
                        <div className="container bg-white">

                            <div className="section-title text-center"><h2>Airport</h2></div>

                            <div className="row">
                                <div className="col-lg-3">
                                    <Form.Group>
                                        <Form.Control
                                            type="text"
                                            name="name"
                                            value={name}
                                            onChange={this.changeHandler}
                                            placeholder="Search airport"
                                        />
                                    </Form.Group>
                                </div>
                                <div className="col-lg-3">
                                    <Form.Group>
                                        <Form.Control
                                            type="text"
                                            name="iata"
                                            value={iata}
                                            onChange={this.changeHandler}
                                            placeholder="Search iata"
                                        />
                                    </Form.Group>
                                </div>


                                <div className="col-lg-9 text-right">
                                    <Button type="button" variant="dark" onClick={() => this.setState({ isOpen: true })}><i className="fa fa-plus" /></Button>
                                </div>
                            </div>

                            <div className="row">
                                <div className="col-lg-12">
                                    <ReactTable
                                        data={data}
                                        columns={columns}
                                        defaultPageSize={limit}
                                        minRows={1}
                                        noDataText='No rows found'
                                        loading={loading}
                                        manual
                                        pages={pages}
                                        onPageChange={(page) => this.fetchData(limit, page + 1)}
                                        onPageSizeChange={(limit, page) => this.fetchData(limit, page)}
                                    />
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
                {isOpen &&
                    <Edit
                        isOpen={isOpen}
                        closeHandler={() => this.setState({ editData: {}, isOpen: false })}
                        actionIsDone={this.actionIsDone.bind(this)}
                        data={editData}
                    />
                }
            </Fragment>
        );
    }
}
export default connect(null, { deleteData })(AirportList)