import React from 'react';
import ReactDOM from 'react-dom';
import App from './components/App';
import * as serviceWorker from './serviceWorker';

/* Set redux data */
import { Provider } from 'react-redux';

import store from './store';
import setAuthToken from './utils/setAuthToken';
import { SET_USER, SET_AIR_PRICING, SET_BASIC_INFO } from './store/actions/types';
import refreshToken from './utils/refreshToken';

// Set user auth token
let token = localStorage.getItem('auth_token');
if (token) {
    setAuthToken(token)
    store.dispatch({
        type: SET_USER,
        payload: token
    })
    refreshToken()
}

// Set basic info
let basicInfo = localStorage.getItem('basicInfo');
if (basicInfo) {
    basicInfo = JSON.parse(basicInfo)
    store.dispatch({
        type: SET_BASIC_INFO,
        payload: {
            airports: basicInfo.airports,
            airlines: basicInfo.airlines,
            countries: basicInfo.countries,
            basicInfo: basicInfo.basicInfo,
            usefulLink: basicInfo.usefulLink,
            footerLogo: basicInfo.footerLogo,
            sliders: basicInfo.sliders,
            packages: basicInfo.packages,
            services: basicInfo.services,
        }
    })
}


// Set air pricing info 
let pricingInfo = localStorage.getItem('pricingInfo');
if (pricingInfo) {
    let { pricing, segmentInfo } = JSON.parse(pricingInfo)
    store.dispatch({
        type: SET_AIR_PRICING,
        payload: {
            pricing,
            segmentInfo,
            status: 2
        }
    })
}

ReactDOM.render(
    <Provider store={store}>
        <App /></Provider>, document.getElementById('root'));

// If you want your app to work offline and load faster, you can change
// unregister() to register() below. Note this comes with some pitfalls.
// Learn more about service workers: https://bit.ly/CRA-PWA
serviceWorker.unregister();
