import React, { Component, Fragment } from 'react'
import { getService } from '../../store/actions/serviceActions';
import { connect } from 'react-redux';
import renderHTML from 'react-render-html';
import { Link } from 'react-router-dom';


class Service extends Component {
    state = {
        data: []
    }
    static getDerivedStateFromProps(nextProps, prevState) {
        if (nextProps.common.services === prevState.data) return null
        return {
            data: nextProps.common.services,
        }
    }
    render() {
        let { data } = this.state
        return (
            <Fragment>
                <section className="service-area section-padding">
                    <div className="container">
                        <div className="row">
                            <div className="col-lg-8 col-md-8 offset-lg-2 offset-md-2">
                                <div className="section-title text-center section-title-padding">
                                    <h2 className="text-white">Our Special Services</h2>
                                </div>
                            </div>
                        </div>

                        <div className="row">
                            {data.length > 0 && data.map(item => <div key={item._id} className="col-lg-4 col-md-6">
                                <div className="single-service-content">
                                    <div className="single-service-inner-content">
                                        <h3>{item.title}</h3>
                                        <div>{item.description && renderHTML(item.description)}</div>
                                        <Link to={`/service/detail/${item._id}`} className="package-book-now">More Detail</Link>
                                    </div>
                                    <div className="service-box">
                                        <i className="fa fa-facebook"></i>
                                    </div>
                                </div>
                            </div>)}

                        </div>
                    </div>
                </section>
            </Fragment>
        )
    }
}
const mapStateToProps = state => ({
    common: state.common
})
export default connect(mapStateToProps)(Service)