import React, { Component, Fragment } from 'react'
import AsyncSelect from 'react-select/async';
import DatePicker from "react-datepicker";
import "react-datepicker/dist/react-datepicker.css";
import { textLimit } from '../../../utils/helper.js';
import dateFormat from 'dateformat';
import { connect } from 'react-redux';


class MultiCity extends Component {
    state = {
        airports: [],
        inputValue: '',
    }
    static getDerivedStateFromProps(nextProps, prevState) {
        if (JSON.stringify(nextProps.airports) === JSON.stringify(prevState.airports)) return null
        return {
            airports: nextProps.airports,
        }
    }
    inputChangeHandle = (newValue) => {
        const inputValue = newValue.replace(/\W/g, '');
        this.setState({ inputValue });
        return inputValue;
    }
    render() {
        let { airports } = this.state;
        let formatOptionLabel = ({ iata, name, flag }) => (
            <div className="d-flex justify-content-between"><span>{textLimit(name, 40)}</span> <span>{iata}{flag && <img src={flag} alt='flag' width='20' />}</span></div>
        );
        let filterData = inputValue => {
            if (inputValue.length < 3) return null
            let result = airports.filter(item => {
                if (inputValue.length === 3) {
                    return item.iata.toLowerCase().includes(inputValue.toLowerCase())
                } else {
                    return item.iata.toLowerCase().includes(inputValue.toLowerCase()) || item.name.toLowerCase().includes(inputValue.toLowerCase())
                }
            });
            return result.slice(0, 10)
        };
        let loadOptions = (inputValue, callback) => {
            setTimeout(() => {
                callback(filterData(inputValue));
            }, 10);
        }

        let { origin2, destination2, departureDate2, origin3, destination3, departureDate3, origin4, destination4, departureDate4 } = this.props.data;
        let numMultiCity = this.props.numMultiCity

        let origin = (numMultiCity === 1 && origin2) || (numMultiCity === 2 && origin3) || (numMultiCity === 3 && origin4) || ''
        let origin_filed = (numMultiCity === 1 && "origin2") || (numMultiCity === 2 && "origin3") || (numMultiCity === 3 && "origin4")

        let destination = (numMultiCity === 1 && destination2) || (numMultiCity === 2 && destination3) || (numMultiCity === 3 && destination4) || ''
        let destination_filed = (numMultiCity === 1 && "destination2") || (numMultiCity === 2 && "destination3") || (numMultiCity === 3 && "destination4")

        let departureDate = (numMultiCity === 1 && departureDate2) || (numMultiCity === 2 && departureDate3) || (numMultiCity === 3 && departureDate4) || ''
        let departureDate_filed = (numMultiCity === 1 && "departureDate2") || (numMultiCity === 2 && "departureDate3") || (numMultiCity === 3 && "departureDate4")

        return (
            <Fragment>
                <div className="row mb-2">
                    <div className="col-lg-6 col-md-12">
                        <label className="eTicketLabel">FROM *</label>
                        <AsyncSelect
                            cacheOptions
                            defaultOptions
                            loadOptions={loadOptions}
                            onChange={(selected) => this.props.stateHandler(origin_filed, selected.iata)}
                            onInputChange={this.inputChangeHandle}
                            formatOptionLabel={formatOptionLabel}
                            value={airports.length > 0 && airports.filter(item => item.iata === origin)}
                        />
                    </div>
                    <div className="col-lg-6 col-md-12">
                        <label className="eTicketLabel">TO *</label>
                        <AsyncSelect
                            cacheOptions
                            defaultOptions
                            loadOptions={loadOptions}
                            onChange={(selected) => this.props.stateHandler(destination_filed, selected.iata)}
                            onInputChange={this.inputChangeHandle}
                            formatOptionLabel={formatOptionLabel}
                            value={airports.length > 0 && airports.filter(item => item.iata === destination)}
                        />
                    </div>
                </div>

                <div className="row mb-2">
                    <div className="col-lg-6 col-md-12">
                        <label className="eTicketLabel">DEPARTURE *</label><br />
                        <DatePicker
                            className="e-ticketing-input-field"
                            selected={departureDate && new Date(departureDate)}
                            onChange={(date) => this.props.stateHandler(departureDate_filed, dateFormat(date, "yyyy-mm-dd"))}
                            placeholder="Enter Departure Date"
                            dateFormat="dd MMMM yyyy"
                            minDate={this.props.data.departureDate && new Date(this.props.data.departureDate)}
                        />
                    </div>
                    <div className="col-lg-6 col-md-12">
                    </div>
                </div>
            </Fragment>
        )
    }
}
const mapStateToProps = state => ({
    airports: state.common.airports,
})
export default connect(mapStateToProps)(MultiCity)