import React, { Component, Fragment } from 'react'
import AsyncSelect from 'react-select/async';
import DatePicker from "react-datepicker";
import "react-datepicker/dist/react-datepicker.css";
import { textLimit } from '../../../utils/helper.js';
import dateFormat from 'dateformat';
import { connect } from 'react-redux';

class FromToArea extends Component {
    constructor(props) {
        super(props);
        this.state = {
            airports: [],
            countries: [],
            inputValue: '',
        }
    }
    static getDerivedStateFromProps(nextProps, prevState) {
        if (JSON.stringify(nextProps.common.airports) === JSON.stringify(prevState.airports)) return null
        return {
            airports: nextProps.common.airports,
            countries: nextProps.common.countries,
        }
    }
    inputChangeHandle = (newValue) => {
        const inputValue = newValue.replace(/\W/g, '');
        this.setState({ inputValue });
        return inputValue;
    }
    render() {
        let { airports, countries } = this.state;
        let { origin, destination, departureDate, returnDate, route } = this.props.data;

        let formatOptionLabel = ({ iata, name, short, iso }) => {
            let flag = null
            let selCnt = countries.length > 0 && countries.filter(item => item.code === iso && item.flag)
            if (selCnt.length > 0) flag = selCnt[0].flag

            return (<div className="d-flex justify-content-between"><span>{textLimit(short || name, 35)}</span> <span>{iata}{flag && <img src={flag} alt='flag' width='20' />}</span></div>)
        };

        let filterData = inputValue => {
            if (inputValue.length < 3) return null
            let result = airports.filter(item => {
                if (inputValue.length === 3) {
                    return item.iata && item.iata.toLowerCase().includes(inputValue.toLowerCase())
                } else {
                    return new RegExp(inputValue, "i").test(item.iata) || new RegExp(inputValue, "i").test(item.name)
                }
            });
            return result.slice(0, 10)
        };
        let loadOptions = (inputValue, callback) => {
            setTimeout(() => {
                callback(filterData(inputValue));
            }, 10);
        }
        return (
            <Fragment>
                <div className="row mb-2">
                    <div className="col-lg-6 col-md-6">
                        <label className="eTicketLabel">FROM *</label>
                        <AsyncSelect
                            cacheOptions
                            defaultOptions
                            loadOptions={loadOptions}
                            onChange={(selected) => this.props.stateHandler('origin', selected.iata)}
                            onInputChange={this.inputChangeHandle}
                            formatOptionLabel={formatOptionLabel}
                            value={airports.length > 0 && airports.filter(item => item.iata === origin)}
                        />
                    </div>
                    <div className="col-lg-6 col-md-6">
                        <label className="eTicketLabel">TO *</label>
                        <AsyncSelect
                            cacheOptions
                            defaultOptions
                            loadOptions={loadOptions}
                            onChange={(selected) => this.props.stateHandler('destination', selected.iata)}
                            onInputChange={this.inputChangeHandle}
                            formatOptionLabel={formatOptionLabel}
                            value={airports.length > 0 && airports.filter(item => item.iata === destination)}
                        // defaultInputValue={destination}
                        />
                    </div>
                </div>

                <div className="row mb-2">
                    <div className="col-lg-6 col-md-6">
                        <label className="eTicketLabel">DEPARTURE *</label><br />
                        <DatePicker
                            className="e-ticketing-input-field"
                            selected={departureDate && new Date(departureDate)}
                            onChange={(date) => this.props.stateHandler('departureDate', dateFormat(date, "yyyy-mm-dd"))}
                            placeholder="Enter Departure Date"
                            minDate={new Date()}
                            dateFormat="dd MMMM yyyy"
                        />
                    </div>
                    <div className="col-lg-6 col-md-6">
                        <label className="eTicketLabel">RETURN *</label><br />
                        <DatePicker
                            className="e-ticketing-input-field"
                            selected={returnDate && new Date(returnDate)}
                            onChange={(date) => this.props.stateHandler('returnDate', dateFormat(date, "yyyy-mm-dd"))}
                            disabled={route !== "RETURN_FLIGHT"}
                            placeholder="Enter Return Date"
                            minDate={departureDate && new Date(departureDate)}
                            dateFormat="dd MMMM yyyy"
                        />
                    </div>
                </div>
            </Fragment>
        )
    }
}
const mapStateToProps = state => ({
    common: state.common,
})
export default connect(mapStateToProps)(FromToArea)