import React, { Component, Fragment } from 'react'

// Import slider files
import Slider from "react-slick";
import "slick-carousel/slick/slick.css";
import "slick-carousel/slick/slick-theme.css";
// import Slide1 from '../assets/images/slider/slider_1.jpg'
// import Slide2 from '../assets/images/slider/slider_2.jpg'
// import Slide3 from '../assets/images/slider/slider_3.jpg'
import { connect } from 'react-redux';


class Sliders extends Component {
    state = {
        data: []
    }
    static getDerivedStateFromProps(nextProps, prevState) {
        if (nextProps.common.sliders === prevState.data) return null
        return {
            data: nextProps.common.sliders,
        }
    }
    render() {
        let { data } = this.state

        let settings = {
            dots: true,
            infinite: true,
            fade: true,
            speed: 5000,
            autoplay: true,
            slidesToShow: 1,
            slidesToScroll: 1
        };

        let type = 0
        if (this.props.auth.isAuth) {
            type = this.props.auth.user.type
        }
        return (
            <Fragment>
                <div className="home-slider">
                    <div id="slides">
                        <Slider {...settings}>
                            {data.length > 0 && data.map(item => (item.type === type || item.type === 0) && <img src={item.image} alt="e ticketing slider" key={item._id} />)}
                        </Slider>
                        <div className="slides-container">
                        </div>
                    </div>
                </div>
            </Fragment>
        )
    }
}
const mapStateToProps = state => ({
    auth: state.auth,
    common: state.common
})
export default connect(mapStateToProps)(Sliders)