import React, { Component, Fragment } from 'react';
import Axios from 'axios';
import ReactTable from 'react-table';
import 'react-table/react-table.css';
import { connect } from 'react-redux';
import { Button, Form } from 'react-bootstrap';
import Edit from './Edit';
import { dateTime } from '../../../utils/helper';
import { deleteData } from '../../../store/actions/bonusActions'


class Bonus extends Component {
    constructor(props) {
        super(props);
        this.state = {
            data: [],
            loading: true,
            limit: 100,
            page: 1,
            pages: -1,
            isOpen: false,
            editData: {},
            authUser: props.user,
            userId: null,
            name: ''
        }
    }
    componentDidMount() {
        this.fetchData();
    }
    fetchData(limit = this.state.limit, page = this.state.page, name = this.state.name) {
        let { userId } = this.state
        this.setState({
            data: [],
            loading: true,
        })
        Axios.post(`/api/bonus/get`, { limit, page, userId, name })
            .then(res => {
                this.setState({
                    data: res.data.data.docs,
                    page: res.data.data.page,
                    pages: res.data.data.totalPages,
                    limit: res.data.data.limit,
                    loading: false
                })
            })
    }
    actionIsDone = () => {
        this.setState({ isOpen: false, editData: {} })
        this.fetchData();
    }
    deleteHandler = async (id) => {
        await this.props.deleteData(id)
        setTimeout(() => {
            this.fetchData()
        }, 500)
    }
    changeHandler = event => {
        let name = event.target.value
        this.setState({ name })
        this.fetchData('', '', event.target.value);
    }
    render() {
        let { data, loading, pages, limit, isOpen, editData, name } = this.state

        const columns = [
            {
                Header: 'SL',
                accessor: 'index',
                Cell: row => <span>{row.index + 1}</span>,
                width: 30,
            },
            {
                Header: 'Date',
                accessor: 'date',
                Cell: row => <span>{dateTime(row.original.date, 2)}</span>,
            },
            {
                Header: 'Purpose',
                accessor: 'purpose',
            },
            {
                Header: 'Recommended',
                accessor: 'recommended',
            },
            {
                Header: 'Agent ID',
                accessor: 'agentId',
            },
            {
                Header: 'Amount',
                accessor: 'amount',
            },
            {
                Header: 'Total Amount',
                accessor: 'totalAmount',
            },
            {
                Header: 'Action',
                Cell: row => <span>
                    <button className="btn btn-danger" onClick={() => window.confirm('Are you sure?') && this.deleteHandler(row.original._id)}><i className="fa fa-trash"></i></button>
                </span>,
            },
        ];

        return (
            <Fragment>
                <div className="bg-light">
                    <div className="section-padding-top">
                        <div className="container bg-white">

                            <div className="section-title text-center"><h2>Bonus</h2></div>

                            <div className="row">
                                <div className="col-lg-3">
                                    <Form.Group>
                                        <Form.Control
                                            type="text"
                                            name="name"
                                            value={name}
                                            onChange={this.changeHandler}
                                            placeholder="Search By Agent ID"
                                        />
                                    </Form.Group>
                                </div>
                                <div className="col-lg-9">
                                    <Button type="button" variant="dark" onClick={() => this.setState({ isOpen: true })}><i className="fa fa-plus" /></Button>
                                </div>
                            </div>

                            <div className="row">
                                <div className="col-lg-12">
                                    <ReactTable
                                        data={data}
                                        columns={columns}
                                        defaultPageSize={limit}
                                        minRows={1}
                                        noDataText='No rows found'
                                        loading={loading}
                                        manual
                                        pages={pages}
                                        onPageChange={(page) => this.fetchData(limit, page + 1)}
                                        onPageSizeChange={(limit, page) => this.fetchData(limit, page)}
                                    />
                                </div>
                            </div>
                        </div>
                    </div>
                </div>


                {isOpen &&
                    <Edit
                        isOpen={isOpen}
                        closeHandler={() => this.setState({ isOpen: false, editData: {} })}
                        actionIsDone={this.actionIsDone.bind(this)}
                        data={editData}
                    />
                }
            </Fragment>
        );
    }
}
const mapStateToProps = state => ({
    common: state.common,
    user: state.auth.user,
})
export default connect(mapStateToProps, { deleteData })(Bonus)