import React, { Component, Fragment } from 'react';
import Axios from 'axios'
import { Link } from 'react-router-dom';


class Request extends Component {
    constructor(props) {
        super(props);
        this.state = {
            user: {},
            ticket: [],
            deposit: []
        }
    }
    componentDidMount() {
        Axios.get(`/api/users/detail`)
            .then(res => {
                this.setState({
                    ticket: res.data.data.ticket,
                    deposit: res.data.data.deposit,
                })
            })
            .catch(err => console.log(err))
    }
    render() {
        let { ticket, deposit } = this.state
        return (
            <Fragment>
                <div className="bg-light">
                    <div className="section-padding-top">
                        <div className="container bg-white">

                            <div className="text-center">
                                <h2>Request</h2>
                            </div>

                            <table className='table'>
                                <thead>
                                    <tr>
                                        <th>Booking</th>
                                        <th>On Hold</th>
                                        <th>Ticket</th>
                                        <th>In Process</th>
                                        <th>Expire</th>
                                        <th>Unconfirmed</th>
                                        <th>Cancelled</th>
                                        <th>Refund</th>
                                        <th>Void</th>
                                        <th>No-Show</th>
                                        <th>Pending Deposit</th>
                                        <th>Approved Deposit</th>
                                        <th>Rejected Deposit</th>
                                    </tr>
                                </thead>
                                <tbody>
                                    <tr>
                                        <td><Link to='/backend/admin/ticket/booking'>{ticket.filter(item => item.status === 'booking').length}</Link></td>
                                        <td><Link to='/backend/admin/ticket/on-hold'>{ticket.filter(item => item.status === 'on-hold').length}</Link></td>
                                        <td><Link to='/backend/admin/ticket/ticket'>{ticket.filter(item => item.status === 'ticket').length}</Link></td>
                                        <td><Link to='/backend/admin/ticket/in-process'>{ticket.filter(item => item.status === 'in-process').length}</Link></td>
                                        <td><Link to='/backend/admin/ticket/expire'>{ticket.filter(item => item.status === 'expire').length}</Link></td>
                                        <td><Link to='/backend/admin/ticket/unconfirmed'>{ticket.filter(item => item.status === 'unconfirmed').length}</Link></td>
                                        <td><Link to='/backend/admin/ticket/cancelled'>{ticket.filter(item => item.status === 'cancelled').length}</Link></td>
                                        <td><Link to='/backend/admin/ticket/refund'>{ticket.filter(item => item.status === 'refund').length}</Link></td>
                                        <td><Link to='/backend/admin/ticket/void'>{ticket.filter(item => item.status === 'void').length}</Link></td>
                                        <td><Link to='/backend/admin/ticket/no-show'>{ticket.filter(item => item.status === 'no-show').length}</Link></td>
                                        <td><Link to='/backend/deposits'>{deposit.filter(item => item.status === 0).length}</Link></td>
                                        <td><Link to='/backend/deposits'>{deposit.filter(item => item.status === 1).length}</Link></td>
                                        <td><Link to='/backend/deposits'>{deposit.filter(item => item.status === 2).length}</Link></td>
                                    </tr>
                                </tbody>
                            </table>
                        </div>
                    </div>
                </div>
            </Fragment>
        );
    }
}
export default Request