import React, { Component, Fragment } from 'react'
import { Button, Form } from 'react-bootstrap';
import { connect } from 'react-redux';
import { uploadPassportVisa } from '../../store/actions/passengerActions'


class PassportVisaForm extends Component {
    state = {
        passenger: this.props.data,
        passport: null,
        visa: null,
        isWait: false
    }
    fileUploadHandler = event => {
        this.setState({
            passport: event.target.files[0],
        });
    }
    visaUploadHandler = event => {
        this.setState({
            visa: event.target.files[0],
        });
    }
    submitHandler = async event => {
        event.preventDefault()
        this.setState({ isWait: true })
        let { passport, visa, passenger } = this.state
        if (passport || visa) {
            let fd = new FormData()
            fd.append('passport', passport)
            fd.append('visa', visa)

            await this.props.uploadPassportVisa(fd, passenger._id)
            this.setState({ isWait: false })
        } else {
            this.setState({ isWait: false })
        }
    }
    render() {
        let { isWait, passport, visa, passenger } = this.state
        let isDone = passport || visa
        return (
            <Fragment>
                <Form onSubmit={this.submitHandler} className='border p-2'>

                    <p className="m-0">
                        {passenger.firstName + ' ' + passenger.lastName}
                    </p>

                    <Form.Group className="custom-file">
                        <Form.Control
                            required
                            id="passport"
                            type="file"
                            className="custom-file-input"
                            onChange={this.fileUploadHandler}
                        />
                        <Form.Label htmlFor="passport" className='custom-file-label'>Choose Passport</Form.Label>
                    </Form.Group>
                    <Form.Group className="custom-file">
                        <Form.Control
                            required
                            id="visa"
                            type="file"
                            className="custom-file-input"
                            onChange={this.visaUploadHandler}
                        />
                        <Form.Label htmlFor="visa" className='custom-file-label'>Choose VISA</Form.Label>
                    </Form.Group>
                    <Button type="submit" variant="dark" disabled={!isDone} size="sm" block className='mt-2'>
                        {isWait ? `Please Wait...` : `Submit`}
                    </Button>
                </Form>
            </Fragment>
        )
    }
}

export default connect(null, { uploadPassportVisa })(PassportVisaForm)