import { SET_MESSAGE } from './types'
import Axios from 'axios'

// Data Store
export const getSlider = () => async dispatch => {
    return await Axios.get(`/api/sliders`)
        .then(res => {
            return res.data.data;
        })
        .catch(err => {
            console.error(err)
            dispatch({
                type: SET_MESSAGE,
                payload: {
                    message: err.response && err.response.data.message,
                    type: 'error',
                }
            })
            return false;
        })
}

// Data Store
export const storeData = data => async dispatch => {
    return await Axios.post(`/api/sliders/store`, data)
        .then(res => {
            dispatch({
                type: SET_MESSAGE,
                payload: {
                    message: res.data.message,
                }
            })
            return true;
        })
        .catch(err => {
            console.error(err)
            dispatch({
                type: SET_MESSAGE,
                payload: {
                    message: err.response && err.response.data.message,
                    type: 'error',
                }
            })
            return false;
        })
}


// Data Update
export const updateData = (data, id) => async dispatch => {
    return Axios.put(`/api/sliders/${id}`, data)
        .then(res => {
            dispatch({
                type: SET_MESSAGE,
                payload: {
                    message: res.data.message,
                }
            })
            return true;
        })
        .catch(err => {
            dispatch({
                type: SET_MESSAGE,
                payload: {
                    message: err.response && err.response.data.message,
                    type: 'error',
                }
            })
            return false;
        })
}

// Data Delete
export const deleteData = id => dispatch => {
    Axios.delete(`/api/sliders/${id}`)
        .then(res => {
            dispatch({
                type: SET_MESSAGE,
                payload: {
                    message: res.data.message,
                }
            })
        })
        .catch(err => {
            console.log(err.response)
            dispatch({
                type: SET_MESSAGE,
                payload: {
                    message: err.response && err.response.data.message,
                    type: 'error',
                }
            })
        })
}
