import React, { Component, Fragment } from 'react';
import Modal from 'react-bootstrap/Modal';
import { Button, Form } from 'react-bootstrap';
import { connect } from 'react-redux';
import { storeData, updateData } from '../../../store/actions/packageActions'

import ReactSummernote from 'react-summernote';
import 'react-summernote/dist/react-summernote.css'; // import styles
import $ from 'jquery';
import 'bootstrap/dist/css/bootstrap.css';
window.jQuery = $;
require('bootstrap');


class Edit extends Component {
    constructor(props) {
        super(props);
        this.state = {
            id: props.data._id || null,
            title: props.data.title || '',
            amount: props.data.amount || '',
            description: props.data.description || '',
            file: null,
            filePath: props.data.image || null,
            isWait: false
        }
    }
    changeHandler = event => {
        this.setState({
            [event.target.name]: event.target.value
        })
    }
    fileUploadHandler = event => {
        this.setState({
            file: event.target.files[0],
        });

        let reader = new FileReader()
        let _this = this
        reader.onload = function (r) {
            _this.setState({
                filePath: r.target.result,
            });
        }
        reader.readAsDataURL(event.target.files[0]);
    }
    submitHandler = async event => {
        event.preventDefault();
        this.setState({ isWait: true })
        let { id, title, amount, description, file } = this.state
        let fd = new FormData()
        fd.append('title', title)
        fd.append('amount', amount)
        fd.append('description', description)
        fd.append('file', file)

        let { actionIsDone, storeData, updateData } = this.props
        let response = false
        if (id) {
            response = await updateData(fd, id)
        } else {
            response = await storeData(fd)
        }
        if (response) actionIsDone()
        this.setState({ isWait: false })
    }
    render() {
        let { id, title, amount, description, filePath, isWait } = this.state
        let isDone = filePath
        return (
            <Fragment>
                <Modal show={this.props.isOpen} onHide={this.props.closeHandler}>
                    <Modal.Header closeButton>
                        <Modal.Title>{id ? `Edit Package information` : `Add Package`}</Modal.Title>
                    </Modal.Header>
                    <Modal.Body>
                        <Form onSubmit={this.submitHandler}>
                            <Form.Group>
                                <Form.Label>Title <span>*</span></Form.Label>
                                <Form.Control
                                    type="text"
                                    name="title"
                                    defaultValue={title}
                                    onChange={this.changeHandler}
                                    placeholder="Enter Title"
                                    required
                                />
                            </Form.Group>

                            <Form.Group>
                                <Form.Label>Amount <span>*</span></Form.Label>
                                <Form.Control
                                    type="text"
                                    name="amount"
                                    defaultValue={amount}
                                    onChange={this.changeHandler}
                                    placeholder="Enter Amount"
                                    required
                                />
                            </Form.Group>

                            <Form.Group>
                                <Form.Label>Description</Form.Label>
                                <ReactSummernote
                                    defaultValue={description}
                                    className='w-100'
                                    options={{
                                        lang: 'ru-RU',
                                        height: 120,
                                        dialogsInBody: true,
                                        toolbar: [
                                            ['style', ['style']],
                                            ['font', ['bold', 'underline', 'clear']],
                                            ['para', ['ul', 'ol', 'paragraph']],
                                            ['insert', ['link']],
                                        ],
                                    }}
                                    onChange={(description) => this.setState({ description })}
                                />
                            </Form.Group>

                            <Form.Group className="custom-file mb-4">
                                <Form.Control
                                    id="file"
                                    type="file"
                                    className="custom-file-input"
                                    onChange={this.fileUploadHandler}
                                />
                                <Form.Label htmlFor="file" className='custom-file-label'>Choose File</Form.Label>
                            </Form.Group>
                            {filePath && <img src={filePath} alt="UploadFile" width="100" />}

                            <Button type="submit" variant="dark" disabled={!isDone} size="lg" block>
                                {isWait ? `Please Wait...` : `Save`}
                            </Button>
                        </Form>
                    </Modal.Body>
                </Modal>
            </Fragment>
        )
    }
}
const mapStateToProps = state => ({
    common: state.common,
})
export default connect(mapStateToProps, { storeData, updateData })(Edit);