import { SET_MESSAGE, SET_BASIC_INFO } from './types'
import Axios from 'axios'

// Data Get
export const getBasicInfo = () => async dispatch => {
    return Axios.get(`/api/basic-info`)
        .then(res => {
            let basicInfo = res.data.data
            if (Object.keys(basicInfo).length !== 0) {
                localStorage.setItem('basicInfo', JSON.stringify(basicInfo))
            }
            dispatch({
                type: SET_BASIC_INFO,
                payload: {
                    airports: basicInfo.airports,
                    airlines: basicInfo.airlines,
                    countries: basicInfo.countries,
                    basicInfo: basicInfo.basicInfo,
                    usefulLink: basicInfo.usefulLink,
                    footerLogo: basicInfo.footerLogo,
                    sliders: basicInfo.sliders,
                    packages: basicInfo.packages,
                    services: basicInfo.services,
                }
            })
            return res.data.data.basicInfo;
        })
        .catch(err => {
            dispatch({
                type: SET_MESSAGE,
                payload: {
                    message: err.response && err.response.data.message,
                    type: 'error',
                }
            })
            return false;
        })
}

// Data Store
export const storeData = data => async dispatch => {
    return Axios.post(`/api/basic-info`, data)
        .then(res => {
            dispatch({
                type: SET_MESSAGE,
                payload: {
                    message: res.data.message,
                }
            })
            return true;
        })
        .catch(err => {
            dispatch({
                type: SET_MESSAGE,
                payload: {
                    message: err.response && err.response.data.message,
                    type: 'error',
                }
            })
            return false;
        })
}


// Data Update
export const updateData = (data, id) => dispatch => {
    Axios.put(`/api/basic-info/${id}`, data)
        .then(res => {
            dispatch({
                type: SET_MESSAGE,
                payload: {
                    message: res.data.message,
                }
            })
        })
        .catch(err => {
            dispatch({
                type: SET_MESSAGE,
                payload: {
                    message: err.response && err.response.data.message,
                    type: 'error',
                }
            })
        })
}
