import { SET_MESSAGE, AGENT_ACTION_STATUS } from './types'
import Axios from 'axios'

export const agentFinancialRecord = () => async dispatch => {
    return Axios.get(`/api/agents/financial/record`)
        .then(res => {
            return res.data.data
        })
        .catch(err => {
            dispatch({
                type: SET_MESSAGE,
                payload: {
                    message: err.response && err.response.data.message,
                    type: 'error',
                }
            })
        })
}
