import React, { Component, Fragment } from 'react'
import { connect } from 'react-redux'
import flightData from './data/response.json'
// import flightData from './data/one-way-response.json'
import FlightList from './FlightList'



class FlightResult extends Component {
  state = {
    lists: [],
  }
  static getDerivedStateFromProps(nextProps, prevState) {
    if (JSON.stringify(nextProps.flight.lists) === JSON.stringify(prevState.lists)) return null
    return {
      lists: nextProps.flight.lists,
    }
  }
  render() {
    let { lists } = this.state
    // lists = flightData.data.flightsList

    return (
      <Fragment>
        <section className="flight-result-area">
          <div className="flight-result-heading">
            <div className="row">
              <div className="col-lg-12 col-md-12">
                <div className="airline">Airline</div>
                <div className="departure">Departure</div>
                <div className="destination">Duration</div>
                <div className="arrival">Arrival</div>
                <div className="fare">Fare</div>
              </div>
            </div>
          </div>

          {lists.length > 0 && lists.map(item => <FlightList
            key={item.pricing['$'].Key}
            pricing={item}
            segment={item.airSegmentGroup}
            fareList={item.fareList}
            history={this.props.history}
            isBook={true}
          />)
          }
        </section>
      </Fragment>
    )
  }
}
const mapStateToProps = state => ({
  flight: state.flight
})
export default connect(mapStateToProps)(FlightResult)