import React, { Component, Fragment } from 'react';
import Modal from 'react-bootstrap/Modal';
import { Button, Form } from 'react-bootstrap';
import { connect } from 'react-redux';
import { storeData } from '../../../store/actions/accountActions'


class NewEntry extends Component {
    state = {
        agentId: null,
        amount: 0,
        comment: '',
        file: null,
        filePath: null,
        isWait: false
    }
    changeHandler = event => {
        this.setState({
            [event.target.name]: event.target.value
        })
    }
    fileUploadHandler = event => {
        this.setState({
            file: event.target.files[0],
        });

        let reader = new FileReader()
        let _this = this
        reader.onload = function (r) {
            _this.setState({
                filePath: r.target.result,
            });
        }
        reader.readAsDataURL(event.target.files[0]);
    }
    submitHandler = async event => {
        event.preventDefault();
        this.setState({ isWait: true })
        let { amount, agentId, comment, file } = this.state
        let fd = new FormData()
        fd.append('agentId', agentId)
        fd.append('amount', amount)
        fd.append('comment', comment)
        fd.append('file', file)

        let { actionIsDone, storeData } = this.props
        let response = await storeData(fd)
        if (response) actionIsDone()
        this.setState({ isWait: false })
    }
    render() {
        let { id, amount, comment, filePath, isWait, agentId } = this.state
        let isDone = amount
        return (
            <Fragment>
                <Modal show={this.props.isOpen} onHide={this.props.closeHandler}>
                    <Modal.Header closeButton>
                        <Modal.Title>{id ? `Edit Deposit information` : `Add Deposit`}</Modal.Title>
                    </Modal.Header>
                    <Modal.Body>
                        <Form onSubmit={this.submitHandler}>
                            <Form.Group>
                                <Form.Label>Agent ID <span>*</span></Form.Label>
                                <Form.Control
                                    type="text"
                                    name="agentId"
                                    defaultValue={agentId}
                                    onChange={this.changeHandler}
                                    placeholder="Enter Agent Id"
                                    required
                                />
                            </Form.Group>

                            <Form.Group>
                                <Form.Label>Amount <span>*</span></Form.Label>
                                <Form.Control
                                    type="number"
                                    name="amount"
                                    defaultValue={amount}
                                    onChange={this.changeHandler}
                                    placeholder="Enter Amount"
                                    required
                                />
                            </Form.Group>

                            <Form.Group>
                                <Form.Label>Note</Form.Label>
                                <Form.Control
                                    type="text"
                                    name="comment"
                                    defaultValue={comment}
                                    onChange={this.changeHandler}
                                    placeholder="Note"
                                />
                            </Form.Group>

                            <Form.Group className="custom-file mb-4">
                                <Form.Control
                                    id="file"
                                    type="file"
                                    className="custom-file-input"
                                    onChange={this.fileUploadHandler}
                                />
                                <Form.Label htmlFor="file" className='custom-file-label'>Choose File</Form.Label>
                            </Form.Group>
                            {filePath && <img src={filePath} alt="UploadFile" width="100" />}

                            <Button type="submit" variant="dark" disabled={!isDone} size="lg" block>
                                {isWait ? `Please Wait...` : `Save`}
                            </Button>
                        </Form>
                    </Modal.Body>
                </Modal>
            </Fragment>
        )
    }
}
const mapStateToProps = state => ({
    common: state.common,
})
export default connect(mapStateToProps, { storeData })(NewEntry);