import React, { Component, Fragment } from 'react'
import { connect } from 'react-redux'

import PersonArea from './PersonArea';
import FromToArea from './FromToArea';
import RouteArea from './RouteArea';
import SubmitBtnArea from './SubmitBtnArea';
import MultiCity from './MultiCity';
import { lowFareSearchReq } from '../../../store/actions/flightActions';
import LoadingImg from '../../layout/LoadingImg';

class SearchForm extends Component {
    state = {
        origin: '',
        destination: '',
        departureDate: null,
        returnDate: null,
        adult: 1,
        child: 0,
        infant: 0,
        cabinClass: 'Economy',
        route: 'ONE_WAY',
        origin2: '',
        destination2: '',
        departureDate2: null,
        origin3: '',
        destination3: '',
        departureDate3: null,
        origin4: '',
        destination4: '',
        departureDate4: null,
        numMultiCity: 1,
        preferAir: 'All',
        isWait: false,
        isOpen: false
    }
    componentDidMount() {
        if (Object.keys(this.props.searchData).length !== 0) {
            let { searchData } = this.props
            this.setState({
                ...searchData
            })
        }
    }
    stateHandler = (name, value) => {
        if (name === 'route' && value === 'MULTI_CITY') {
            this.setState({
                origin2: this.state.destination
            })
        }
        this.setState({
            [name]: value
        })
    }
    submitHandler = async event => {
        event.preventDefault()
        let { history, auth, lowFareSearchReq } = this.props
        if (!auth.isAuth) history.push('/login')

        let { adult, child, infant } = this.state
        if (Number(adult) + Number(child) + Number(infant) > 9) {
            alert('Maximum 9')
            return false
        }
        this.setState({ isWait: true })
        let response = await lowFareSearchReq(this.state)
        if (response) {
            history.push('/flight')
        }
        this.setState({ isWait: false })
    }
    render() {
        let { route, numMultiCity, isOpen, adult, child, infant, cabinClass, isWait } = this.state
        return (
            <Fragment>
                {isWait && <LoadingImg isFlight={true} />}
                <form onSubmit={this.submitHandler}>

                    {/* ********************Route Area************************** */}
                    <RouteArea
                        data={this.state}
                        stateHandler={this.stateHandler.bind(this)}
                    />
                    <div className="e-ticketing-form-wrapper">

                        {/* ********************From To Area************************** */}
                        <FromToArea
                            data={this.state}
                            stateHandler={this.stateHandler.bind(this)}
                        />

                        {/* ********************Multi City************************** */}
                        {route === "MULTI_CITY" && numMultiCity > 0 &&
                            <div className="row">
                                <div className="col-lg-6">
                                    {numMultiCity < 3 && <a className="text-white" href="#blank" onClick={() => this.setState({ numMultiCity: numMultiCity + 1 })}>+Add New</a>}
                                </div>
                                <div className="col-lg-6">
                                    {numMultiCity > 1 && <a className="text-white" href="#blank" onClick={() => this.setState({ numMultiCity: numMultiCity - 1 })}>-Remove</a>}
                                </div>
                            </div>
                        }

                        {route === "MULTI_CITY" &&
                            <MultiCity
                                stateHandler={this.stateHandler.bind(this)}
                                data={this.state}
                                numMultiCity={1}
                            />}

                        {numMultiCity > 1 &&
                            <MultiCity
                                stateHandler={this.stateHandler.bind(this)}
                                data={this.state}
                                numMultiCity={2}
                            />}
                        {numMultiCity > 2 &&
                            <MultiCity
                                stateHandler={this.stateHandler.bind(this)}
                                data={this.state}
                                numMultiCity={3}
                            />}


                        {/************************Person Adult Area******************/}
                        <div className="row">
                            <div className='col-lg-6'>
                                <h6 onClick={() => this.setState({ isOpen: !isOpen })} className='passenger-select-section'>
                                    {cabinClass}-{Number(adult) + Number(child) + Number(infant)} Person <i className='fa fa-caret-down' />
                                </h6>
                                {isOpen && <PersonArea
                                    menuHandler={() => this.setState({ isOpen: false })}
                                    data={this.state}
                                    stateHandler={this.stateHandler.bind(this)}
                                />}
                            </div>

                            {/***************************More Option Area********************/}
                            {/* <MoreOptionArea /> */}


                            {/***************************Submit Search Button Area********************/}
                            <SubmitBtnArea
                                data={this.state}
                            />
                        </div>
                    </div>

                </form>
            </Fragment>
        )
    }
}
const mapStateToProps = state => ({
    flight: state.flight,
    auth: state.auth
})
export default connect(mapStateToProps, { lowFareSearchReq })(SearchForm)