import React, { Component, Fragment } from 'react';
import { connect } from 'react-redux';
import { Link } from 'react-router-dom';


class Package extends Component {
    state = {
        data: []
    }
    static getDerivedStateFromProps(nextProps, prevState) {
        if (nextProps.common.packages === prevState.data) return null
        return {
            data: nextProps.common.packages,
        }
    }
    render() {
        let { data } = this.state
        return (
            <Fragment>
                <section className="ticket-package-area section-padding">
                    <div className="container">
                        <div className="row">
                            <div className="col-lg-8 col-md-8 offset-lg-2 offset-md-2">
                                <div className="section-title text-center section-title-padding">
                                    <h2>Our Special Packages</h2>
                                </div>
                            </div>
                        </div>

                        <div className="row">
                            {data.length > 0 && data.map(item =>
                                <div className="col-lg-3 col-md-6" key={item._id}>
                                    <div className="package-wrapper-content">
                                        <div className="package-heading-content">
                                            <Link to={`/package/detail/${item.id}`}>
                                                <div className="package-thumbnail">
                                                    <img src={item.image} alt="packagePhoto" />
                                                </div>
                                            </Link>
                                            <div className="package-meta">
                                                <div className="package-price">
                                                    <span className="price">From  {item.amount}</span>
                                                </div>
                                                <div className="package-rating">
                                                    <ul>
                                                        <li><i className="fa fa-star"></i></li>
                                                        <li><i className="fa fa-star"></i></li>
                                                        <li><i className="fa fa-star"></i></li>
                                                        <li><i className="fa fa-star"></i></li>
                                                        <li><i className="fa fa-star"></i></li>
                                                    </ul>
                                                </div>
                                            </div>
                                        </div>
                                        <h2 className="package-title"><Link to={`/package/detail/${item._id}`}>{item.title}</Link></h2>
                                        {/* <div className="package-publish-date"><span>{dateTime(item.createdAt)}</span></div> */}
                                    </div>
                                </div>)}
                        </div>
                    </div>
                </section>
            </Fragment>
        )
    }
}
const mapStateToProps = state => ({
    common: state.common
})
export default connect(mapStateToProps)(Package)