import React, { Component, Fragment } from 'react';
import Modal from 'react-bootstrap/Modal';
import { Button, Form } from 'react-bootstrap';
import { connect } from 'react-redux';
import { storeData, updateData } from '../../../store/actions/userActions'


class Edit extends Component {
    constructor(props) {
        super(props);
        this.state = {
            id: props.data._id || null,
            agentId: props.data.agentId || null,
            name: props.data.name || null,
            contact: props.data.contact || null,
            email: props.data.email || null,
            address: props.data.address || null,
            password: '',
            confirmPassword: '',
            type: props.data.type || 1,
            status: props.data.status || 1,
            isAdmin: props.data.isAdmin || false,
            isWait: false
        }
    }
    changeHandler = event => {
        this.setState({
            [event.target.name]: event.target.value
        })
    }
    submitHandler = event => {
        event.preventDefault()
        this.setState({ isWait: true })
        let { updateData, storeData, actionIsDone } = this.props
        let response = false
        let data = this.state
        let { id, type } = this.state
        if (Number(type) !== 2 && id === null) {
            data.isAdmin = true
        }
        if (id) {
            if (Number(type) === 2 && this.props.data.agentId === null) {
                data.agentId = id
            }
            updateData(data, id)
        } else {
            storeData(data)
        }
        if (response) actionIsDone()
        this.setState({ isWait: false })
    }
    render() {
        let { id, name, contact, email, address, status, type, password, confirmPassword, isWait } = this.state
        let isDone = name && contact && email && type !== "" && status !== ""

        return (
            <Fragment>
                <Modal show={this.props.isOpen} onHide={this.props.closeHandler}>
                    <Modal.Header closeButton>
                        <Modal.Title>{id ? `Edit user information` : `Add New User`}</Modal.Title>
                    </Modal.Header>
                    <Modal.Body>
                        <Form onSubmit={this.submitHandler}>
                            <Form.Group>
                                <Form.Label>Full Name <span>*</span></Form.Label>
                                <Form.Control
                                    type="text"
                                    name="name"
                                    defaultValue={name}
                                    onChange={this.changeHandler}
                                    placeholder="Enter Full Name"
                                />
                            </Form.Group>

                            <Form.Group>
                                <Form.Label>Contact Number <span>*</span></Form.Label>
                                <Form.Control
                                    type="number"
                                    name="contact"
                                    defaultValue={contact}
                                    onChange={this.changeHandler}
                                    placeholder="Enter Contact Number"
                                />
                            </Form.Group>

                            <Form.Group>
                                <Form.Label>Email <span>*</span></Form.Label>
                                <Form.Control
                                    type="email"
                                    name="email"
                                    defaultValue={email}
                                    onChange={this.changeHandler}
                                    placeholder="Enter Email"
                                />
                            </Form.Group>

                            {!id &&
                                <Form.Group>
                                    <Form.Label>Password <span>*</span></Form.Label>
                                    <Form.Control
                                        type="password"
                                        name="password"
                                        value={password}
                                        onChange={this.changeHandler}
                                        className="form-control"
                                        placeholder="Enter Password"
                                    />
                                </Form.Group>}

                            {!id &&
                                <Form.Group>
                                    <Form.Label>Confirm Password <span>*</span></Form.Label>
                                    <Form.Control
                                        type="password"
                                        name="confirmPassword"
                                        value={confirmPassword}
                                        onChange={this.changeHandler}
                                        className="form-control"
                                        placeholder="Enter Confirm Password"
                                    />
                                </Form.Group>}

                            <Form.Group>
                                <Form.Label>Address</Form.Label>
                                <Form.Control
                                    type="text"
                                    name="address"
                                    defaultValue={address}
                                    onChange={this.changeHandler}
                                    placeholder="Enter Address"
                                />
                            </Form.Group>

                            <Form.Group>
                                <Form.Label>Type <span>*</span></Form.Label>
                                <Form.Control
                                    as="select"
                                    name="type"
                                    defaultValue={type}
                                    onChange={this.changeHandler}
                                >
                                    <option value="1">Customer</option>
                                    <option value="2">Agent</option>
                                    <option value="3">Moderators</option>
                                </Form.Control>
                            </Form.Group>

                            <Form.Group>
                                <Form.Label>Status <span>*</span></Form.Label>
                                <Form.Control
                                    as="select"
                                    name="status"
                                    defaultValue={status}
                                    onChange={this.changeHandler}
                                >
                                    <option value="0">Pending</option>
                                    <option value="1">Active</option>
                                    <option value="2">Blocked</option>
                                </Form.Control>
                            </Form.Group>

                            <Button type="submit" variant="dark" disabled={!isDone} size="lg" block>
                                {isWait ? `Please Wait...` : `Save`}
                            </Button>
                        </Form>
                    </Modal.Body>
                </Modal>
            </Fragment>
        )
    }
}
const mapStateToProps = state => ({
    user: state.user,
})
export default connect(mapStateToProps, { storeData, updateData })(Edit);