import { SET_MESSAGE } from './types'
import Axios from 'axios'
import refreshToken from '../../utils/refreshToken'

// Data Store
export const storeData = data => async dispatch => {
    return await Axios.post(`/api/deposits/store`, data)
        .then(res => {
            dispatch({
                type: SET_MESSAGE,
                payload: {
                    message: res.data.message,
                }
            })
            return true;
        })
        .catch(err => {
            console.error(err)
            dispatch({
                type: SET_MESSAGE,
                payload: {
                    message: err.response && err.response.data.message,
                    type: 'error',
                }
            })
            return false;
        })
}


// Data Update
export const updateData = (data, id) => async dispatch => {
    return Axios.put(`/api/deposits/${id}`, data)
        .then(res => {
            dispatch({
                type: SET_MESSAGE,
                payload: {
                    message: res.data.message,
                }
            })
            return true;
        })
        .catch(err => {
            dispatch({
                type: SET_MESSAGE,
                payload: {
                    message: err.response && err.response.data.message,
                    type: 'error',
                }
            })
            return false;
        })
}

// Data Delete
export const deleteData = id => dispatch => {
    Axios.delete(`/api/deposits/${id}`)
        .then(res => {
            dispatch({
                type: SET_MESSAGE,
                payload: {
                    message: res.data.message,
                }
            })
        })
        .catch(err => {
            console.log(err.response)
            dispatch({
                type: SET_MESSAGE,
                payload: {
                    message: err.response && err.response.data.message,
                    type: 'error',
                }
            })
        })
}

// Data Approve
export const approveData = id => async dispatch => {
    return Axios.put(`/api/deposits/approve/${id}`)
        .then(res => {
            dispatch({
                type: SET_MESSAGE,
                payload: {
                    message: res.data.message,
                }
            })
            refreshToken()
            return true;
        })
        .catch(err => {
            dispatch({
                type: SET_MESSAGE,
                payload: {
                    message: err.response && err.response.data.message,
                    type: 'error',
                }
            })
            return false;
        })
}

