import React, { Component, Fragment } from 'react';
import Modal from 'react-bootstrap/Modal';
import { Button, Form } from 'react-bootstrap';
import { connect } from 'react-redux';
import { storeData, updateData } from '../../../store/actions/sliderActions'


class Edit extends Component {
    constructor(props) {
        super(props);
        this.state = {
            id: props.data._id || null,
            serial: props.data.serial || 1,
            type: props.data.type || 0,
            file: null,
            filePath: props.data.image || null,
            isWait: false
        }
    }
    changeHandler = event => {
        this.setState({
            [event.target.name]: event.target.value
        })
    }
    fileUploadHandler = event => {
        this.setState({
            file: event.target.files[0],
        });

        let reader = new FileReader()
        let _this = this
        reader.onload = function (r) {
            _this.setState({
                filePath: r.target.result,
            });
        }
        reader.readAsDataURL(event.target.files[0]);
    }
    submitHandler = async event => {
        event.preventDefault();
        this.setState({ isWait: true })
        let { id, serial, type, file } = this.state
        let fd = new FormData()
        fd.append('serial', serial)
        fd.append('file', file)
        fd.append('type', type)

        let { actionIsDone, storeData, updateData } = this.props
        let response = false
        if (id) {
            response = await updateData(fd, id)
        } else {
            response = await storeData(fd)
        }
        if (response) actionIsDone()
        this.setState({ isWait: false })
    }
    render() {
        let { id, serial, type, filePath, isWait } = this.state
        let isDone = filePath
        return (
            <Fragment>
                <Modal show={this.props.isOpen} onHide={this.props.closeHandler}>
                    <Modal.Header closeButton>
                        <Modal.Title>{id ? `Edit Slider information` : `Add Slider`}</Modal.Title>
                    </Modal.Header>
                    <Modal.Body>
                        <Form onSubmit={this.submitHandler}>
                            <Form.Group>
                                <Form.Label>Serial <span>*</span></Form.Label>
                                <Form.Control
                                    type="number"
                                    name="serial"
                                    defaultValue={serial}
                                    onChange={this.changeHandler}
                                    placeholder="Enter Serial"
                                    required
                                />
                            </Form.Group>

                            <Form.Group>
                                <Form.Label>Type <span>*</span></Form.Label>
                                <Form.Control
                                    as="select"
                                    name="type"
                                    value={type}
                                    onChange={this.changeHandler}
                                >
                                    <option value="0">All</option>
                                    <option value="1">B2C</option>
                                    <option value="2">B2B</option>
                                    <option value="3">Admin</option>
                                </Form.Control>
                            </Form.Group>

                            <Form.Group className="custom-file mb-4">
                                <Form.Control
                                    id="file"
                                    type="file"
                                    className="custom-file-input"
                                    onChange={this.fileUploadHandler}
                                />
                                <Form.Label htmlFor="file" className='custom-file-label'>Choose File</Form.Label>
                            </Form.Group>
                            {filePath && <img src={filePath} alt="UploadFile" width="100" />}

                            <Button type="submit" variant="dark" disabled={!isDone} size="lg" block>
                                {isWait ? `Please Wait...` : `Save`}
                            </Button>
                        </Form>
                    </Modal.Body>
                </Modal>
            </Fragment>
        )
    }
}
const mapStateToProps = state => ({
    common: state.common,
})
export default connect(mapStateToProps, { storeData, updateData })(Edit);