import React, { Component, Fragment } from 'react';
import Modal from 'react-bootstrap/Modal';
import { Button, Form } from 'react-bootstrap';
import { connect } from 'react-redux';
import { updateSiteLogo } from './../../../store/actions/profileActions';


class EditPhoto extends Component {
    constructor(props) {
        super(props);
        this.state = {
            isWait: false,
            file: null,
            filePath: props.file || null,
        }
    }
    fileUploadHandler = event => {
        this.setState({
            file: event.target.files[0],
        });

        let reader = new FileReader()
        let _this = this
        reader.onload = function (r) {
            _this.setState({
                filePath: r.target.result,
            });
        }
        reader.readAsDataURL(event.target.files[0]);
    }
    submitHandler = async event => {
        event.preventDefault()
        this.setState({ isWait: true })
        let { file } = this.state
        let { updateSiteLogo, closeHandler } = this.props
        let fd = new FormData()
        fd.append('file', file)
        let response = await updateSiteLogo(fd)
        if (response) closeHandler()
        this.setState({ isWait: true })
    }
    render() {
        let { filePath, isWait } = this.state
        let isDone = filePath
        return (
            <Fragment>
                <Modal show={this.props.isOpen} onHide={this.props.closeHandler}>
                    <Modal.Header closeButton>
                        <Modal.Title>Update Site Logo</Modal.Title>
                    </Modal.Header>
                    <Modal.Body>
                        <Form onSubmit={this.submitHandler}>

                            <Form.Group className="custom-file mb-4">
                                <Form.Control
                                    id="file"
                                    type="file"
                                    className="custom-file-input"
                                    onChange={this.fileUploadHandler}
                                />
                                <Form.Label htmlFor="file" className='custom-file-label'>Choose File</Form.Label>
                            </Form.Group>
                            {filePath && <img src={filePath} alt="UploadFile" width="100" />}

                            <Button type="submit" variant="dark" disabled={!isDone} size="lg" block>
                                {isWait ? `Please Wait...` : `Save`}
                            </Button>
                        </Form>
                    </Modal.Body>
                </Modal>
            </Fragment>
        )
    }
}
const mapStateToProps = state => ({
    user: state.user,
})
export default connect(mapStateToProps, { updateSiteLogo })(EditPhoto);