import { TARGET_BRANCH } from '../../store/actions/types'

const LowFareSearchReqLog = data => {
    let { origin, destination, departureDate, returnDate, adult, child, infant, route, numMultiCity, origin2, destination2, departureDate2, origin3, destination3, departureDate3, origin4, destination4, departureDate4, cabinClass, preferAir } = data


    let SearchPassenger = `<com:SearchPassenger Code="ADT" xmlns:com="http://www.travelport.com/schema/common_v47_0"/>`;

    for (let i = 1; i < adult; i++) {
        SearchPassenger += `
        <com:SearchPassenger Code="ADT" xmlns:com="http://www.travelport.com/schema/common_v47_0"/>`;
    }

    if (child > 0) {
        for (let i = 0; i < child; i++) {
            SearchPassenger += `
        <com:SearchPassenger Code="CNN" Age="7" xmlns:com="http://www.travelport.com/schema/common_v47_0"/>`;
        }
    }

    if (infant > 0) {
        for (let i = 0; i < infant; i++) {
            SearchPassenger += `
        <com:SearchPassenger Code="INF" Age="1" xmlns:com="http://www.travelport.com/schema/common_v47_0"/>`;
        }
    }


    let SearchAirLeg = `<air:SearchAirLeg>
                <air:SearchOrigin>
                    <com:CityOrAirport Code="${origin}" PreferCity="true"/>
                </air:SearchOrigin>
                <air:SearchDestination>
                    <com:CityOrAirport Code="${destination}" PreferCity="true"/>
                </air:SearchDestination>
                <air:SearchDepTime PreferredTime="${departureDate}"/>
                <air:AirLegModifiers></air:AirLegModifiers>
            </air:SearchAirLeg>`;

    if (route === 'RETURN_FLIGHT') {
        SearchAirLeg += `<air:SearchAirLeg>
                <air:SearchOrigin>
                    <com:CityOrAirport Code="${destination}" PreferCity="true"/>
                </air:SearchOrigin>
                <air:SearchDestination>
                    <com:CityOrAirport Code="${origin}" PreferCity="true"/>
                </air:SearchDestination>
                <air:SearchDepTime PreferredTime="${returnDate}"/>
                <air:AirLegModifiers></air:AirLegModifiers>
            </air:SearchAirLeg>`
    } else if (route === 'MULTI_CITY') {
        if (numMultiCity > 0) {
            SearchAirLeg += `<air:SearchAirLeg>
                <air:SearchOrigin>
                    <com:CityOrAirport Code="${origin2}" PreferCity="true"/>
                </air:SearchOrigin>
                <air:SearchDestination>
                    <com:CityOrAirport Code="${destination2}" PreferCity="true"/>
                </air:SearchDestination>
                <air:SearchDepTime PreferredTime="${departureDate2}"/>
                <air:AirLegModifiers></air:AirLegModifiers>
                </air:SearchAirLeg>`
        }
        if (numMultiCity > 1) {
            SearchAirLeg += `<air:SearchAirLeg>
                <air:SearchOrigin>
                    <com:CityOrAirport Code="${origin3}" PreferCity="true"/>
                </air:SearchOrigin>
                <air:SearchDestination>
                    <com:CityOrAirport Code="${destination3}" PreferCity="true"/>
                </air:SearchDestination>
                <air:SearchDepTime PreferredTime="${departureDate3}"/>
                <air:AirLegModifiers></air:AirLegModifiers>
                </air:SearchAirLeg>`
        }
        if (numMultiCity > 2) {
            SearchAirLeg += `<air:SearchAirLeg>
                <air:SearchOrigin>
                    <com:CityOrAirport Code="${origin4}" PreferCity="true"/>
                </air:SearchOrigin>
                <air:SearchDestination>
                    <com:CityOrAirport Code="${destination4}" PreferCity="true"/>
                </air:SearchDestination>
                <air:SearchDepTime PreferredTime="${departureDate4}"/>
                <air:AirLegModifiers></air:AirLegModifiers>
                </air:SearchAirLeg>`
        }
    }

    let cabinClassLog = ``
    if (cabinClass !== 'All') {
        cabinClassLog = `<air:PreferredCabins>
                    <com:CabinClass xmlns="http://www.travelport.com/schema/common_v47_0" Type="${cabinClass}"/>
                </air:PreferredCabins>`
    }

    let PreferredCarriers = ``
    if (preferAir !== 'All') {
        PreferredCarriers += `<air:PreferredCarriers>
                    <com:Carrier xmlns:com="http://www.travelport.com/schema/common_v47_0" Code="${preferAir}" />
                </air:PreferredCarriers>`
    }

    let schema = `<soap:Envelope
        xmlns:soap="http://schemas.xmlsoap.org/soap/envelope/">
    <soap:Body>
        <air:LowFareSearchReq
        AuthorizedBy="user" TraceId="test-request" TargetBranch="${TARGET_BRANCH}"
                SolutionResult="true"
                ReturnUpsellFare="true"
                xmlns:air="http://www.travelport.com/schema/air_v47_0"
                xmlns:com="http://www.travelport.com/schema/common_v47_0"
        >
            <com:BillingPointOfSaleInfo OriginApplication="ST"/>
            ${SearchAirLeg}
            <air:AirSearchModifiers>
                <air:PreferredProviders>
                    <com:Provider Code="1G"
                                  xmlns:com="http://www.travelport.com/schema/common_v47_0"/>
                </air:PreferredProviders>
                ${PreferredCarriers}
                ${cabinClassLog}
            </air:AirSearchModifiers>
            ${SearchPassenger}
        </air:LowFareSearchReq>
    </soap:Body>
</soap:Envelope>`
    return schema
}
export default LowFareSearchReqLog