import React, { Component, Fragment } from 'react'
import { connect } from 'react-redux';
import { lowFareSearchReq } from '../../store/actions/flightActions';
import OriginDestinationForm from './OriginDestinationForm';
import DatePicker from "react-datepicker";
import "react-datepicker/dist/react-datepicker.css";
import dateFormat from 'dateformat';
import PersonArea from '../home/search/PersonArea';
import { textLimit } from '../../utils/helper';
import LoadingImg from '../layout/LoadingImg';


class ModifySearch extends Component {
    state = {
        origin: '',
        destination: '',
        departureDate: null,
        returnDate: null,
        adult: 1,
        child: 0,
        infant: 0,
        route: 'ONE_WAY',
        origin2: '',
        destination2: '',
        departureDate2: null,
        origin3: '',
        destination3: '',
        departureDate3: null,
        origin4: '',
        destination4: '',
        departureDate4: null,
        numMultiCity: 1,
        isWait: false,
        isCollapse: false,
        cabinClass: 'Economy',
        isOpen: false,
    }
    componentDidMount() {
        if (Object.keys(this.props.searchData).length !== 0) {
            let { searchData } = this.props
            this.setState({
                ...searchData
            })
        }
    }
    stateHandler = (name, value) => {
        this.setState({
            [name]: value
        })
    }
    changeHandler = event => {
        this.setState({
            [event.target.name]: event.target.value
        })
    }
    submitHandler = async event => {
        event.preventDefault()
        let { adult, child, infant } = this.state
        if (Number(adult) + Number(child) + Number(infant) > 9) {
            alert('Maximum 9')
            return false
        }
        this.setState({ isWait: true })
        await this.props.lowFareSearchReq(this.state)
        this.setState({ isWait: false })
    }
    nextDateHandler = async () => {
        this.setState({ isWait: true })
        let { departureDate, origin, destination, adult, child, infant } = this.state

        if (Number(adult) + Number(child) + Number(infant) > 9) {
            alert('Maximum 9')
            return false
        }

        let date = new Date(departureDate);
        departureDate = dateFormat(new Date(date.setDate(date.getDate() + 1)), "yyyy-mm-dd");
        this.setState({ departureDate })
        if (origin && destination && departureDate) {
            await this.props.lowFareSearchReq({ ...this.state, departureDate })
        }
        this.setState({ isWait: false })
    }
    previousDateHandler = async () => {
        this.setState({ isWait: true })
        let { departureDate, origin, destination, adult, child, infant } = this.state

        if (Number(adult) + Number(child) + Number(infant) > 9) {
            alert('Maximum 9')
            return false
        }

        let date = new Date(departureDate);
        departureDate = dateFormat(new Date(date.setDate(date.getDate() - 1)), "yyyy-mm-dd");
        this.setState({ departureDate })
        if (origin && destination && departureDate) {
            await this.props.lowFareSearchReq({ ...this.state, departureDate })
        }
        this.setState({ isWait: false })
    }
    render() {
        let { origin, destination, departureDate, returnDate, route, adult, child, infant, isWait, cabinClass, isOpen } = this.state
        let isDone = origin && destination && departureDate && !isWait
        return (
            <Fragment>
                {isWait && <LoadingImg />}
                <div className="modify-search-form-area">
                    <div className='px-2'>
                        <div className="row">
                            <div className="col-lg-2 col-md-12">
                                <label htmlFor="oneWay" className="eTicketLabel">
                                    <input
                                        type="radio"
                                        id="oneWay"
                                        name='route'
                                        value="ONE_WAY"
                                        checked={route === 'ONE_WAY' ? true : false}
                                        onChange={() => this.setState({ 'route': 'ONE_WAY' })}
                                    /> ONE-WAY
                                </label>
                            </div>
                            <div className="col-lg-2 col-md-12">
                                <label htmlFor="returnFlight" className="eTicketLabel">
                                    <input
                                        type="radio"
                                        id="returnFlight"
                                        name='route'
                                        value="RETURN_FLIGHT"
                                        checked={route === 'RETURN_FLIGHT' ? true : false}
                                        onChange={() => this.setState({ 'route': 'RETURN_FLIGHT' })}
                                    /> RETURN-FLIGHT
                                </label>
                            </div>
                        </div>
                    </div>


                    <div className="row">
                        <div className="col-lg-12">
                            <div className="origin">
                                <span>FROM</span>
                                <OriginDestinationForm
                                    filedName='origin'
                                    data={origin}
                                    stateHandler={this.stateHandler.bind(this)}
                                />
                            </div>

                            <div className="destination">
                                <span>TO</span>
                                <OriginDestinationForm
                                    filedName='destination'
                                    data={destination}
                                    stateHandler={this.stateHandler.bind(this)}
                                />
                            </div>

                            <div className="departure">
                                <span>DEPARTURE</span>
                                <DatePicker
                                    className="e-ticketing-input-field"
                                    selected={departureDate && new Date(departureDate)}
                                    onChange={(date) => this.setState({ departureDate: dateFormat(date, "yyyy-mm-dd") })}
                                    placeholder="Enter Departure Date"
                                    minDate={new Date()}
                                    dateFormat="dd MMM yyyy"
                                />
                            </div>

                            <div className="return">
                                <span>RETURN</span>
                                <DatePicker
                                    className="e-ticketing-input-field"
                                    selected={returnDate && new Date(returnDate)}
                                    onChange={(date) => this.setState({ returnDate: dateFormat(date, "yyyy-mm-dd") })}
                                    disabled={route !== "RETURN_FLIGHT"}
                                    placeholder="Enter Return Date"
                                    minDate={departureDate && new Date(departureDate)}
                                    dateFormat="dd MMM yyyy"
                                />
                            </div>

                            <div className="passenger-area">
                                <h6 onClick={() => this.setState({ isOpen: !isOpen })} className='passenger-select-section'>
                                    {textLimit(cabinClass, 5)}-{Number(adult) + Number(child) + Number(infant)} Person <i className='fa fa-caret-down' />
                                </h6>
                                {isOpen && <PersonArea
                                    menuHandler={() => this.setState({ isOpen: false })}
                                    data={this.state}
                                    stateHandler={this.stateHandler.bind(this)}
                                />}
                            </div>
                            <div className="modify-search-area">
                                <span>&nbsp;</span>
                                <button
                                    className="modify-search-button"
                                    onClick={isDone ? this.submitHandler : null}
                                >SEARCH</button>
                            </div>
                            <div className="day-change-arrow">
                                <div>
                                    <i className='fa fa-arrow-left fa-2x' onClick={this.previousDateHandler} />
                                    <i className='fa fa-arrow-right fa-2x' onClick={this.nextDateHandler} />
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </Fragment>
        )
    }
}
const mapStateToProps = state => ({
    searchData: state.flight.searchData,
    lowFareSearchStatus: state.flight.lowFareSearchStatus
})
export default connect(mapStateToProps, { lowFareSearchReq })(ModifySearch)