import { SET_MESSAGE, LOW_FARE_SEARCH, SET_AIR_PRICING } from './types'
import Axios from 'axios'
import LowFareSearchReqLog from '../../utils/schema/LowFareSearchReqLog';
import AirPriceReqLog from '../../utils/schema/AirPriceReqLog';
import AirCreateReservationReqLog from '../../utils/schema/AirCreateReservation';
import UniversalRecordCancelReqLog from '../../utils/schema/UniversalRecordCancelReq';


/* Low fare search Request */
export const lowFareSearchReq = data => async dispatch => {
    localStorage.removeItem('pricing')
    dispatch({
        type: LOW_FARE_SEARCH,
        payload: {
            lists: [],
        }
    })
    let schema = LowFareSearchReqLog(data)
    console.log(schema)
    return Axios.post(`/api/flights/low-fare-search`, { schema, searchData: data })
        .then(res => {
            if (!res.data.data) {
                dispatch({
                    type: SET_MESSAGE,
                    payload: {
                        message: res.data.message,
                        type: 'error'
                    }
                })
            } else {
                console.log(res.data.data.flightsList)
                dispatch({
                    type: LOW_FARE_SEARCH,
                    payload: {
                        lists: res.data.data !== null ? res.data.data.flightsList : [],
                        searchData: data,
                    }
                })
            }
            return true
        })
        .catch(error => {
            dispatch({
                type: SET_MESSAGE,
                payload: {
                    message: error.response !== undefined && error.response.data.message,
                    status: 'error'
                }
            })
            return false
        })
}


/* Air pricing Request */
export const airPricingReq = (segmentInfo, AirSegmentList) => async dispatch => {
    let schema = AirPriceReqLog(segmentInfo['pricing'], AirSegmentList)
    console.log(schema)
    return Axios.post(`/api/flights/air-pricing`, { schema })
        .then(res => {
            dispatch({
                type: SET_AIR_PRICING,
                payload: {
                    pricing: res.data.data !== null ? res.data.data : {},
                    segmentInfo: segmentInfo,
                }
            })
            localStorage.setItem('pricingInfo', JSON.stringify({ pricing: res.data.data, segmentInfo }))
            return res.data.data !== null || false
        })
        .catch(error => {
            dispatch({
                type: SET_MESSAGE,
                payload: {
                    message: error.response !== undefined && error.response.data.message,
                    type: 'error'
                }
            })
            return false
        })
}


/* Air Create Reservation Request */
export const airCreateReservationReq = (AirPriceRsp, travelerInfo) => async dispatch => {
    let schema = AirCreateReservationReqLog(AirPriceRsp, travelerInfo)
    console.log(schema)
    let pricingInfo = localStorage.getItem('pricingInfo')
    let segmentInfo = JSON.parse(pricingInfo).segmentInfo


    return Axios.post(`/api/flights/create-reservation`, { schema, AirPriceRsp, customerInfo: travelerInfo, segmentInfo })
        .then(res => {
            if (res.data.data !== null) {
                return res.data.data
            } else {
                dispatch({
                    type: SET_MESSAGE,
                    payload: {
                        message: res.data.message,
                        type: 'error'
                    }
                })
                return false
            }
        })
        .catch(error => {
            console.log(error.response)
            dispatch({
                type: SET_MESSAGE,
                payload: {
                    message: error.response !== undefined && error.response.data.message,
                    type: 'error'
                }
            })
            return false
        })
}


