import { TARGET_BRANCH } from '../../store/actions/types'

const UniversalRecordCancelReq = PNR => {
    let schema = `<soap:Envelope
        xmlns:soap="http://schemas.xmlsoap.org/soap/envelope/"
        xmlns:com="http://www.travelport.com/schema/common_v47_0" 
        xmlns:univ="http://www.travelport.com/schema/universal_v47_0"
        >
        <soap:Body>
            <univ:UniversalRecordCancelReq AuthorizedBy="user" TargetBranch="${TARGET_BRANCH}" TraceId="trace" UniversalRecordLocatorCode="${PNR}" Version="1">
                <com:BillingPointOfSaleInfo OriginApplication="UAPI"/>
            </univ:UniversalRecordCancelReq>
        </soap:Body>
    </soap:Envelope>`
    return schema
}
export default UniversalRecordCancelReq