import React, { Component, Fragment } from 'react';
import Axios from 'axios';
import ReactTable from 'react-table';
import 'react-table/react-table.css';
import { connect } from 'react-redux';
import UserHeader from '../user/UserHeader';
import { dateTime } from '../../../utils/helper';
import { Form } from 'react-bootstrap';

class B2BBonus extends Component {
    constructor(props) {
        super(props);
        this.state = {
            data: [],
            loading: true,
            limit: 100,
            page: 1,
            pages: -1,
            authUser: props.user,
            userId: props.match.params.userId
        }
    }
    componentDidMount() {
        this.fetchData();
    }
    fetchData(limit = this.state.limit, page = this.state.page) {
        let { userId } = this.state
        this.setState({
            data: [],
            loading: true,
        })
        Axios.post(`/api/bonus/get`, { limit, page, userId })
            .then(res => {
                this.setState({
                    data: res.data.data.docs,
                    page: res.data.data.page,
                    pages: res.data.data.totalPages,
                    limit: res.data.data.limit,
                    loading: false
                })
            })
    }
    render() {
        let { data, loading, pages, limit, userId } = this.state

        const columns = [
            {
                Header: 'SL',
                accessor: 'index',
                Cell: row => <span>{row.index + 1}</span>,
                width: 30,
            },
            {
                Header: 'Date',
                accessor: 'date',
                Cell: row => <span>{dateTime(row.original.date, 2)}</span>,
            },
            {
                Header: 'Purpose',
                accessor: 'purpose',
            },
            {
                Header: 'Recommended',
                accessor: 'recommended',
            },
            {
                Header: 'Amount',
                accessor: 'amount',
            },
            {
                Header: 'Total Amount',
                accessor: 'totalAmount',
            },
            // {
            //     Header: 'Agent ID',
            //     Cell: row => <Link className="link-color" to={`/backend/counter/${row.original.userId.type === 2 ? row.original.userId.agentId : row.original.userId._id}`}>{row.original.agentId}</Link>,
            // },
        ];

        return (
            <Fragment>
                <div className="bg-light">
                    <div className="section-padding-top">
                        <div className="container bg-white">
                            <UserHeader userId={userId} />

                            <div className="section-title text-center"><h2>Bonus</h2></div>

                            <div className="row">
                                <div className="col-lg-3">
                                    <Form.Group>
                                        <Form.Control
                                            type="text"
                                            name="name"
                                            // value={name}
                                            // onChange={this.changeHandler}
                                            placeholder="Search"
                                        />
                                    </Form.Group>
                                </div>
                            </div>

                            <div className="row">
                                <div className="col-lg-12">
                                    <ReactTable
                                        data={data}
                                        columns={columns}
                                        defaultPageSize={limit}
                                        minRows={1}
                                        noDataText='No rows found'
                                        loading={loading}
                                        manual
                                        pages={pages}
                                        onPageChange={(page) => this.fetchData(limit, page + 1)}
                                        onPageSizeChange={(limit, page) => this.fetchData(limit, page)}
                                    />
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </Fragment>
        );
    }
}
const mapStateToProps = state => ({
    common: state.common,
    user: state.auth.user,
})
export default connect(mapStateToProps)(B2BBonus)