import React, { Component, Fragment } from 'react';
import Axios from 'axios';
import ReactTable from 'react-table';
import 'react-table/react-table.css';
import { Link } from 'react-router-dom';
import { connect } from 'react-redux';
import UserHeader from '../user/UserHeader'
import { dateTime } from './../../../utils/helper';
import { Form } from 'react-bootstrap';
import TicketHeader from './TicketHeader';


class B2BTicket extends Component {
    constructor(props) {
        super(props);
        let { userId, ticketStatus } = props.match.params
        this.state = {
            _this: this,
            data: [],
            loading: true,
            limit: 50,
            page: 1,
            pages: -1,
            status: ticketStatus,
            price: [],
            passengers: [],
            airSegment: [],
            authUser: props.user,
            userId
        }
    }
    static getDerivedStateFromProps(nextProps, prevState) {
        if (JSON.stringify(nextProps.match.params.ticketStatus) === JSON.stringify(prevState.status)) return null
        prevState._this.fetchData('', '', nextProps.match.params.ticketStatus)
        return null
    }
    componentDidMount() {
        this.fetchData();
    }
    fetchData(limit = this.state.limit, page = this.state.page, status = this.state.status) {
        let { userId } = this.state
        this.setState({
            data: [],
            loading: true,
            status,
        })
        Axios.post(`/api/tickets/get`, { limit, page, status, userId })
            .then(res => {
                this.setState({
                    data: res.data.data.data.docs,
                    page: res.data.data.data.page,
                    pages: res.data.data.data.totalPages,
                    limit: res.data.data.data.limit,
                    loading: false,
                    price: res.data.data.price,
                    passengers: res.data.data.passenger,
                    airSegment: res.data.data.airSegment,
                })
            })
    }
    render() {
        let { data, loading, pages, limit, userId, price, passengers, airSegment, status } = this.state

        const getPassenger = ticketId => {
            if (Object.keys(passengers).length !== 0) {
                let selObj = passengers.filter(item => item.ticketId === ticketId)
                if (selObj.length === 0) return null
                let noPass = Object.keys(selObj).length !== 0 ? `(${Object.keys(selObj).length})` : ''
                return <span>{`${selObj[0].firstName} ${selObj[0].lastName} ${noPass}`} </span>
            } else {
                return null
            }
        }
        const getPassportVisa = ticketId => {
            if (Object.keys(passengers).length !== 0) {
                let selObj = passengers.filter(item => item.ticketId === ticketId)
                if (selObj.length === 0) return null
                if (selObj[0].passport) {
                    return <div><span>V-Y</span>, <span>P-N</span></div>
                } else {
                    return <div><span>V-N</span>, <span>P-N</span></div>
                }
            } else {
                return null
            }
        }
        const getFlightDate = ticketId => {
            if (Object.keys(airSegment).length !== 0) {
                let selObj = airSegment.filter(item => item.ticketId === ticketId)
                if (selObj.length === 0) return null
                return dateTime(selObj[0].departureTime)
            } else {
                return null
            }
        }
        const getPrice = ticketId => {
            if (Object.keys(price).length !== 0) {
                let selObj = price.filter(item => item.ticketId === ticketId)
                if (selObj.length === 0) return null
                return selObj[0].totalPrice.slice(3)
            } else {
                return null
            }
        }
        const getSegmentAndAirline = ticketId => {
            let newArr = []
            if (Object.keys(airSegment).length !== 0) {
                airSegment.map(item => {
                    if (item.ticketId === ticketId) {
                        newArr.push(item.origin)
                        newArr.push(item.destination)
                        return true;
                    }
                    return true;
                })
                airSegment.map(item => {
                    if (item.ticketId === ticketId) {
                        newArr.push(item.carrier, item.providerCode, item.classOfService)
                        return true;
                    }
                    return true;
                })
                let abc = [...new Set(newArr)]
                let result = ''
                abc.map(item => {
                    result = `${result}-${item}`
                    return true;
                })
                return result.slice(1)
            } else {
                return null
            }
        }
        const getFlightWay = ticketId => {
            let result = 'One-way'
            if (Object.keys(airSegment).length !== 0) {
                airSegment.map(item => {
                    if (item.ticketId === ticketId && item.Group === '1') {
                        result = 'Return-way'
                    }
                    return true;
                })
                return result;
            } else {
                return null
            }
        }


        const columns = [
            {
                Header: 'SL',
                accessor: 'index',
                Cell: row => <span>{row.index + 1}</span>,
                width: 30,
            },
            {
                Header: 'Ref ID',
                accessor: '_id',
                Cell: row => <Link className="link-color" to={`/backend/ticket/detail/${row.original._id}`}>{row.original.refId}</Link>,
                width: 100,
            },
            {
                Header: 'PNR',
                accessor: 'locatorCode',
                width: 70,
            },
            {
                Header: 'Segment and Airlines',
                Cell: row => <span>{getSegmentAndAirline(row.original._id) + '-' + (row.original.refundable === 'true' ? 'R' : 'N')}</span>,
                width: 160,
            },
            {
                Header: 'PAX Name N No.',
                Cell: row => <span>{getPassenger(row.original._id)}</span>,
                width: 200,
            },
            {
                Header: 'Trip Type',
                Cell: row => <span>{getFlightWay(row.original._id)}</span>,
                width: 100,
            },
            {
                Header: 'Agent Net Fare',
                Cell: row => <span>BDT={getPrice(row.original._id)}</span>,
            },
            {
                Header: 'Status',
                accessor: 'status',
            },
            {
                Header: 'Particular',
                Cell: row => <Link to='#blank'>Show comment</Link>,
            },
            {
                Header: 'By',
                Cell: row => <Link className="link-color" to={`/backend/users/${row.original.userId.type === 2 ? row.original.userId.agentId : row.original.selfId._id}`}>{row.original.selfId.name}</Link>,
            },
        ];
        let date = {}
        if (status === 'ticket') {
            columns.splice(11, 0, {
                Header: 'SMS/Email',
                Cell: row => <Link className="link-color" to={`/backend/users/${row.original.userId.type === 2 ? row.original.userId.agentId : row.original.userId._id}`}>Email</Link>,
            })

            date = {
                Header: 'Date',
                Cell: row => <span>
                    <span className='text-danger'>Booking: {dateTime(row.original.createdAt)}</span><br />
                    <span className='text-danger'>TL: {dateTime(row.original.latestTicketingTime)}</span><br />
                    <span className='text-success'>Issued: {dateTime(row.original.createdAt)}</span><br />
                    <span className='text-success'>Travel: {getFlightDate(row.original._id)}</span><br />
                </span>,
                width: 200,
            }
        } else {
            date = {
                Header: 'Date',
                Cell: row => <span>
                    <span className='text-danger'>Booking: {dateTime(row.original.createdAt)}</span><br />
                    <span className='text-danger'>TL: {dateTime(row.original.latestTicketingTime)}</span><br />
                </span>,
                width: 200,
            }
        }

        columns.splice(1, 0, date)

        if (status === 'ticket' || status === 'no-show' || status === 'void' || status === 'refund') {
            columns.push({
                Header: 'SMS/Email',
                Cell: row => <Link className="link-color" to={`/backend/users/${row.original.userId.type === 2 ? row.original.userId.agentId : row.original.userId._id}`}>SMS/Email</Link>,
            })
        }

        if (status === 'ticket' || status === 'no-show' || status === 'void' || status === 'refund' || status === 'in-process') {
            columns.splice(9, 0, {
                Header: 'DocX',
                Cell: row => <span>{getPassportVisa(row.original._id)}</span>,
                width: 50,
            })
        }
        if (status === 'ticket' || status === 'no-show' || status === 'booking') {
            columns.splice(columns.length - 1, 0, {
                Header: 'Action',
                accessor: 'status',
            })
        }


        return (
            <Fragment>
                <div className="bg-light">
                    <div className="section-padding-top">
                        <div className="container bg-white">

                            <UserHeader userId={userId} />

                            <TicketHeader status={status} />

                            <div className="row">
                                <div className="col-lg-3">
                                    <Form.Group>
                                        <Form.Control
                                            type="text"
                                            name="name"
                                            // value={name}
                                            // onChange={this.changeHandler}
                                            placeholder="Search"
                                        />
                                    </Form.Group>
                                </div>
                            </div>

                            <div className="row">
                                <div className="col-lg-12">
                                    <ReactTable
                                        data={data}
                                        columns={columns}
                                        defaultPageSize={limit}
                                        minRows={1}
                                        noDataText='No rows found'
                                        loading={loading}
                                        manual
                                        pages={pages}
                                        onPageChange={(page) => this.fetchData(limit, page + 1)}
                                        onPageSizeChange={(limit, page) => this.fetchData(limit, page)}
                                    />
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </Fragment>
        );
    }
}
const mapStateToProps = state => ({
    user: state.auth.user,
})
export default connect(mapStateToProps)(B2BTicket)