import { SET_MESSAGE } from './types'
import Axios from 'axios'

// Get Data
export const getData = () => async dispatch => {
    return Axios.post(`/api/countries/get`)
        .then(res => {
            return res.data.data.docs
        })
        .catch(err => {
            dispatch({
                type: SET_MESSAGE,
                payload: {
                    message: err.response && err.response.data.message,
                    type: 'error',
                }
            })
            return false
        })
}


// Store Data
export const storeData = data => async dispatch => {
    return Axios.post(`/api/countries/`, data)
        .then(res => {
            dispatch({
                type: SET_MESSAGE,
                payload: {
                    message: res.data.message,
                    type: 'success',
                }
            })
            return true;
        })
        .catch(err => {
            dispatch({
                type: SET_MESSAGE,
                payload: {
                    message: err.response && err.response.data.message,
                    type: 'error',
                }
            })
            return false
        })
}

// Update Data
export const updateData = (data, id) => async dispatch => {
    return Axios.put(`/api/countries/${id}`, data)
        .then(res => {
            dispatch({
                type: SET_MESSAGE,
                payload: {
                    message: res.data.message,
                    type: 'success',
                }
            })
            return true;
        })
        .catch(err => {
            dispatch({
                type: SET_MESSAGE,
                payload: {
                    message: err.response && err.response.data.message,
                    type: 'error',
                }
            })
            return false;
        })
}

// Delete Data
export const deleteData = id => async dispatch => {
    return Axios.delete(`/api/countries/${id}`)
        .then(res => {
            dispatch({
                type: SET_MESSAGE,
                payload: {
                    message: res.data.message,
                }
            })
            return true;
        })
        .catch(err => {
            dispatch({
                type: SET_MESSAGE,
                payload: {
                    message: err.response && err.response.data.message,
                    type: 'error',
                }
            })
            return false;
        })
}
