import React, { Component, Fragment } from "react";
import {
  getMyDate,
  getMyDay,
  getMyFullYear,
  getMyMonth,
  getMyTime,
  timeConvert,
} from "./../../../utils/helper";
import { Collapse } from "react-collapse";
import { FaPlane, FaArrowsAltH } from "react-icons/fa";
import { GetLayover } from "./Layover";
import { GetAirline, GetAirport } from "../../../utils/GetAirportAirline";


class TransitItem extends Component {
  render() {
    let { isCollapse, segmentList } = this.props;


    return (
      <Fragment>
        <Collapse isOpened={isCollapse}>
          {segmentList.map((segmentItem, i) => segmentItem.length > 0 && <div key={i + segmentItem.length} className="transit-section">
            {segmentItem.map((item, index) => (
              <div style={{ position: "relative" }} key={index + item["$"].Key}>


                {segmentItem.length > 1 && index > 0 && index < segmentItem.length && <GetLayover firstSegment={segmentItem[index - 1]} lastSegment={segmentItem[index]} />}


                <div className="row pad pb-0 pt-0" key={item["$"].Key}>
                  <div className="col-lg-2 logo-line pr-0">
                    <div className='transit-biman-logo' style={{ maxHeight: '31px' }}>
                      <GetAirline code={item["$"].Carrier} type='logo' />
                    </div>
                    <p className="normaltext"><GetAirline code={item["$"].Carrier} type='abc' /> || {item["$"].Carrier} {item["$"].FlightNumber}</p>
                  </div>
                  <div className="col-lg-1">
                    <div className="textset">
                      {item["$"].Group === "1" ? (
                        <>
                          <FaPlane style={{ transform: "rotate(180deg)" }} />
                        </>
                      ) : (
                          <>
                            <FaPlane />
                          </>
                        )}
                      <br />
                      <span>{item["$"].Equipment}</span>
                    </div>
                  </div>
                  <div className="col-lg-3">
                    <p>{`${getMyDay(item["$"].DepartureTime)}, ${getMyDate(
                      item["$"].DepartureTime
                    )} ${getMyMonth(item["$"].DepartureTime)} ${getMyFullYear(
                      item["$"].DepartureTime
                    )}`}</p>

                    <span className="normaltext">
                      <GetAirport code={item["$"].Origin} limit={50} />
                    </span>
                  </div>

                  <div className="col-lg-1">
                    <span className="md-0 bigtext">
                      {getMyTime(item["$"].DepartureTime)}
                    </span>
                    <br />
                    {item["$"].Origin}
                  </div>
                  <div className="col-lg-1 text-center p-0">
                    <span><i className="fa fa-clock-o" /> {timeConvert(item["$"]["FlightTime"])}</span>
                    <h3 className='mb-1'>
                      <FaArrowsAltH className="normaltext" />
                    </h3>
                  </div>
                  <div className="col-lg-1">
                    <span className="md-0 bigtext">
                      {getMyTime(item["$"].ArrivalTime)}
                    </span>
                    <br />
                    {item["$"].Destination}
                  </div>
                  <div className="col-lg-3">
                    <p>{`${getMyDay(item["$"].ArrivalTime)}, ${getMyDate(
                      item["$"].ArrivalTime
                    )} ${getMyMonth(item["$"].ArrivalTime)} ${getMyFullYear(
                      item["$"].ArrivalTime
                    )}`}</p>
                    <span className="normaltext">
                      <GetAirport code={item["$"].Destination} limit={50} />
                    </span>
                  </div>
                </div>
              </div>
            ))}
          </div>)}

        </Collapse>
      </Fragment>
    );
  }
}
export default TransitItem;