import React, { Component, Fragment } from 'react';
import Modal from 'react-bootstrap/Modal';
import { Button, Form } from 'react-bootstrap';
import { connect } from 'react-redux';
import { storeData, updateData } from '../../../store/actions/IATARecordActions'

class Edit extends Component {
    constructor(props) {
        super(props);
        this.state = {
            id: props.data._id || null,
            iataNumber: props.data.iataNumber || 0,
            queue: props.data.queue || '',
            agencyName: props.data.agencyName || '',
            contact: props.data.contact || '',
            email: props.data.email || '',
            civilNumber: props.data.civilNumber || '',
            contactPerson: props.data.contactPerson || '',
            address: props.data.address || '',
            country: props.data.country || '',
            status: props.data.status || '',
            isWait: false
        }
    }
    changeHandler = event => {
        this.setState({
            [event.target.name]: event.target.value
        })
    }
    submitHandler = async event => {
        event.preventDefault()
        this.setState({ isWait: true })
        let { actionIsDone, storeData, updateData } = this.props

        let response = false;
        let { id } = this.state
        if (id) {
            response = await updateData(this.state, id)
        } else {
            response = await storeData(this.state)
        }
        if (response) actionIsDone()
        this.setState({ isWait: false })
    }
    render() {
        let { id, iataNumber, queue, agencyName, contact, email, civilNumber, contactPerson, address, country, status, isWait } = this.state
        let isDone = iataNumber && queue
        return (
            <Fragment>
                <Modal show={this.props.isOpen} onHide={this.props.closeHandler}>
                    <Modal.Header closeButton>
                        <Modal.Title>{id ? `Edit IATA Record` : `Add IATA Record`}</Modal.Title>
                    </Modal.Header>
                    <Modal.Body>
                        <Form onSubmit={this.submitHandler}>
                            <Form.Group>
                                <Form.Label>IATA Number <span>*</span></Form.Label>
                                <Form.Control
                                    type="text"
                                    name="iataNumber"
                                    defaultValue={iataNumber}
                                    onChange={this.changeHandler}
                                    placeholder="Enter IATA Number"
                                    required
                                />
                            </Form.Group>
                            <Form.Group>
                                <Form.Label>Queue <span>*</span></Form.Label>
                                <Form.Control
                                    type="text"
                                    name="queue"
                                    defaultValue={queue}
                                    onChange={this.changeHandler}
                                    placeholder="Enter Queue"
                                    required
                                />
                            </Form.Group>
                            <Form.Group>
                                <Form.Label>Agency Name <span>*</span></Form.Label>
                                <Form.Control
                                    type="text"
                                    name="agencyName"
                                    defaultValue={agencyName}
                                    onChange={this.changeHandler}
                                    placeholder="Enter Agency Name"
                                    required
                                />
                            </Form.Group>
                            <Form.Group>
                                <Form.Label>Contact Number</Form.Label>
                                <Form.Control
                                    type="number"
                                    name="contact"
                                    defaultValue={contact}
                                    onChange={this.changeHandler}
                                    placeholder="Enter Contact Number"
                                />
                            </Form.Group>
                            <Form.Group>
                                <Form.Label>Email</Form.Label>
                                <Form.Control
                                    type="email"
                                    name="email"
                                    defaultValue={email}
                                    onChange={this.changeHandler}
                                    placeholder="Enter Email"
                                />
                            </Form.Group>
                            <Form.Group>
                                <Form.Label>Civil Number</Form.Label>
                                <Form.Control
                                    type="text"
                                    name="civilNumber"
                                    defaultValue={civilNumber}
                                    onChange={this.changeHandler}
                                    placeholder="Enter Civil Number"
                                />
                            </Form.Group>
                            <Form.Group>
                                <Form.Label>Contact Person</Form.Label>
                                <Form.Control
                                    type="text"
                                    name="contactPerson"
                                    defaultValue={contactPerson}
                                    onChange={this.changeHandler}
                                    placeholder="Enter Contact Person"
                                />
                            </Form.Group>
                            <Form.Group>
                                <Form.Label>Address</Form.Label>
                                <Form.Control
                                    type="text"
                                    name="address"
                                    defaultValue={address}
                                    onChange={this.changeHandler}
                                    placeholder="Enter Address"
                                />
                            </Form.Group>
                            <Form.Group>
                                <Form.Label>Country</Form.Label>
                                <Form.Control
                                    type="text"
                                    name="country"
                                    defaultValue={country}
                                    onChange={this.changeHandler}
                                    placeholder="Enter Country"
                                />
                            </Form.Group>
                            <Form.Group>
                                <Form.Label>Status</Form.Label>
                                <Form.Control
                                    type="text"
                                    name="status"
                                    defaultValue={status}
                                    onChange={this.changeHandler}
                                    placeholder="Enter Status"
                                />
                            </Form.Group>

                            <Button type="submit" variant="dark" disabled={!isDone} size="lg" block>
                                {isWait ? `Please Wait...` : `Save`}
                            </Button>
                        </Form>
                    </Modal.Body>
                </Modal>
            </Fragment>
        )
    }
}
const mapStateToProps = state => ({
    common: state.common,
})
export default connect(mapStateToProps, { storeData, updateData })(Edit);