import React, { Component, Fragment } from 'react'

class SubmitBtnArea extends Component {

    render() {
        let { origin, destination, departureDate, returnDate, adult, isWait, route } = this.props.data
        let isDone = origin && destination && departureDate && adult && !isWait
        if (route === "RETURN_FLIGHT" && returnDate === null) {
            isDone = false
        }
        return (
            <Fragment>
                <div className="col-lg-6 col-md-12 mt-4">
                    <div className="search-flight">
                        <button
                            type="submit"
                            // className={isDone ? `btn submit-btn-flight` : `btn submit-btn-flight`}
                            className='btn submit-btn-flight'
                            disabled={!isDone}
                        >{isWait ? `Please Wait...` : `Search Flight`}</button>
                    </div>
                </div>
            </Fragment>
        )
    }
}
export default SubmitBtnArea