import React, { Component, Fragment } from 'react';
import { Collapse } from "react-collapse";
import PassportVisaForm from './PassportVisaForm'
import { Link } from 'react-router-dom';
import { connect } from 'react-redux';


class PdfMakerBtn extends Component {
    constructor(props) {
        super(props);
        this.state = {
            serviceCharge: false,
            fareSummery: false,
            passportVisa: false,
            printDownload: false,
            ticketWithPrice: false,
            ticketWithoutPrice: false
        }
    }

    render() {
        let { serviceCharge, fareSummery, passportVisa, printDownload, ticketWithPrice, ticketWithoutPrice } = this.state
        let { passenger, ticket, deposit, statement } = this.props.data

        let { anyService } = this.props.common.basicInfo
        let available = 0
        if (ticket.userId) {
            available = ticket.userId.balance
        }
        let totalDeposit = 0
        if (deposit) {
            for (var i = 0; i < deposit.length; i++) {
                totalDeposit += deposit[i].amount;
            }
        }
        let totalExpense = 0
        if (statement) {
            for (var i = 0; i < statement.length; i++) {
                totalExpense += statement[i].amount;
            }
        }
        return (
            <Fragment>
                <div className="p-3 my-5">
                    <div className="row">
                        <div className="col-lg-3">
                            <div className=" bg-white">
                                <h5 className='d-flex justify-content-between px-2' style={{ cursor: 'pointer' }} onClick={() => this.setState({ fareSummery: !fareSummery })}>Account Status <i className='fa fa-caret-down' /> </h5>
                                <Collapse isOpened={fareSummery}>
                                    <table className='table'>
                                        <thead>
                                            <tr>
                                                <th>Deposit</th>
                                                <th>Expense</th>
                                                <th>Available</th>
                                            </tr>
                                        </thead>
                                        <tbody>
                                            <tr>
                                                <td>{totalDeposit}</td>
                                                <td>{totalExpense}</td>
                                                <td>{available}</td>
                                            </tr>
                                        </tbody>
                                    </table>
                                </Collapse>
                            </div>
                        </div>
                        <div className="col-lg-3">
                            <div className="bg-white">
                                <h5 className='d-flex justify-content-between px-2' style={{ cursor: 'pointer' }} onClick={() => this.setState({ serviceCharge: !serviceCharge })}>Ticketmet Service Charge <i className='fa fa-caret-down' /></h5>
                                <Collapse isOpened={serviceCharge}>
                                    <table className='table'>
                                        <tbody>
                                            <tr>
                                                <td>{anyService}</td>
                                            </tr>
                                        </tbody>
                                    </table>
                                </Collapse>
                            </div>
                        </div>
                        <div className="col-lg-3">
                            <div className="bg-white">
                                <h5 className='d-flex justify-content-between px-2' style={{ cursor: 'pointer' }} onClick={() => this.setState({ passportVisa: !passportVisa })}>Passport VISA Profile <i className='fa fa-caret-down' /> </h5>
                                <Collapse isOpened={passportVisa}>
                                    {passenger && passenger.length > 0 && passenger.map(item => <PassportVisaForm key={item._id} data={item} />)}
                                </Collapse>
                            </div>
                        </div>
                        <div className="col-lg-3">
                            <div className="bg-white">
                                <h5
                                    className='d-flex justify-content-between px-2'
                                    style={{ cursor: 'pointer' }}
                                    onClick={() => this.setState({ printDownload: !printDownload })}
                                >Print\Download <i className='fa fa-caret-down' /> </h5>
                                <Collapse isOpened={printDownload}>
                                    <div className="print-items">
                                        <Link to={`/pdf/ticket/${ticket._id}`} target='_blank'>Print Invoice</Link>
                                        <Link to={`/pdf/ticket/${ticket._id}`} target='_blank' >Print Customer Invoice</Link>
                                        <Link to='#blank' onClick={() => this.setState({ ticketWithPrice: !ticketWithPrice })} >Ticket With Price(Per Passenger) <i className='fa fa-arrow-down' /></Link>
                                        <Collapse isOpened={ticketWithPrice}>
                                            <div className="ticket-with-price pl-3">
                                                {passenger && passenger.length > 0 && passenger.map(item => <Link target='_blank' to={`/pdf/ticket/${ticket._id}`} key={item._id}>{item.firstName + ' ' + item.lastName}</Link>)}
                                            </div>
                                        </Collapse>
                                        <Link to='#blank' onClick={() => this.setState({ ticketWithoutPrice: !ticketWithoutPrice })} >Ticket Without Price(Per Passenger) <i className='fa fa-arrow-down' /></Link>
                                        <Collapse isOpened={ticketWithoutPrice}>
                                            <div className="ticket-with-price pl-3">
                                                {passenger && passenger.length > 0 && passenger.map(item => <Link target='_blank' to={`/pdf/ticket/${ticket._id}`} key={item._id}>{item.firstName + ' ' + item.lastName}</Link>)}
                                            </div>
                                        </Collapse>
                                        <Link to={`/pdf/ticket/${ticket._id}`} target='_blank'>Print Confirmation With Price</Link>
                                        <Link to={`/pdf/ticket/${ticket._id}`} target='_blank'>Print Confirmation Without Price</Link>
                                    </div>
                                </Collapse>
                            </div>
                        </div>
                    </div>
                </div>


                {/* <div className="float-right">
                    <h5
                        className='d-flex justify-content-between px-2'
                        style={{ cursor: 'pointer' }}
                        onClick={() => this.setState({ fareSummery: !fareSummery })}
                    >SSR <i className='fa fa-caret-down' /> </h5>
                    <Collapse isOpened={fareSummery}>
                    </Collapse>
                </div> */}

            </Fragment>
        )
    }
}
const mapStateToProps = state => ({
    common: state.common,
})
export default connect(mapStateToProps)(PdfMakerBtn)